import Logger from '../../Common-Utils-Package/logger/logger';
import Storage from '../../Common-Utils-Package/storage/storage';
import HttpService from '../../Common-Utils-Package/http_service/http_service';
import GTIResponseBuilder from './gti_response_builder';
import { GtiBody, GtiQueryItems } from './gti_body';
import GTICache from './gti_cache';
import { GTIStorageVariables } from './gti_storage_variables';
import { GTIGlobals } from './gti_globals';
import { GTIConstants } from './gti_contants';

export default class GTIRequestor
{
  /**
   * GTI Requestor: constructor
   * @param {object} wasm - wasm engine object
   * @param {function} wasm.wasm_gti_hmac_sha1 - should contain function to hmac_sha1 for gti
   * @param {object} options - object containinig optional params that alter the behavior of this lib
   * @param {string} options.clientIdGuid - client id passed as part of the gti request body (default as: empty string)
   * @param {string} options.serverUrl - url string to the gti server (default as: 'https://webadvisorc.rest.gti.mcafee.com/1')
   * @param {string} options.cspid - cspid use for telemetry (default as: '__not_available__')
   * @param {string} options.productVersion - product version for gti call (default as: '0.0.0.0')
   */
  constructor(wasm, options)
  {
    // required param
    this.wasm = wasm;

    // local variables
    this.httpService = null;
    this.storage = null;
    this.gtiCache = null;

    // optional params
    GTIGlobals.cspid = options.cspid || GTIGlobals.cspid;
    GTIGlobals.clientIdGuid = options.clientIdGuid || GTIGlobals.clientIdGuid;
    GTIGlobals.serverUrl = options.serverUrl || GTIGlobals.serverUrl;
    GTIGlobals.productVersion = options.productVersion || '0.0.0.0';
  }

  async init()
  {
    if (!this.wasm || typeof this.wasm.wasm_gti_hmac_sha1 !== 'function')
    {
      Logger.error('Invalid wasm object given on gti requestor.');
      return false;
    }

    this.storage = new Storage(Object.keys(GTIStorageVariables));
    const storageInitialized = await this.storage.init();
    if (!storageInitialized)
    {
      Logger.error('Failed to initialized gti cache storage.');
      return false;
    }

    this.gtiCache = new GTICache(this.storage);
    this.gtiCache.setAlarmCacheClean();

    this.httpService = new HttpService();

    return true;
  }

  /**
   * This function allows modifying the global variables
   * ie: globalValues = { serverUrl: 'https://someurl.com', otherGlobal: 'somedata' }
   * @param {object} globalValues
   * @param {key} globalsValues.key
   * @param {value} globalsValues.value
   */
  setGlobals(globalValues)
  {
    if (typeof globalValues !== 'object'
    || Array.isArray(globalValues)
    || globalValues === null)
    {
      Logger.error('Received invalid object (json) type when trying to call MTI setGlobals.');
      return;
    }

    for (const [key, value] of Object.entries(globalValues))
    {
      Logger.log(`GTI Global values updated: ${key}: ${value}`);
      GTIGlobals[key] = value;
    }
  }

  getCache()
  {
    return this.gtiCache;
  }

  getTelemetryData(referrer)
  {
    // Telemetry
    const telemetryData = {};
    telemetryData.uag = btoa(navigator.userAgent);
    telemetryData.cspid = GTIGlobals.cspid;

    if (typeof referrer === 'string')
    {
      telemetryData.refurl = btoa(referrer);
    }

    return telemetryData;
  }

  prepareBody(requestData, builder, rID, referrer, checkCacheOnly = false)
  {
    const body = new GtiBody(GTIGlobals.clientIdGuid, rID);
    let questIndex = 0; // This is actual number of questions for GTI request.

    for (let startIndex = 0; startIndex < requestData.length; startIndex++)
    {
      const uri = requestData[startIndex];
      const data = this.gtiCache.getReputation(uri);

      if (data.cache_entry === null)
      {
        if (checkCacheOnly)
        {
          // if we are to do cache check only, no need to prepare
          // for GTI request calls.
          continue;
        }

        // There is no data for this uri in cache so add this to the question list.
        body.q[questIndex] = new GtiQueryItems();
        body.q[questIndex].op = 'url';
        body.q[questIndex].url = uri;
        body.q[questIndex].qmd = this.getTelemetryData(referrer);
        questIndex++;
      }
      else
      {
        builder.addCachedItem({ uri, value: data.cache_entry });
        // Remove cached item from requestdata.
        requestData.splice(startIndex, 1);
        startIndex--;
      }
    }

    return { body };
  }

  request(headers, body)
  {
    return this.makeGTIRequest({
      method: 'POST',
      body: JSON.stringify(body),
      url: GTIGlobals.serverUrl,
      headers,
    });
  }

  makeGTIRequest(request)
  {
    return this.httpService.sendHttpRequest(request).then((res) => res.json());
  }

  generateRequestId()
  {
    const min = 1000;
    const max = 100000000;
    return Math.floor(Math.random() * (max - min)) + min;
  }

  async generateHeaders(msgBody)
  {
    const result = await this.wasm.wasm_gti_hmac_sha1(JSON.stringify(msgBody));
    const headersData = {};
    headersData['Content-Type'] = GTIConstants.contentType;
    headersData.xdate = new Date().toUTCString();
    headersData.Authorization = `GTI ${GTIConstants.gtiId}:${result}`;
    return headersData;
  }

  toResponse(gtiResponse, builder, requestData, fncallback)
  {
    builder.init(gtiResponse, requestData); // "[]" = empty gti response.
    builder.prepareResponse(fncallback); // Concatenate the responses and then return to content.
  }

  /**
   * Does post to GTI splitting the requests as per guidelines to 5 questions per request.
   * @param {object} requestData      - Array of request URLs whose reputation is asked to GTI
   * @param {function} fncallback     - Callback function invoked to respond to the query from content process
   * @param {object} options          - optional params
   * @param {string} options.referrer         - Value of referer header (or undefined)
   * @param {boolean} options.checkCacheOnly  - Check Cache only for GTI, do not make HTTP GTI request
   */
  async post(requestData, fncallback, options)
  {
    const referrer = options.referrer || null;
    const checkCacheOnly = options.checkCacheOnly || false;

    const promises = this.gtiCache.checkRequest(requestData);
    if (promises.length > 0)
    {
      await Promise.all(promises);
    }

    try
    {
      const builder = new GTIResponseBuilder(this.gtiCache);

      const fullRequestData = requestData.slice();
      const requestId = this.generateRequestId();

      // requestData will only contain non-cached items after this
      const reputation = this.prepareBody(requestData, builder, requestId, referrer, checkCacheOnly);
      const gtiResponse = [];

      // If there are records to ask with GTI then post. If the record is in cache then we do not ask.
      if (reputation.body.q.length > 0)
      {
        try
        {
          const promise = new Promise(async (resolve) =>
          {
            const headers = await this.generateHeaders(reputation.body);
            const response = await this.request(headers, reputation.body);
            this.toResponse(response, builder, requestData, fncallback);
            this.gtiCache.deleteRequest(requestData);
            resolve();
          });

          this.gtiCache.addRequest(requestData, promise);
        }
        catch (error)
        {
          fncallback(null);
          Logger.error(error);
        }
      }
      // If there are no GTI requests needed, prepare the response right away. (i.e. Everything is cached already)
      else
      {
        this.toResponse(gtiResponse, builder, fullRequestData, fncallback);
      }
    }
    catch (e)
    {
      Logger.error(e.message);
    }
  }
}
