import { BackgroundGlobals } from '@extension/background/background_globals';
import { CacheStorageVariables } from '@extension/common/cache_store_variables';
import { Logger } from '@extension/common/logger';
import BackgroundUtils from '@extension/background/background_utils';

export default class UnifiedAuthClient
{
  async launchLoginFlow()
  {
    try
    {
      const wssEmail = BackgroundGlobals.cacheStore.get(CacheStorageVariables.WSS_EMAIL.name, '');
      const authAAI = { ea: wssEmail };
      const encryptedEmail = await BackgroundGlobals.wasm.uauth_aes_encrypt(JSON.stringify(authAAI));
      const webAppLoginUrl = `${BackgroundGlobals.IDPS_LOGIN_DOMAIN}/identity?skip-welcome=1&source=wa&auth-aai=${encodeURIComponent(encryptedEmail)}`;
      BackgroundUtils.focusOrCreateTab(webAppLoginUrl);
    }
    catch (error)
    {
      Logger.error('Unified Auth Login Error:', error);
    }
  }
}
