import { StoredSearchVariables } from './constants';

const { SUGGESTION_URL,
  MAX_STORED_SEARCHES,
  SHOW_MAX_RESULTS,
  SEARCH_RESULT_TTL } = StoredSearchVariables;

export default class SearchSuggest
{
  constructor(httpService)
  {
    this.httpService = httpService;
    this.storedSearches = new Map();
  }

  getUrl(searchTerm)
  {
    return `${SUGGESTION_URL}${searchTerm}`;
  }

  /**
   * Retrieve search results from the cache
   *
   * @param {String} searchTerm
   */
  async retrieveSearchTerm(searchTerm)
  {
    let results = null;
    if (this.hasSearchTerm(searchTerm))
    {
      results = this.storedSearches.get(searchTerm).searchResults;
    }
    else
    {
      results = await this.getSearchSuggestions(searchTerm);
    }
    return results;
  }

  async getSearchSuggestions(searchTerm)
  {
    try
    {
      let searchResults = [];
      const response = await this.httpService.sendHttpRequest({ method: 'GET', url: this.getUrl(searchTerm), headers: {} });
      const responseJson = await response.json();

      if (responseJson)
      {
        searchResults = responseJson[1].slice(0, SHOW_MAX_RESULTS);
        this.storedSearches.set(searchTerm, {
          timeCached: Date.now(),
          searchResults
        });
      }

      return searchResults;
    }
    catch (error)
    {
      throw new Error(`HttpService failure: ${error}`);
    }
  }

  /**
   * Checks the cache to see if request had already been made before
   *
   * @param {String} searchTerm
   */
  hasSearchTerm(searchTerm)
  {
    return this.storedSearches.has(searchTerm);
  }

  /**
   * Set a timer to clean up the cache every 30 minutes
   */
  checkCache()
  {
    if (this.storedSearches.size > MAX_STORED_SEARCHES * 0.5)
    {
      this.cleanPreviousSearches();
    }
  }

  cleanPreviousSearches()
  {
    this.storedSearches.forEach((searchResultObj, searchTerm, searchMap) =>
    {
      if (Date.now() > searchResultObj.timeCached + SEARCH_RESULT_TTL)
      {
        searchMap.delete(searchTerm);
      }
    });
  }
}
