// Background
import { BackgroundGlobals } from '@extension/background/background_globals';
import { ContentCommands } from '@extension/common/commands';
import InjectBase from '@extension/background/content_injector/inject_base';

export default class InjectAutoPlayDetection extends InjectBase
{
  constructor()
  {
    super('content_autoplay_detection.js');
    this.pingCommand = ContentCommands.PING_CONTENT_AUTOPLAY_DETECTION;
  }

  check()
  {
    return BackgroundGlobals.AUTO_PLAY_NOTIFICATION_ENABLED;
  }
}
