// The cache where reputation of URLs are stored. There is a maximum number of
// reputations that can be stored. Each stored item has an associated time of
// insertion into cache. When the URL is accessed from cache, it checks whether
// reputation has expired. If expired the reputation for that URL is removed
// from cache, else the entry from cache is returned.

// Also keeps track of which GTI requests are in progress. This makes sure
// browser action is updated and the safety measures - isFrameBlocked()
// works if GTI response comes too early or too late.
// Also caches the response from the Typosquatting server as well as the content
// that should be rendered on the typosquatting block page.
import { GTIConstants } from './gti_contants';
import { GTIStorageVariables } from './gti_storage_variables';

/**
 * TODO: Comments
 */
export default class GTICache
{
  constructor(cacheStore)
  {
    this.urlMap = new Map();
    this.cacheStore = cacheStore;
  }

  clear()
  {
    this.urlMap = new Map();
    this.cacheStore.set(GTIStorageVariables.CACHE_GTI, new Map());
  }

  // Check to clean cache every hour
  setAlarmCacheClean()
  {
    // clear any pass created alarms
    chrome.alarms.clear(GTIConstants.reputationAlarmName);

    // create new alarm
    chrome.alarms.create(GTIConstants.reputationAlarmName, { delayInMinutes: 60 });

    // setup alarm listener
    chrome.alarms.onAlarm.addListener((alarmInfo) =>
    {
      if (alarmInfo.name !== GTIConstants.reputationAlarmName)
      {
        return;
      }

      // Clean reputation cache when it is over half full
      const cache = this.cacheStore.get(GTIStorageVariables.CACHE_GTI, new Map());

      if (Object.keys(cache).length > GTIConstants.maxCacheRecords * 0.5)
      {
        this.cleanReputationCache(cache);
      }
    });
  }

  // Look through the reputation cache and remove the expired entries
  cleanReputationCache(cache)
  {
    for (const [key, value] of Object.entries(cache))
    {
      if (Date.now() > GTIConstants.reputationTTL + value.timeCached)
      {
        delete (cache[key]);
      }
    }
    this.cacheStore.set(GTIStorageVariables.CACHE_GTI, cache);
  }

  checkRequest(requestdata)
  {
    const listOfPromises = [];
    requestdata.forEach((value) =>
    {
      if (this.urlMap.has(value))
      {
        listOfPromises.push(this.urlMap.get(value));
      }
    });
    return listOfPromises;
  }

  addRequest(requestdata, promise)
  {
    requestdata.forEach((value) =>
    {
      this.urlMap.set(value, promise);
    });
  }

  deleteRequest(requestdata)
  {
    requestdata.forEach((value) =>
    {
      this.urlMap.delete(value);
    });
  }

  setReputation(url, value)
  {
    const cache = this.cacheStore.get(GTIStorageVariables.CACHE_GTI, new Map());

    if (Object.keys(cache).length > GTIConstants.maxCacheRecords)
    {
      return false;
    }

    const timeCached = Date.now();
    cache[url] = { timeCached, value };
    this.cacheStore.set(GTIStorageVariables.CACHE_GTI, cache);
    return true;
  }

  // Get item from cache, and at that point if the timeout has expired then remove it from cache.
  getReputation(url)
  {
    const cache = this.cacheStore.get(GTIStorageVariables.CACHE_GTI, new Map());
    const cacheEntry = cache[url] ? cache[url] : null;
    const cacheObj = { cache_entry: cacheEntry };
    return cacheObj;
  }
}
