import { BackgroundGlobals } from '@extension/background/background_globals';
import { LocalStorageVariables } from '@extension/common/cache_store_variables';
import ScheduledTaskBase from '@extension/background/schedulers/scheduledtaskbase';
import { BuildSettings } from '@extension/common/constants';
import { Logger } from '@extension/common/logger';

export default class EngineDownloadTask extends ScheduledTaskBase
{
  init()
  {
    // Note that getEngines from EngineDownloadHelper is called on background's main() function
  }

  async run()
  {
    const serverUpdate = await this.fetchVersion();
    if (serverUpdate !== null && this.isNewerVersion(BackgroundGlobals.ENGINES_VERSION, serverUpdate.version)) // If the engine in server is newer one
    {
      const engines = await EngineDownloadHelper.fetchEngine(serverUpdate.url); // Download latest engine
      if (engines !== null)
      {
        BackgroundGlobals.searchEngines = engines; // Update the store cache and the version now.
        BackgroundGlobals.ENGINES_VERSION = serverUpdate.version;
        BackgroundGlobals.cacheStore.set(LocalStorageVariables.ENGINES.name, engines);
        BackgroundGlobals.cacheStore.set(LocalStorageVariables.ENGINES_VERSION.name, serverUpdate.version);
        Logger.log(`Engines have been updated to version ${serverUpdate.version}`);
      }
    }
  }

  async fetchVersion()
  {
    try
    {
      const response = await fetch(BackgroundGlobals.enginesVersionUrl);
      if (response !== null && response.status === 200)
      {
        return await response.json();
      }
    }
    catch (ex)
    {
      Logger.error(ex);
    }
    return null;
  }

  isNewerVersion(currentVersion, newVersion)
  {
    const curSplits = currentVersion.split('.');
    const newSplits = newVersion.split('.');
    const minLength = curSplits.length < newSplits.length ? curSplits.length : newSplits.length;
    for (let index = 0; index < minLength; ++index)
    {
      const curVer = parseInt(curSplits[index], 10);
      const newVer = parseInt(newSplits[index], 10);
      // If new version part is higher than current version part then this is new version

      if (newVer > curVer)
      {
        return true;
      }
      // If new version part is lower than current version part ( which is unlikely) then the new version is outdated one.
      if (newVer < curVer)
      {
        return false;
      }
    }
    return false;
  }
}

export class EngineDownloadHelper
{
  static async getEngines()
  {
    // At startup take engine from local Storage as first preference, if such an engine is non existent
    // then fallback to the engine packaged along with installer.
    BackgroundGlobals.searchEngines = BackgroundGlobals.cacheStore.get(LocalStorageVariables.ENGINES.name);
    if (BackgroundGlobals.searchEngines === null)
    {
      BackgroundGlobals.searchEngines = await EngineDownloadHelper.fetchEngine(chrome.runtime.getURL('json/engines.json'));
    }

    // If local storage has engine version then use it else use the default version from installed extension.
    BackgroundGlobals.ENGINES_VERSION = BackgroundGlobals.cacheStore.get(LocalStorageVariables.ENGINES_VERSION.name, BuildSettings.ENGINES_VERSION);
  }

  static async fetchEngine(url)
  {
    try
    {
      const response = await fetch(url);
      if (response !== null && response.status === 200)
      {
        const engineJSON = await response.json();
        if (engineJSON !== null && engineJSON.searchengines !== undefined)
        {
          return engineJSON.searchengines;
        }
      }
    }
    catch (ex)
    {
      Logger.error(ex);
    }
    return null;
  }
}
