// Background
import { BackgroundGlobals } from '@extension/background/background_globals';
// Common
import { BuildSettings, BrowserType } from '@extension/common/constants';
import { LocalStorageVariables } from '@extension/common/cache_store_variables';
import { extension } from '@extension/common/webextension';
import { AnnotationOptions } from '@extension/common/annotation_variables';
import { SCOPE, SETTINGS_TYPE } from '@extension/background/dispatchers/native/native_settings';

export default class FeatureSettings
{
  static uninstallSurvey()
  {
    if (BackgroundGlobals.browserType === BrowserType.CHROME)
    {
      const survey = BackgroundGlobals.nativeSettings.UNINSTALL_SURVEY_ENABLED.value;
      extension.runtime.setUninstallURL(survey ? BackgroundGlobals.nativeSettings.UNINSTALL_SURVEY_URL.value : '');
    }
  }

  static annotationOptions()
  {
    const showAnnotationsOnSSOnly = BackgroundGlobals.nativeSettings.SECURE_SEARCH_ANNOTATION_ONLY.value;
    const showIcons = BackgroundGlobals.nativeSettings.SECURE_SEARCH_SHOW_ICONS.value;
    let searchAnnotationOption = AnnotationOptions.NONE;
    if (BrowserType.EDGE === BuildSettings.BROWSER_TYPE)
    {
      searchAnnotationOption = BackgroundGlobals.cacheStore.get(LocalStorageVariables.SEARCH_ANNOTATION_OPTION.name, AnnotationOptions.ALL);
    }
    else if (showAnnotationsOnSSOnly && showIcons)
    {
      searchAnnotationOption = BackgroundGlobals.cacheStore.get(LocalStorageVariables.SEARCH_ANNOTATION_OPTION.name, AnnotationOptions.ONLY_SECURE_SEARCH);
    }
    else if (showIcons)
    {
      searchAnnotationOption = BackgroundGlobals.cacheStore.get(LocalStorageVariables.SEARCH_ANNOTATION_OPTION.name, AnnotationOptions.ALL);
    }

    const { SHOW_SEARCH_SETTINGS, CURRENT_SAO_IN_EXTENSION, SECURE_SEARCH_ANNOTATION_ONLY, SECURE_SEARCH_SHOW_ICONS } = BackgroundGlobals.nativeSettings;
    if (SHOW_SEARCH_SETTINGS.value === false && searchAnnotationOption === AnnotationOptions.ONLY_SECURE_SEARCH)
    {
      // If ShowSearchSettings is false and if the stored annotation option is Secure Search Only
      // Then we change the option to ALL since the annotation options would be selected but at the
      // same time hidden on the UI.
      searchAnnotationOption = AnnotationOptions.ALL;
    }

    // Update CurrentSAOInExtension state in native settings
    const isValidSAO = [AnnotationOptions.ONLY_SECURE_SEARCH, AnnotationOptions.NONE, AnnotationOptions.ALL].includes(CURRENT_SAO_IN_EXTENSION.value);
    if (!isValidSAO)
    {
      const setCurrentSAOInExtensionNativeSettings = {
        name: CURRENT_SAO_IN_EXTENSION.name,
        value: searchAnnotationOption,
        scope: SCOPE.USER,
        type: SETTINGS_TYPE.STRING
      };
      const setSecureSearchAnnotationOnlyNativeSettings = {
        name: SECURE_SEARCH_ANNOTATION_ONLY.name,
        value: showAnnotationsOnSSOnly,
        scope: SCOPE.SYSTEM,
        type: SETTINGS_TYPE.BOOL
      };
      const setSecureSearchShowIconsNativeSettings = {
        name: SECURE_SEARCH_SHOW_ICONS.name,
        value: showIcons,
        scope: SCOPE.USER,
        type: SETTINGS_TYPE.BOOL
      };
      BackgroundGlobals.messageDispatcher && BackgroundGlobals.messageDispatcher.resetNativeSetting(setCurrentSAOInExtensionNativeSettings);
      BackgroundGlobals.messageDispatcher && BackgroundGlobals.messageDispatcher.resetNativeSetting(setSecureSearchAnnotationOnlyNativeSettings);
      BackgroundGlobals.messageDispatcher && BackgroundGlobals.messageDispatcher.resetNativeSetting(setSecureSearchShowIconsNativeSettings);
    }
    else if (isValidSAO && CURRENT_SAO_IN_EXTENSION.value !== searchAnnotationOption)
    {
      searchAnnotationOption = CURRENT_SAO_IN_EXTENSION.value;
    }

    BackgroundGlobals.cacheStore.set(LocalStorageVariables.SEARCH_ANNOTATION_OPTION.name, searchAnnotationOption);
  }

  static setupFeatures()
  {
    FeatureSettings.uninstallSurvey();
    FeatureSettings.annotationOptions();
  }
}
