import { BrowserType, BuildSettings } from '@extension/common/constants';

const SUPPORTED_EXTENSION_LIST = {
  [BrowserType.CHROME]:
  {
    WebBoost: 'klekeajafkkpokaofllcadenjdckhinm',
    SearchExtension: 'enppghjcblldgigemljohkgpcompnjgh',
    NewTabExtension: 'kanjcmmieblbpbihaafnedamppkhfadn',
    AutomationExtension: 'bipjijaejfebbgbhchciejpabkhgpegh',
    EdgeWebAdvisorExtension: 'fdhgeoginicibhagdmblfikbgbkahibd',
    ChromeWebAdvisorExtension: 'fheoggkfdfchfphceeifdbepaooicaho',
  },
  [BrowserType.FIREFOX]:
  {
    AutomationExtension: '{cdaf3803-bb0b-41e2-b369-a504caff36da}',
  },
  [BrowserType.EDGE]:
  {
    AutomationExtension: 'lamehkegphbbfdailghaeeleoajilfho',
  },
};

const supportedExtensions = SUPPORTED_EXTENSION_LIST[BuildSettings.BROWSER_TYPE];

/**
 * TODO: Comments
 */
export default class ExternalExtensions
{
  static isWhiteListed(extnId)
  {
    return supportedExtensions && Object.values(supportedExtensions).includes(extnId);
  }

  static get(extnName)
  {
    return supportedExtensions[extnName];
  }

  static getExtnIds()
  {
    return Object.values(supportedExtensions);
  }
}
