import Logger from '../logger';

export default class TransportBase
{
  constructor()
  {
    this.name = null;
    this.dictionary = null;
    this.transportConfig = null;
  }

  /**
   * Initializing the transport mapped and store variables
   * @param {String} name
   * @param {String} dictionary - JSON stringified dictionary field from profile
   * @param {String} config - JSON strigified transportConfig from profile
   */
  initialize(name, dictionary, transportConfig)
  {
    this.dictionary = JSON.parse(dictionary); // Can be an empty object
    this.transportConfig = JSON.parse(transportConfig);
    this.name = name;

    if (!this.transportConfig || !this.name)
    {
      Logger.error(`TransportPlugin_Template: Failed to initialize..\nConfig: ${transportConfig}\nName: ${this.name}`);
      return false;
    }

    return true;
  }
}
