import Logger from './logger';

export default class OperationsManager
{
  /**  Applies an operation configuration to a value
   *
   *  PARAMS:
   *      @param {string} value: Value to be operated on
   *      @param {string} operationConfig: Defines the operation name and any required params
   *
   *  RETURN:
   *      postOperation value (null if exception caught)
   *
   **/
  static apply(value, operationConfig)
  {
    try
    {
      if (!operationConfig)
      {
        return value;
      }
      if (!OperationsManager._isValidValue(value))
      {
        OperationsManager._logWarn(`Invalid value Val(${value}). Operation with operationConfig(${JSON.stringify(operationConfig)}) will not be applied`);
        return null;
      }
      return this[operationConfig.name](value, operationConfig.params);
    }
    catch (e)
    {
      OperationsManager._logError(`operations:apply: Excption caught(${e.message}. Val(${value}), operationConfig(${JSON.stringify(operationConfig)})`);
      return null;
    }
  }

  /* No operation
  */
  static noop(value) { return value; }

  /* Equal operation
  */
  static equal(value, param) { return value == param; }

  /* Valid value operation
  */
  static isValueValid(value)
  {
    return (value != '[not assigned]') && (value != '[ruleMismatch]') && (value != '[ruleError]');
  }

  /* Returns true if value is not null, false otherwise
  */
  static notNull(value) { return (value != null); }

  /* Return value at index params
  */
  static at(value, params)
  {
    if (!value || !OperationsManager._isValidValue(value))
    {
      return null;
    }

    return value[params];
  }

  /* Combines day, month and year into a date
  */
  static combineCustomDate(value, params)
  {
    //should be same size
    if (findObjectSize(value) != findObjectSize(params))
    {
      return null;
    }
    return `${value.InstalledYear}-${value.InstalledMonth}-${value.InstalledDate}`;
  }

  /* Splits value based on params and returns the result
  */
  static split(value, params)
  {
    return value.split(params);
  }

  /*  Return first not null item in values (values is expected to be an array or object)
  */
  static first(values)
  {
    for (const idx in values)
    {
      if (values[idx] != null)
      {
        return values[idx];
      }
    }
    return null;
  }

  /* Apply a mask operation between value and params
  */
  static mask(value, params)
  {
    return value & params;
  }

  /* Applies logical OR between the values in array or object
  */
  static or(values)
  {
    for (const idx in values)
    {
      if (values[idx])
      {
        return true;
      }
    }
    return false;
  }

  /* Applies logical AND between the values in array or object
  */
  static and(values)
  {
    for (const idx in values)
    {
      if (!values[idx])
      {
        return false;
      }
    }
    return true;
  }

  /* Returns value*params
  */
  static multiply(value, params)
  {
    return parseInt(value, 10) * params;
  }

  /* Returns value/params
  */
  static divide(value, params)
  {
    return parseInt(value, 10) / params;
  }

  /* Joins value based on params (value is expected to be an array)
  */
  static join(value, params)
  {
    return value.join(params);
  }

  /*
  * Used to add "0x" to OS locale from windows WMI e.g cleanLocaleCode("0320") ==> "0x0320"
  */
  static cleanLocaleCode(value, params)
  {
    try
    {
      if (value)
      {
        //value should be a string
        if (typeof value !== 'string')
        {
          value = String(value);
        }

        //It's a 16 bit hex value,append 0x
        if (value.length == 4)
        {
          value = `0x${value}`;
        }
      }
    }
    catch (e)
    {
      OperationsManager._logError(`splitLocaleCode exception: ${e.message}`);
    }

    return value;
  }

  /* Returns number of days until a value
  */
  static daysTo(value)
  {
    if (typeof value.getTime !== 'function')
    {
      OperationsManager._logError(`daysTo: value is not datetime object (${value})`);
      return null;
    }
    return OperationsManager._getDaysBetween(value, null);
  }

  /* Returns a value rounded up/down
  * params is in the form {multiple: multipleValue, roundUp: flag to detonate rounding up or down}
  */
  static round(value, params)
  {
    try
    {
      if (!params)
      {
        return value;
      }

      var { multiple } = params;
      var { roundUp } = params;
      if (!multiple)
      {
        return value;
      }

      const roundDown = parseInt((value / multiple), 10) * multiple;
      if (roundUp == true)
      {
        return roundDown + multiple;
      }

      return roundDown;
    }
    catch (e)
    {
      OperationsManager._logError(`roundtoNearestMultiple: { numToRound: ${value}, multiple: ${multiple}, roundUp: ${roundUp} } exception caught with message: ${e.message}`);
      return value;
    }
  }

  /* Returns time since a value
  * params can be used to return time in (days, minutes, hours)
  */
  static timeSince(value, params)
  {
    if ((value == null) || (typeof value.getTime !== 'function'))
    {
      OperationsManager._logError(`daysTo: value is not datetime object (${value})`);
      return null;
    }

    const one_minute_ms = 1000 * 60;
    const one_hour_ms = one_minute_ms * 60;
    const one_day_ms = one_hour_ms * 24;

    const timeNow = Date.now();

    const difference_ms = timeNow - value.getTime();

    if (!params || params === 'days')
    {
      return parseInt(difference_ms / one_day_ms, 10); // days
    }

    if (params === 'hours')
    {
      return parseInt((difference_ms % one_day_ms) / one_hour_ms, 10); // hours
    }

    if (params === 'minutes')
    {
      return parseInt(((difference_ms % one_day_ms) % one_hour_ms) / one_minute_ms, 10); // minutes
    }

    return null;
  }

  /* Returns value for given key
  * params stringified json object
  */
  static getKeyFromJson(value, params)
  {
    try
    {
      const data = JSON.parse(value);
      return data[params.key];
    }
    catch (e)
    {
      OperationsManager._logError(`getKeyFromJson: exception (${e.message})`);
      return null;
    }
  }

  /* Transforms the value based on the params
  */
  static transform(value, params)
  {
    return params[value];
  }


  /* Converts a Date object to epoch
  */
  static dateToEpoch(value)
  {
    return value.getTime() / 1000;
  }

  /* Converts a epoch to ISO date string
  */
  static epochToISODate(value)
  {
    if (!value) return null;

    const dateValue = new Date(0);
    dateValue.setUTCSeconds(value);
    return dateValue.toISOString();
  }

  /* Converts a Date object to ISO string
  *  if params === dataTime, the function will return ISO datetime format
  */
  static dateToISO(value, params)
  {
    if (!value || !OperationsManager._isValidValue(value))
    {
      return null;
    }

    if (params && params === 'dateTime')
    {
      return value.toISOString();
    }
    return value.toISOString(true);
  }

  /* Converts WSS date format to Date object
  */
  static wssDateToDate(value)
  {
    if (!value || (value.length != 8))
    {
      return null;
    }

    const year = parseInt(value.substr(0, 4), 10);
    const month = parseInt(value.substr(4, 2), 10);
    const day = parseInt(value.substr(6, 2), 10);
    if (!year || !month || !day)
    {
      return null;
    }

    return new Date(year, month - 1, day);
  }

  /*
  */
  static _isValidValue(value)
  {
    if (value == '[not assigned]' || value == '[ruleMismatch]' || value == '[ruleError]')
    {
      return false;
    }
    return true;
  }

  static _getDaysBetween(date2, date1)
  {
    try
    {
      //Get 1 day in milliseconds
      const one_day = 1000 * 60 * 60 * 24;

      // Convert both dates to milliseconds
      const date2_ms = date2.getTime();

      let date1_ms;
      if (!date1)
      {
        date1_ms = Date.now();
      }
      else
      {
        date1_ms = date1.getTime();
      }

      // Calculate the difference in milliseconds
      const difference_ms = date2_ms - date1_ms;

      // Convert back to days and return
      return parseInt((difference_ms / one_day), 10);
    }
    catch (e)
    {
      OperationsManager._logError(`Exception caught while calculating difference between dates: ${e.message}`);
    }

    return null;
  }

  // Map id to index in array (params)
  static containsIn(value, params)
  {
    const Ivalue = parseInt(value, 10);
    if (params[Ivalue] !== undefined)
    {
      return params[Ivalue];
    }
    return null;
  }

  // Convert a date string (YYYYMMDDhhmmss) to iso date time
  static stringToDate(value)
  {
    try
    {
      if (!value || (value.length < 14))
      {
        return null;
      }

      const year = parseInt(value.substr(0, 4), 10);
      const month = parseInt(value.substr(4, 2), 10);
      const day = parseInt(value.substr(6, 2), 10);
      const hour = parseInt(value.substr(8, 2), 10);
      const minute = parseInt(value.substr(10, 2), 10);
      const second = parseInt(value.substr(12, 2), 10);

      const invalidDates = !year || !month || !day || !hour || !minute || !second;
      if (invalidDates)
      {
        return null;
      }

      return new Date(year, month - 1, day, hour, minute, second);
    }
    catch (e)
    {
      OperationsManager._logError(`Exception caught in stringToDate: ${e.message}`);
    }
    return null;
  }

  static stringToISODate(params)
  {
    return OperationsManager.dateToISO(OperationsManager.stringToDate(params), 'dateTime');
  }

  static _logInfo(msg)
  {
    Logger.log(`operations: ${msg}`);
  }

  static _logError(msg)
  {
    Logger.error(`operations: ${msg}`);
  }

  static _logWarn(msg)
  {
    Logger.warn(`operations: ${msg}`);
  }
}
