import { TelemetryGlobals } from './telemetry_globals';

const EXTN_TELEMETRY = 'EXTN_TELEMETRY';
const LOG_COLOR = '#036C5C';

export default class Logger
{
  static log(msg)
  {
    if (typeof TelemetryGlobals.externalLogger.log !== 'function')
    {
      console.log(`log: ${msg}`);
    }
    else
    {
      TelemetryGlobals.externalLogger.log(msg, EXTN_TELEMETRY, LOG_COLOR);
    }
  }

  static error(msg)
  {
    if (typeof TelemetryGlobals.externalLogger.error !== 'function')
    {
      console.log(`error: ${msg}`);
    }
    else
    {
      TelemetryGlobals.externalLogger.error(msg, EXTN_TELEMETRY, LOG_COLOR);
    }
  }

  static warn(msg)
  {
    if (typeof TelemetryGlobals.externalLogger.warn !== 'function')
    {
      console.log(`warn: ${msg}`);
    }
    else
    {
      TelemetryGlobals.externalLogger.warn(msg, EXTN_TELEMETRY, LOG_COLOR);
    }
  }

  static debug(msg)
  {
    if (typeof TelemetryGlobals.externalLogger.debug !== 'function')
    {
      console.log(`debug: ${msg}`);
    }
    else
    {
      TelemetryGlobals.externalLogger.debug(msg, EXTN_TELEMETRY, LOG_COLOR);
    }
  }


  static logRawTrack()
  {
    // TODO
  }

  static logAutomationError()
  {
    // TODO
  }
}
