import { LocalStorageVariables } from '@extension/common/cache_store_variables';
import EngineDownloadTask from '@extension/background/schedulers/enginedownloadtask';
import TelemetryHeartBeatTask from '@extension/background/schedulers/telemetryheartbeattask';

export const schedulers = {
  [LocalStorageVariables.ENGINE_DOWNLOAD_TASK.name]: { frequency: 1000 * 60 * 60 * 24, runImmediatelyInitially: true, enabled: true, task: new EngineDownloadTask() },
  [LocalStorageVariables.TELEMETRY_HEARTBEAT_TASK.name]: { frequency: 1000 * 60 * 60 * 24, runImmediatelyInitially: true, enabled: false, task: new TelemetryHeartBeatTask() }
};
