// Background
import { BackgroundGlobals } from '@extension/background/background_globals';
// Common
import { LocalStorageVariables } from '@extension/common/cache_store_variables';

export const featureStatsTypes = {
  PAGES_SCANNED: 'PAGES_SCANNED',
  PAGES_BLOCKED: 'PAGES_BLOCKED',
  DOWNLOADS_SCANNED: 'DOWNLOADS_SCANNED',
  DOWNLOADS_BLOCKED: 'DOWNLOADS_BLOCKED',
  CRYPTO_BLOCKED: 'CRYPTO_BLOCKED',
};

/**
 * TODO: Comment
 */
export default class FeatureStats
{
  static init()
  {
    const cacheFeatureStats = this.read();

    // Try to sync with native's feature stats first
    // Otherwise fallback to the feature stats stored previously in cache
    const {
      PAGES_SCANNED, PAGES_BLOCKED, DOWNLOADS_SCANNED,
      DOWNLOADS_BLOCKED, CRYPTO_BLOCKED
    } = BackgroundGlobals.nativeSettings;
    const featureStats = {
      pagesScanned: PAGES_SCANNED.value || cacheFeatureStats.pagesScanned,
      pagesBlocked: PAGES_BLOCKED.value || cacheFeatureStats.pagesBlocked,
      downloadsScanned: DOWNLOADS_SCANNED.value || cacheFeatureStats.downloadsScanned,
      downloadsBlocked: DOWNLOADS_BLOCKED.value || cacheFeatureStats.downloadsBlocked,
      cryptoBlocked: CRYPTO_BLOCKED.value || cacheFeatureStats.cryptoBlocked,
    };

    BackgroundGlobals.cacheStore.set(LocalStorageVariables.POPUP_STATS.name, featureStats);
  }

  static add(type)
  {
    const featureStats = this.read();

    switch (type)
    {
      case featureStatsTypes.PAGES_SCANNED:
        featureStats.pagesScanned++;
        break;
      case featureStatsTypes.PAGES_BLOCKED:
        featureStats.pagesBlocked++;
        break;
      case featureStatsTypes.DOWNLOADS_SCANNED:
        featureStats.downloadsScanned++;
        break;
      case featureStatsTypes.DOWNLOADS_BLOCKED:
        featureStats.downloadsBlocked++;
        break;
      case featureStatsTypes.CRYPTO_BLOCKED:
        featureStats.cryptoBlocked++;
        break;
      default:
        return;
    }

    BackgroundGlobals.cacheStore.set(LocalStorageVariables.POPUP_STATS.name, featureStats);
  }

  static read()
  {
    const featureStats = {};
    featureStats.pagesScanned = 0;
    featureStats.pagesBlocked = 0;
    featureStats.downloadsScanned = 0;
    featureStats.downloadsBlocked = 0;
    featureStats.cryptoBlocked = 0;

    const cacheStats = BackgroundGlobals.cacheStore.get(LocalStorageVariables.POPUP_STATS.name);
    if (cacheStats)
    {
      Object.assign(featureStats, cacheStats);
    }

    return featureStats;
  }
}
