import Logger from '../../Common-Utils-Package/logger/logger';
import { HTICommands } from './hti_commands';
import { HTIGlobals } from './hti_globals';

export default class HTIBackgroundListener
{
  constructor(promisesToComplete)
  {
    this.promisesToComplete = promisesToComplete || null;
  }

  init()
  {
    chrome.runtime.onMessage.addListener((request, sender, response) =>
    {
      try
      {
        const sendResponse = (details = null) =>
        {
          response(details);
        };

        this.executeCommand(request, sender, sendResponse);
      }
      catch (error)
      {
        Logger.error(`HTIBackgroundListener addListener err, ${error.message}`);
        return false;
      }

      return true;
    });
  }

  async executeCommand(request, sender, sendResponse)
  {
    // Making sure all the promises that needs to be initialized is complete
    if (this.promisesToComplete)
    {
      await this.promisesToComplete;
    }

    this[request.command] && this[request.command]({ request, sender, sendResponse });
  }

  async [HTICommands.HTI_REQUEST]({ request, sendResponse })
  {
    const { requestData, referrer, checkCacheOnly } = request;

    const options = { referrer, checkCacheOnly };
    const promise = new Promise((resolve) => HTIGlobals.htiRequestor.post(requestData, resolve, options));

    const response = await promise;
    sendResponse(response);
  }
}
