// Background
import { BackgroundGlobals } from '@extension/background/background_globals';
import FeatureStats, { featureStatsTypes } from '@extension/background/feature_stats';
import WhitelistWrapper from '@extension/background/whitelist_wrapper';
import NativeSetValues from '@extension/background/dispatchers/native/native_set_values';
import { SETTINGS_TYPE, SCOPE } from '@extension/background/dispatchers/native/native_settings';
import { NativeCommands } from '@extension/background/dispatchers/native/native_message_dispatcher_v1';
import { extension } from '@extension/common/webextension';
import { BlockState, BrowserType, Color, WPSBlockPageType } from '@extension/common/constants';
import { EngineType, SearchAnnotationActions, AnnotationOptions } from '@extension/common/annotation_variables';
import { SocialMediaTypes, SocialMediaAllTypes, SocialMediaActions } from '@extension/common/sma_variables';
import { ContentCommands } from '@extension/common/commands';
import { WhitelistTypes, WhitelistActions, WhitelistStoreMappings } from '@extension/common/whitelist_variables';
import { LocalStorageVariables, CacheStoreActions, CacheStorageVariables } from '@extension/common/cache_store_variables';
import BackgroundCommon from '@extension/common/background_common';
import UrlParser from '@extension/common/url_parser';
import { WSSNativeCommands, SubRequestTypes } from '@extension/background/dispatchers/native/native_wss_message_dispatcher';
import { ProcessTypes, Logger } from '@extension/common/logger';
// Injector
import Injector from '@extension/background/content_injector/injector';
import InjectAPSBalloon from '@extension/background/content_injector/inject_aps_balloon';
import InjectSidebarMain from '@extension/background/content_injector/inject_sidebar_main';
import InjectFormDetection from '@extension/background/content_injector/inject_form_detection';

import SearchEngine from '@core-engine/search_engine';
import BlockPageCommon from '../block_page_common';

// Native settings
const { INT, STRING, BOOL } = SETTINGS_TYPE;
const { SYSTEM, USER } = SCOPE;
const {
  SECURE_SEARCH_TOAST_STAMP,
  SECURE_SEARCH_SHOW_ICONS,
  SECURE_SEARCH_ANNOTATION_ONLY,
  ANNOTATIONS_CHOICE_EXPLICIT,
  CURRENT_SAO_IN_EXTENSION,
  TS_WHITELIST,
  CHECKLIST_START_INGORE_DATE_AV,
  CHECKLIST_START_IGNORE_DATE_FW,
  CHECKLIST_IGNORE_DURACTION,
  CHECKLIST_GRACE_PERIOD_AV,
  CHECKLIST_GRACE_PERIOD_FW,
  CHECKLIST_GRACE_PERIOD_DURATION,
  SMA_ANNOTATION_OPTIONS,
  SMA_USER_TOGGLE,
  FF_EXTENSION_LAST_TOAST_TIME,
  FF_EXTENSION_TOAST_COUNT,
  ANNOTATION_JOURNEY_TOAST
} = BackgroundGlobals.nativeSettings;

/**
 * Handles the incoming commands from the message listener.
 */
export default class LocalDispatcher
{
  constructor(nativeDisp)
  {
    this.nativeDispatcher = nativeDisp;
  }

  /**
   * TODO: Comments
   */
  autoPlayVideoSite(topDomain, srcVideo, x, y)
  {
    const msg = {
      request_type: NativeCommands.AUTO_PLAY_VIDEO_SITE,
      payload: {
        site_domain: topDomain,
        video_url: srcVideo,
        coordinate_x: x,
        coordinate_y: y
      }
    };
    Logger.log(`Autoplay site found in page ${topDomain}`);

    this.nativeDispatcher && this.nativeDispatcher.autoPlayVideoSite(msg);
  }

  /**
   * Get/Set/Clear cache/local storage variables. Allow multiple set and get
   * @param {string} action - one of CacheStoreActions (GET, SET, CLEAR, etc)
   * @param {object} data   - data object. Should look like the following:
   *                        SET: { DBNAME: 'ABC', ... }, GET: { DBNAME: '', ... }, CLEAR: { DBNAME: '', ... }
   * @param {callback} callback - callback with data object for GET action, otherwise true
   *                        GET: { DBNAME: 'ABC', ... }
   */
  cacheStore(action, data, callback)
  {
    const { SET, GET, CLEAR } = CacheStoreActions;
    if (action === GET)
    {
      for (const prop in data)
      {
        data[prop] = BackgroundGlobals.cacheStore.get(prop, data[prop]);
      }
      callback(data);
      return true;
    }

    if (action === SET && data)
    {
      for (const prop in data)
      {
        BackgroundGlobals.cacheStore.set(prop, data[prop]);
      }
    }

    if (action === CLEAR)
    {
      for (const prop in data) BackgroundGlobals.cacheStore.clear(prop);
    }

    callback(true);
    return false;
  }

  /**
   * TODO: Comments
   */
  executeCommand(commandId, params)
  {
    this.nativeDispatcher && this.nativeDispatcher.executeCommand(commandId, params);
  }

  extensionStatusUpdate({ extensionId })
  {
    this.nativeDispatcher && this.nativeDispatcher.executeCommand({ extensionId });
  }

  /**
   * TODO: Comments
   */
  getPopupData(id, url, callback)
  {
    const data = {};
    data.stats = FeatureStats.read();
    data.url = url;
    data.tabData = BackgroundGlobals.tabData.get(id);
    data.tabId = id;
    data.BINARY_VERSION = BackgroundGlobals.nativeSettings.BINARY_VERSION.value;
    data.cacheStore = BackgroundGlobals.cacheStore;

    callback(data);
  }

  /**
   * TODO: Comments
   */
  getSettingsData(callback)
  {
    const { cacheStore } = BackgroundGlobals;

    const settingsData = {};
    const whitelistedLinks = WhitelistWrapper.getDomainsFromWhitelist(LocalStorageVariables.BLOCK_PAGE_WHITELIST.name);
    const searchAnnotationOption = cacheStore.get(LocalStorageVariables.SEARCH_ANNOTATION_OPTION.name, AnnotationOptions.ONLY_SECURE_SEARCH);
    const socialMediaOption = cacheStore.get(LocalStorageVariables.SMA_ANNOTATION_OPTIONS.name, SocialMediaAllTypes);
    const socialMediaToggle = cacheStore.get(LocalStorageVariables.SMA_USER_TOGGLE.name, true);
    const personal = cacheStore.get(LocalStorageVariables.FF_POLICY_PERSONAL_COLLECTION.name, true);
    const activity = cacheStore.get(LocalStorageVariables.FF_POLICY_ACTIVITY_COLLECTION.name, true);
    const apsToggle = cacheStore.get(LocalStorageVariables.APS_TOGGLE_SETTING.name, true);
    const ajToast = cacheStore.get(LocalStorageVariables.AJ_TOAST.name, null);
    const ajAPSEnable = cacheStore.get(LocalStorageVariables.AJ_APS_ENABLE.name, null);

    settingsData.whitelistedLinks = whitelistedLinks;
    settingsData.searchAnnotationOption = searchAnnotationOption;
    settingsData.sma = { socialMediaOption, socialMediaToggle };
    settingsData.ffpolicy = { personal, activity };
    settingsData.apsToggle = apsToggle;
    settingsData.ajToast = ajToast !== null ? ajToast : ANNOTATION_JOURNEY_TOAST.value;
    settingsData.ajAPSEnable = ajAPSEnable;

    // Statistics Data
    settingsData.stats = FeatureStats.read();
    callback(settingsData);
  }

  postMessage(message)
  {
    return this.nativeDispatcher && this.nativeDispatcher.postMessage(message);
  }

  /**
   * Make a HTTP POST request to the GTI server or HTI server.
   * @param {string[]} requestData - Data to pass into the request
   * @param {string}   referrer - value of referer header or undefined
   * @param {boolen}   checkCacheOnly - only get URL reputation from cache and not do an actual HTTP request call
   *
   * @return {promise} promise containing GTI request data
   */
  makeMTIRequest(requestData, referrer = null, checkCacheOnly = false)
  {
    return BackgroundGlobals.mtiRequestor.post(requestData, referrer, checkCacheOnly);
  }

  async getURLReputation(originalUrl, referrer = null, checkCacheOnly = false)
  {
    const response = { color: Color.UNKNOWN, categories: [] };

    let url = originalUrl;
    if (!BackgroundCommon.isProperWebURL(url))
    {
      return response;
    }

    if (BackgroundCommon.isSafeUrl(url))
    {
      response.color = Color.GREEN;
      response.categories = [];
      return response;
    }

    if (BackgroundGlobals.browserType === BrowserType.FIREFOX)
    {
      url = this.removeSearchTermOnEngineUrl(originalUrl);
    }

    if (!BackgroundGlobals.messageDispatcher)
    {
      Logger.warn('getURLReputation: messageDispatcher is undefined');
      return response;
    }

    const repResponse = await this.makeMTIRequest([url], referrer, checkCacheOnly);

    if (!repResponse || !repResponse.annotationMap)
    {
      Logger.warn('getURLReputation: repResponse or annotationMap is undefined');
      return response;
    }

    const annotationDetails = repResponse.annotationMap[url];
    if (!annotationDetails)
    {
      Logger.warn('getURLReputation: makeMTIRequest annotation details undefined');
      return response;
    }

    const blockState = BackgroundGlobals.messageDispatcher.toBlockState(annotationDetails);
    response.color = blockState.color;
    response.categories = annotationDetails.category || [];
    return response;
  }

  /**
   * TODO: Comments
   */
  isFrameBlocked(url, tabId)
  {
    const domain = UrlParser.getDomain(url);
    if (WhitelistWrapper.isDomainInWhitelist(domain, LocalStorageVariables.BLOCK_PAGE_WHITELIST.name))
    {
      return { isBlocked: false, redirectURI: url };
    }

    const tabData = BackgroundGlobals.tabData.get(tabId);
    if (tabData.tabDomainTrusted)
    {
      return { isBlocked: false, redirectURI: url };
    }

    const data = BackgroundGlobals.mtiRequestor.getReputation(url);
    if (!data || !data.cache_entry)
    {
      return { isBlocked: false, redirectURI: url };
    }

    const blockState = this.toBlockState(data.cache_entry.value);
    if (blockState.color === Color.RED || blockState.color === Color.YELLOW)
    {
      return { isBlocked: true, redirectURI: url };
    }
    return { isBlocked: false, redirectURI: url };
  }

  /**
   * TODO: Comments
   */
  installExtension(extensionId, redirectUrl)
  {
    this.nativeDispatcher && this.nativeDispatcher.installExtension(extensionId, redirectUrl);
  }

  /**
   * TODO: Comments
   */
  logger(details)
  {
    this.nativeDispatcher && this.nativeDispatcher.logger(details);
  }

  /**
   * TODO: Comments
   */
  onDownloadFinished(item)
  {
    const msg = {
      request_type: NativeCommands.ON_DOWNLOAD_FINISHED,
      id: 0,
      payload: {
        url: item.url,
        file_path: item.filename,
        referrer: item.referrer || '',
        mime_type: item.mime_type || '',
        ext_side_ui: false,
      }
    };

    if (this.nativeDispatcher)
    {
      // Send message to native to start scanning the downloaded file
      this.nativeDispatcher.onDownloadFinished(msg);
      FeatureStats.add(featureStatsTypes.DOWNLOADS_SCANNED);
    }
  }

  /**
   * TODO: Comments
   */
  resetSettings()
  {
    // Reset extension settings
    BackgroundGlobals.cacheStore.clear(LocalStorageVariables.RAT_DETECTION_WHITELIST.name);
    BackgroundGlobals.cacheStore.clear(LocalStorageVariables.APS_FULL_MSG_SHOWN_ARRAY.name);

    // Reset native settings
    const settings = new NativeSetValues(this.nativeDispatcher);
    settings.add(SECURE_SEARCH_TOAST_STAMP.name, 0, USER, INT);
    settings.add(TS_WHITELIST.name, '', USER, STRING);
    settings.add(CHECKLIST_START_INGORE_DATE_AV.name, '', SYSTEM, STRING);
    settings.add(CHECKLIST_START_IGNORE_DATE_FW.name, '', SYSTEM, STRING);
    settings.add(CHECKLIST_IGNORE_DURACTION.name, '14', SYSTEM, STRING);
    settings.add(CHECKLIST_GRACE_PERIOD_AV.name, '', SYSTEM, STRING);
    settings.add(CHECKLIST_GRACE_PERIOD_FW.name, '', SYSTEM, STRING);
    settings.add(CHECKLIST_GRACE_PERIOD_DURATION.name, '24', SYSTEM, STRING);

    // Reset firefox toast timers
    settings.add(FF_EXTENSION_LAST_TOAST_TIME.name, 0, USER, INT);
    settings.add(FF_EXTENSION_TOAST_COUNT.name, 0, USER, INT);
    settings.send();

    // Send MFW event to let native reset additional native settings
    this.nativeDispatcher && this.nativeDispatcher.mfwResetNativeSettings();
  }

  /**
   * Reset specific setting from native
   */
  resetNativeSetting(setting)
  {
    if (!setting) return;

    const { name, value, scope, type } = setting;
    const settings = new NativeSetValues(this.nativeDispatcher);
    settings.add(name, value, scope, type);
    settings.send();
  }

  /**
   * TODO: Comments
   */
  searchAnnotation(action, data, callback)
  {
    const { cacheStore } = BackgroundGlobals;
    const { GET, SET } = SearchAnnotationActions;
    if (action === GET)
    {
      const searchAnnotationOption = cacheStore.get(LocalStorageVariables.SEARCH_ANNOTATION_OPTION.name, AnnotationOptions.ONLY_SECURE_SEARCH);
      callback(searchAnnotationOption);
      return true;
    }

    const searchOption = data.searchAnnotationOption;
    if (action === SET && searchOption)
    {
      cacheStore.set(LocalStorageVariables.SEARCH_ANNOTATION_OPTION.name, searchOption);

      // Update Native search annotation option
      let secureSearchOnly = false;
      let secureSearchIcons = false;
      if (searchOption === AnnotationOptions.ALL)
      {
        secureSearchIcons = true;
      }
      else if (searchOption === AnnotationOptions.ONLY_SECURE_SEARCH)
      {
        secureSearchOnly = true;
        secureSearchIcons = true;
      }

      const settings = new NativeSetValues(this.nativeDispatcher);
      settings.add(SECURE_SEARCH_ANNOTATION_ONLY.name, secureSearchOnly, SYSTEM, BOOL);
      settings.add(SECURE_SEARCH_SHOW_ICONS.name, secureSearchIcons, USER, BOOL);
      settings.add(ANNOTATIONS_CHOICE_EXPLICIT.name, true, SYSTEM, BOOL);
      settings.add(CURRENT_SAO_IN_EXTENSION.name, searchOption, USER, STRING);
      settings.send();
    }

    callback(true);
    return false;
  }

  /**
   * TODO: Comments
   */
  sendTelemetry(data)
  {
    Logger.log(data, ProcessTypes.TELEMETRY);

    const msg = {
      request_type: NativeCommands.TELEMETRY,
      id: 0,
      payload: {
        telemetry_data: data
      }
    };
    this.nativeDispatcher && this.nativeDispatcher.sendTelemetry(msg);
  }

  /**
   * TODO: Comments
   */
  SetXYViewport(xView, yView)
  {
    this.nativeDispatcher && this.nativeDispatcher.SetXYViewport(xView, yView);
  }

  /**
   * TODO: Comments
   */
  socialMediaAnnotation(action, data, callback)
  {
    const { cacheStore } = BackgroundGlobals;
    const socialMediaOption = cacheStore.get(LocalStorageVariables.SMA_ANNOTATION_OPTIONS.name, SocialMediaAllTypes);
    const socialMediaToggle = cacheStore.get(LocalStorageVariables.SMA_USER_TOGGLE.name, true);

    const {
      TOGGLE_ON,
      TOGGLE_OFF,
      ANNOTATION_ENABLE,
      ANNOTATION_ENABLE_ALL,
      ANNOTATION_DISABLE,
      ANNOTATION_DISABLE_ALL,
      GET
    } = SocialMediaActions;

    if (action === GET)
    {
      callback({ socialMediaOption, socialMediaToggle });
      return true;
    }

    if (action === TOGGLE_ON)
    {
      // Used by Telemetry
      const settings = new NativeSetValues(this.nativeDispatcher);
      settings.add(SMA_USER_TOGGLE.name, true, SYSTEM, BOOL);
      settings.send();

      cacheStore.set(LocalStorageVariables.SMA_USER_TOGGLE.name, true);
      callback({ socialMediaOption, socialMediaToggle: true });
      return true;
    }

    if (action === TOGGLE_OFF)
    {
      // Used by Telemetry
      const settings = new NativeSetValues(this.nativeDispatcher);
      settings.add(SMA_USER_TOGGLE.name, false, SYSTEM, BOOL);
      settings.send();

      cacheStore.set(LocalStorageVariables.SMA_USER_TOGGLE.name, false);
    }

    if (action === ANNOTATION_ENABLE_ALL)
    {
      // Used by Telemetry
      const settings = new NativeSetValues(this.nativeDispatcher);
      settings.add(SMA_ANNOTATION_OPTIONS.name, SocialMediaAllTypes, SYSTEM, INT);
      settings.send();

      cacheStore.set(LocalStorageVariables.SMA_ANNOTATION_OPTIONS.name, SocialMediaAllTypes);
    }

    if (action === ANNOTATION_DISABLE_ALL)
    {
      const smaOption = SocialMediaTypes.NONE;

      // Used by Telemetry
      const settings = new NativeSetValues(this.nativeDispatcher);
      settings.add(SMA_ANNOTATION_OPTIONS.name, smaOption, SYSTEM, INT);
      settings.send();

      cacheStore.set(LocalStorageVariables.SMA_ANNOTATION_OPTIONS.name, smaOption);
    }

    if (action === ANNOTATION_ENABLE)
    {
      const { socialMediaType } = data;
      const existsInTypes = Object.values(SocialMediaTypes).includes(socialMediaType);
      // Only add if it is one of the annotation types
      // and it has not yet been added to the db.
      if (existsInTypes && !(socialMediaType & socialMediaOption))
      {
        const smaOption = socialMediaOption + socialMediaType;
        cacheStore.set(LocalStorageVariables.SMA_ANNOTATION_OPTIONS.name, smaOption);

        // Used by Telemetry
        const settings = new NativeSetValues(this.nativeDispatcher);
        settings.add(SMA_ANNOTATION_OPTIONS.name, smaOption, SYSTEM, INT);
        settings.send();
      }
    }

    if (action === ANNOTATION_DISABLE)
    {
      const { socialMediaType } = data;
      const existsInTypes = Object.values(SocialMediaTypes).includes(socialMediaType);

      // Only add if it is one of the annotation types
      // and if it exists as part of the db value
      if (existsInTypes && (socialMediaType & socialMediaOption))
      {
        const smaOption = socialMediaOption - socialMediaType;
        cacheStore.set(LocalStorageVariables.SMA_ANNOTATION_OPTIONS.name, smaOption);

        // Used by Telemetry
        const settings = new NativeSetValues(this.nativeDispatcher);
        settings.add(SMA_ANNOTATION_OPTIONS.name, smaOption, SYSTEM, INT);
        settings.send();
      }
    }

    callback(true);
    return false;
  }

  /**
   * Return the block state and color
   * @param {String} linkInfo
   */
  toBlockState(linkInfo)
  {
    const colorVal = BackgroundCommon.toColor(linkInfo);
    let blockState = colorVal;
    let state = null;

    if (linkInfo)
    {
      if ((colorVal === Color.RED || colorVal === Color.YELLOW) && BackgroundCommon.isPhishingURI(linkInfo.category))
      {
        blockState = BlockState.PHISHING;
      }

      state = {
        color: colorVal,
        state: blockState
      };
    }
    else
    {
      state = {
        color: Color.UNKNOWN,
        state: BlockState.UNKNOWN
      };
    }

    return state;
  }

  /**
   * Returns the browser action color for the URL if the URL reputation is already cached.
   * This method does not make GTI call
   * @param {any} url
   */
  toBrowserActionColor(url)
  {
    const data = BackgroundGlobals.mtiRequestor.getReputation(url);
    if (!data || !data.cache_entry)
    {
      return null;
    }

    return BackgroundCommon.toColor(data.cache_entry.value);
  }

  whitelistNoCallback(action, type, data)
  {
    const storeVariable = WhitelistStoreMappings[type];
    if (!type || !storeVariable)
    {
      Logger.error(`invalid type: '${type}' or storeVariable: '${storeVariable}'`);
      return true;
    }

    const { ADD, GET, REMOVE, CLEAR } = WhitelistActions;
    if (action === GET)
    {
      const whitelist = WhitelistWrapper.getDomainsFromWhitelist(storeVariable);
      return whitelist;
    }

    if (action === ADD && data.domains)
    {
      WhitelistWrapper.addDomainsToWhitelist(data.domains, storeVariable);
    }

    if (action === REMOVE && data.domains)
    {
      WhitelistWrapper.removeDomainFromWhitelist(data.domains, storeVariable);
    }

    if (action === CLEAR)
    {
      WhitelistWrapper.clearWhitelist(storeVariable);
    }
    return true;
  }

  /**
   * TODO: Comments
   */
  whitelist(action, type, data, callback)
  {
    callback(this.whitelistNoCallback(action, type, data));
    return true;
  }

  unblockAllIframes(tabId, tabUrl)
  {
    const blockedIframes = BackgroundGlobals.tabData.getBlockedFrames(tabId);

    let brefreshPage = false;

    blockedIframes.forEach(({ url, frameId }) =>
    {
      const domain = UrlParser.getDomain(url);
      this.whitelist(WhitelistActions.ADD, WhitelistTypes.IFRAME, { tabId, url, frameId, domains: [domain] }, () => {});
      BlockPageCommon.sendBlockPageEvent(WPSBlockPageType.iFrame, tabUrl, url, true);
      const message = {
        command: ContentCommands.UNBLOCK_IFRAME,
        url
      };
      if (frameId !== -1)
      {
        extension.tabs.sendMessage(tabId, message, { frameId });
      }
      else
      {
        brefreshPage = true;
      }
    });
    BackgroundGlobals.tabData.resetBlockedFrames(tabId);

    if (brefreshPage)
    {
      extension.tabs.update(tabId, { url: tabUrl });
    }
  }

  /**
   * First check whether the tab has cache of blocked frames. If the cache is not present then
   * for each frameUrl for the tab get from GTI cache to see whether it is blocked.
   */
  anyBlockedIframes(tabId, frameUrls, responseCb)
  {
    const blockedIframes = BackgroundGlobals.tabData.getBlockedFrames(tabId);
    if (blockedIframes.length > 0)
    {
      responseCb(true);
    }
    else
    {
      let isBlocked = false;
      for (let index = 0; index < frameUrls.length; ++index)
      {
        if (!BackgroundCommon.isProperWebURL(frameUrls[index])) continue;

        const ret = this.isFrameBlocked(frameUrls[index], tabId);
        if (ret.isBlocked)
        {
          isBlocked = true;
          BackgroundGlobals.tabData.addBlockedFrame(tabId, { url: frameUrls[index], frameId: -1 });
        }
      }
      responseCb(isBlocked);
    }
  }

  /**
   * For handling the CryptoJacking topbar
   */
  anyBlockedCryptoScripts(tabId, responseCb)
  {
    const tabDataGetValue = BackgroundGlobals.tabData.get(tabId);
    responseCb(tabDataGetValue.cryptoBlockData);
  }

  getExtensionStatus(payload, cb)
  {
    this.nativeDispatcher && this.nativeDispatcher.getExtensionStatus(payload, cb);
  }

  updateRatDetectionShowingFlag(showed, url = null, tabId = null)
  {
    const storeVariables = CacheStorageVariables.RAT_DETECTION_SHOWING.name;
    const allTabDomains = BackgroundGlobals.cacheStore.get(storeVariables) || {};
    let currentTabDomain = allTabDomains[tabId] || '';
    const domain = UrlParser.getDomain(url);

    if (showed && domain)
    {
      currentTabDomain = domain;
    }
    else
    {
      currentTabDomain = '';
    }

    allTabDomains[tabId] = currentTabDomain;
    BackgroundGlobals.cacheStore.set(storeVariables, allTabDomains);
  }

  /**
   * Called by the messageListener from content to retrieve the search results for search suggestion
   *
   * @param {String} searchTerm
   */
  async searchSuggest(searchTerm)
  {
    return BackgroundGlobals.searchSuggest.retrieveSearchTerm(searchTerm);
  }

  // Get specific native setting values
  getValues(getList)
  {
    return this.nativeDispatcher && this.nativeDispatcher.getValues(getList);
  }

  /**
   * Injects full form detection library once pre check determines there may be forms on the page.
   * @param {*} tabId
   */
  formPreCheckPassed(tabId)
  {
    // Content Script Inject
    const contentToInject = [new InjectFormDetection()];
    Injector.inject(tabId, contentToInject);
  }

  async signUpFormDetected(url, tabId)
  {
    const { color, categories } = await BackgroundGlobals.messageDispatcher.getURLReputation(url);

    // Content Script Inject
    const contentToInject = [
      new InjectAPSBalloon({ tabId, url, categories, color, isSignUp: true }),
    ];

    Injector.inject(tabId, contentToInject);
  }

  showSidebarMain(tabId, url)
  {
    const contentToInject = [new InjectSidebarMain({ url, tabId })];
    Injector.inject(tabId, contentToInject);
  }

  removeSearchTermOnEngineUrl(url)
  {
    const { searchEngines, nativeSettings } = BackgroundGlobals;
    const searchEngine = new SearchEngine(searchEngines, null, null, nativeSettings.SECURE_SEARCH_REGEXES);
    const engine = searchEngine.getEngineForUrl(url);
    if (engine && engine.enginetype === EngineType.SEARCH)
    {
      try
      {
        // remove the p or q search arguments from the url
        const urlOjb = new URL(url);
        urlOjb.searchParams.delete('p');
        urlOjb.searchParams.delete('q');
        return urlOjb.href;
      }
      catch (err)
      {
        return url;
      }
    }

    return url;
  }

  /**
   *
   * These are events that are sent to native from extension. The currently supported evtype = 1 which is for blockpage. Blockpagetypes supported are 1= Red, 2= Yellow, 3= Phishing, 4= Typosquat, 5= iFrame, 6= Crypto Topurl is the top level tab URL blockedUrl is ignored if the block page types are 1,2,3,4. In other cases these are the actual URLs that are blocked such as in case of iFrame and Crypto. UserOverride is false when block page is displayed initially and if user accepts risk and went ahead then another even is sent with same parameters as first with overriden set to true.
   * @param {*} typeOfBlockPage
   * @param {*} topUrl
   * @param {*} blockedUrl
   * @param {*} override
   */
  sendBlockPageEvent(typeOfBlockPage, topUrl, blockedUrl, override)
  {
    const subrequestpayload = {
      eventtype: 1,
      type: typeOfBlockPage,
      top_url: topUrl,
      blocked_url: blockedUrl,
      override,
    };
    const payload = {
      sub_request_type: `${SubRequestTypes.WA_EVENT}.${BackgroundGlobals.browserType}`,
      subrequestpayload: JSON.stringify(subrequestpayload)
    };
    const request = {
      request_type: WSSNativeCommands.PUBLISH,
      payload
    };
    Logger.log(`[WPS Native] Sent block page event to WPS Native ${JSON.stringify(request)}`);
    this.postMessage(request);
  }

  setSettings(settingName, settingValue)
  {
    this.nativeDispatcher.setSettings(settingName, settingValue);
  }
}
