import Logger from './logger';
import { TelemetryGlobals } from './telemetry_globals';

const preconfigured_detections = {
  device: [{
    event_id: 'analytics_detect_device_msft_guid_changed',
    key: 'Device.Microsoft.Machine.Guid',
    from: '^((?!^null$).)*$',
    to: '.*'
  },]
};

export default class DataSetManager
{
  constructor(datasetCatalog)
  {
    this.name = null;
    this.cachedData = {};
    this.datasets = {};
    this.datasetsCatalog = datasetCatalog;
    this.watchList = {};

    this.logDebug('Setup Started');
    try
    {
      this.createWatchList();
    }
    catch (e)
    {
      this.logError(`datasets_catalog: Setup failed: ${e.message}`);
      return;
    }
    this.logDebug('Setup Complete');
  }

  /**
   * Retrieves a specific Dataset object
   */
  get(name)
  {
    const dataset = this.create(name);

    if (dataset.isDirty())
    {
      this.checkForChange(name, dataset);
    }

    return dataset;
  }

  createWatchList()
  {
    for (const dataset of Object.keys(preconfigured_detections))
    {
      if (!this.watchList[dataset])
      {
        this.watchList[dataset] = [];
      }
      for (const data of preconfigured_detections[dataset])
      {
        const { key } = data;
        this.watchList[dataset].push(key);
      }
    }
  }

  create(name)
  {
    if (this.datasets[name]) return this.datasets[name];

    const target = new Dataset(name);
    target.initialize(this.datasetsCatalog[name]);

    // Add to dataset store
    this.datasets[name] = target;

    return target;
  }

  // TODO: Change implementation / refactor
  checkForChange(datasetName, dataset)
  {
    try
    {
      if ((datasetName == null) || (datasetName == undefined))
      {
        return;
      }

      const datasetWatchList = this.watchList[datasetName];

      if ((datasetWatchList == null) || (datasetWatchList == undefined))
      {
        return;
      }


      const storedObjName = `analytics_dataset_${datasetName}_content`;
      let oldDatasetContent = this.cachedData[storedObjName];

      const newDatasetContent = dataset.getContent();

      if (oldDatasetContent == null)
      {
        // We still want to send an event if this is the first time we collect an attribute
        oldDatasetContent = '{}';
      }
      this.logDebug(`Retrieved watch list information for dataset: ${datasetName}`);

      oldDatasetContent = JSON.parse(oldDatasetContent);

      // Only store the elements we marked for watching
      const watchListNewValues = {};
      const escapedRuleMismatchStr = escape('[ruleMismatch]');
      const escapedUnknown = escape('[unknown]');
      const escapedNotAssigned = escape('[not assigned]');
      const escapedBracketNull = escape('[null]');

      const changedValueEvents = []; // A list to hold change value events

      for (const idx in datasetWatchList)
      {
        const key = datasetWatchList[idx];
        if ((key == null) || (key == undefined)) continue; // A bug has occured from defining Array.prototype.indexOf which caused null keys

        // If this is the first time we collect the value, oldValue will be null
        let oldValue = oldDatasetContent[key];
        const newValue = escape(newDatasetContent[key]);

        // Don't store new value if it was a ruleMismatch OR unknown, keep the old value
        watchListNewValues[key] = ((escapedRuleMismatchStr == newValue) || (escapedUnknown == newValue)) ? oldValue : newValue;

        if (oldValue == newValue) continue;

        if (escapedRuleMismatchStr == newValue) continue;
        if (escapedRuleMismatchStr == oldValue) continue;

        if (escapedUnknown == newValue) continue;

        if ((escapedUnknown == oldValue)
          || (escapedNotAssigned == oldValue)
          || (oldValue == null)
          || (undefined == oldValue)
          || (oldValue == 'null')
          || (oldValue == 'undefined')
          || (escapedBracketNull == oldValue))
        {
          oldValue = null;
        }

        //Send event
        const eventInfo = {
          datasetName,
          key,
          oldValue,
          newValue
        };

        changedValueEvents.push(eventInfo);
      }

      // In case the dataset got dirty again. Let's store the dataset before sending context changed event
      const cachedData = JSON.stringify(watchListNewValues);
      this.cachedData[storedObjName] = cachedData;
      for (const idx in changedValueEvents)
      {
        // this.sendContextChangedEvent(changedValueEvents[idx]);
      }
    }
    catch (e)
    {
      this.logError(`Exception while calling _checkForChange: ${e.message}`);
    }
  }

  sendContextChangedEvent(eventInfo)
  {
    //Code changes are needed to refactor and delete `_sendContextChangedEvent`
    this.sendAnalyticsDetectEvent(eventInfo);

    if (eventInfo.oldValue === null) return;

    const record = {
      UniqueIdentifier: 'analytics_dataset_value_changed',
      type: 'event',
      payload: {
        'Tracker.Type': 'event',
        'Event.Category': 'Analytics.Content',
        'Sub.Category': eventInfo.datasetName,
        'Event.Action': 'Value.Changed',
        'Event.Label': eventInfo.key,
        'Hit.Label.1': eventInfo.oldValue,
        'Hit.Label.2': eventInfo.newValue
      }
    };

    this.logDebug(`Detected change for key: ${eventInfo.key}. In dataset: ${eventInfo.datasetName}. Going to send an event`);
    TelemetryGlobals.eventHandler.handleEvent(record);
  }

  sendAnalyticsDetectEvent(eventInfo)
  {
    const detections = preconfigured_detections[eventInfo.datasetName];
    if (!detections) return false;

    for (const idx in detections)
    {
      try
      {
        const item = detections[idx];

        if (item.key != eventInfo.key) continue;
        if (item.event_id == null || item.event_id == '') continue; //This is an error case

        const from_exp = (item.from != null) ? new RegExp(item.from) : null;
        const to_exp = (item.to != null) ? new RegExp(item.to) : null;

        if (from_exp != null && !from_exp.test(eventInfo.oldValue)) continue;
        if (from_exp == null && eventInfo.oldValue != null) continue;
        if (to_exp != null && !to_exp.test(eventInfo.newValue)) continue;
        if (to_exp == null && eventInfo.newValue != null) continue;

        const record = {
          UniqueIdentifier: item.event_id,
          type: 'event',
          payload: {
            'Tracker.Type': 'event',
            'Event.Category': 'Analytics.Content',
            'Sub.Category': eventInfo.datasetName,
            'Event.Action': 'Value.Changed',
            'Event.Label': eventInfo.key,
            'Hit.Label.1': eventInfo.oldValue,
            'Hit.Label.2': eventInfo.newValue
          }
        };

        this.logDebug(`Detected dataset item change flow: ${eventInfo.key}. Going to send an event`);
        TelemetryGlobals.eventHandler.handleEvent(record);
        return true;
      }
      catch (e)
      {
        this.logError(`_sendAnalyticsDetectEvent: ${e.message}`);
      }
    }

    return false;
  }

  logInfo(msg)
  {
    Logger.log(`_datasetsManager: ${msg}`);
  }

  logDebug(msg)
  {
    Logger.debug(`_datasetsManager: ${msg}`);
  }

  logError(msg)
  {
    Logger.error(`_datasetsManager: ${msg}`);
  }

  logWarn(msg)
  {
    Logger.warn(`_datasetsManager: ${msg}`);
  }
}

class Dataset
{
  constructor(name)
  {
    this.dirty = true; // Denotes the datapoints in the dataset is dirty and needs to be refreshed
    this.name = name;
    this.itemsList = []; // Item's names that belong to the datasets
  }

  /**
   * Initializes the dataset with corresponding config
   * @param {Object} config - {'data_items': array of items that belongs in the dataset, 'refresh': refres configuration}
   */
  initialize(config)
  {
    try
    {
      if (!config)
      {
        this.logWarn('No configuration defined');
        return false;
      }

      const { data_items } = config;
      if (!data_items)
      {
        this.logError(`Invalid Data items. Config (${JSON.stringify(config)})`);
        return false;
      }
      this.itemsList = data_items;

      const refreshConfig = config.refresh;
      this.setRefresh(refreshConfig);

      this.logDebug('Initialization complete');
      return true;
    }
    catch (e)
    {
      this.logError(`initialize: ${e.message}`);
      return false;
    }
  }

  /**
   * Retrieves a datapoint from the dataset
   * @param {String} key - Target value key
   */
  get(key)
  {
    try
    {
      const content = this.getContent()[key];
      return content;
    }
    catch (e)
    {
      this.logError(`get: ${e.message}`);
    }
  }

  /**
   * Retrieves key value pairs for all items that belong to the datasets. Uses the datastore to collect the itemsList.
   * If the dataset is dirty, the dataset will ask the datastore to mark the datapoints as expired.
   */
  getContent()
  {
    try
    {
      this.logDebug('getContent starting');
      this.logDebug(`itemsList${JSON.stringify(this.itemsList)}`);

      if (this.dirty)
      {
        TelemetryGlobals.dataCollector.markDataExpired(this.itemsList);
        this.dirty = false;
      }

      const content = TelemetryGlobals.dataCollector.get(this.itemsList);
      return content;
    }
    catch (e)
    {
      this.logError(`getContent: ${e.message}`);
    }
  }

  /**
   * Checks if dataset is dirty.
   */
  isDirty()
  {
    if (this.dirty) return true;

    return TelemetryGlobals.dataCollector.isExpired(this.itemsList);
  }

  /**
   * Sets when to set the dirty flag. Allows subscribing to messages on the msg bus. Allows creating timeouts to refresh on.
   * @param {Object} refreshConfig - {timeoutInterval: timeout to reset the dataset, onMessageBusMsg: array of msgs that will trigger marking the dataset to dirty}
   */
  setRefresh(refreshConfig)
  {
    if (!refreshConfig)
    {
      return;
    }

    this.createDatasetRefreshTimer(refreshConfig, this);
  }

  /**
   * Refresh the dataset
   * @param {Object} refreshConfig - { useEngineDefaultTimeout: if true, the timeoutInterval is ignored, timeoutInterval: time out to refresh dataset }
   * @param {String} dataset - Dataset name
   */
  createDatasetRefreshTimer(refreshConfig, dataset)
  {
    const { useEngineDefaultTimeout } = refreshConfig;
    let { timeoutInterval } = refreshConfig;

    if (useEngineDefaultTimeout)
    {
      timeoutInterval = 60 * 60 * 1000; // 1 hour
    }

    if (!timeoutInterval)
    {
      return;
    }

    setInterval(function ()
    {
      try
      {
        this.dirty = true;
        this.logDebug('Marking dirty flag to true');
      }
      catch (e)
      {
        this.logError(`createDatasetRefreshTimer: ${e.message}`);
      }
    }, timeoutInterval);
  }

  logError(message) { Logger.error(`dataset(${this.name}):  ${message}`); }
  logDebug(message) { Logger.debug(`dataset(${this.name}): ${message}`); }
  logInfo(message) { Logger.log(`dataset(${this.name}): ${message}`); }
  logWarn(message) { Logger.warn(`dataset(${this.name}): ${message}`); }
}
