// Common
import { Logger } from '@extension/common/logger';

export default class InjectorCommon
{
  // Helper function that will keep executingScipt and pinging the script until it succeeds,
  // or resolves to 'false' if timeout/tries has been reached.
  static retryExecuteScript(details, keepInjecting, maxTries)
  {
    const { object, tabId, file, frameId } = details;
    const promise = new Promise(async (resolve) =>
    {
      const pingRequest = { command: object.pingCommand };
      // Perform an initial ping before trying to inject
      const pingResp = await InjectorCommon.pingCommand(tabId, pingRequest, frameId, 0, 100);
      if (pingResp)
      {
        resolve(true);
        return;
      }

      // If the initial ping did not get a response, we will not inject
      const execHandler = async (currentTries) =>
      {
        const execResp = await InjectorCommon.executeScriptPromise(tabId, file, frameId);
        if (chrome.runtime.lastError || !execResp || !execResp.length)
        {
          // Check if we have reached the max number of tries
          if (currentTries >= maxTries)
          {
            resolve(false);
            return;
          }

          // Failed to inject to the content script
          if (keepInjecting)
          {
            // Keep trying to inject if we need to (keepInjecting)
            setTimeout(() => execHandler(currentTries + 1), 200);
            return;
          }

          // Otherwise if on first failure return false (do not try to keep injecting);
          resolve(false);
          return;
        }

        // If injection is successful, lets verify by sending a ping
        const pingResp = await InjectorCommon.pingCommand(tabId, pingRequest, frameId, 2, 100);
        if (!pingResp)
        {
          // if ping did not respond, we try to inject the script again
          execHandler(currentTries + 1);
        }
        else
        {
          // ping received, everything is loaded
          resolve(true);
        }
      };

      execHandler(0);
    });

    return promise;
  }

  // Helper function that will keep sending a ping command based on the 'maxTries' and 'timeout'
  // resolves to true if ping responds otherwise resolves to false if maxTries is reached
  static pingCommand(tabId, request, frameId, maxTries = 10, timeout = 300)
  {
    const promise = new Promise((resolve) =>
    {
      const messageSender = async (currentTries) =>
      {
        const response = await InjectorCommon.sendMessagePromise(tabId, request, frameId);
        if (!response)
        {
          if (currentTries >= maxTries)
          {
            resolve(false);
            return;
          }

          setTimeout(() => messageSender(currentTries + 1), timeout);
          return;
        }

        resolve(true);
      };

      messageSender(0);
    });

    return promise;
  }

  // Helper function to convert 'sendMessage' as a 'promise'
  static sendMessagePromise(tabId, request, frameId = 0)
  {
    const optional = {};
    if (frameId > 0) optional.frameId = frameId;
    const promise = new Promise((resolve) =>
    {
      chrome.tabs.sendMessage(tabId, request, optional, (response) =>
      {
        if (chrome.runtime.lastError)
        {
          Logger.debug(`Failed to sendMessage at sendMessagePromise, err: ${chrome.runtime.lastError.message}`);
        }
        resolve(response);
      });
    });

    return promise;
  }

  // Helper function to convert 'executeScript' as a 'promise'
  static executeScriptPromise(tabId, file, frameId)
  {
    const promise = new Promise((resolve) =>
    {
      chrome.scripting.executeScript({ target: { tabId, frameIds: [frameId] }, files: [file] }, (response) =>
      {
        if (chrome.runtime.lastError)
        {
          Logger.debug(`Failed to executeScript at executeScriptPromise, err: ${chrome.runtime.lastError.message}`);
        }

        resolve(response);
      });
    });

    return promise;
  }
}
