// Common
import { SocialMediaTypes } from '@extension/common/sma_variables';

/**
 * TODO: Comment
 */
export default class SearchEngine
{
  constructor(engines, socialMediaToggle, socialMediaOption, secureSearchRegExes)
  {
    this.engines = engines;
    this.secureSearchRegExes = secureSearchRegExes;

    this.socialMediaToggle = socialMediaToggle;
    this.socialMediaOption = socialMediaOption;
    if (socialMediaToggle && socialMediaOption)
    {
      this.updateSocialMedia();
    }
  }

  updateSocialMedia()
  {
    if (!this.socialMediaToggle || this.socialMediaOption === SocialMediaTypes.NONE) return;

    // The social media annotation engines are disabled by default.
    // Here we are enabling those engines that have been set as enabled by user.
    for (const engine of this.engines)
    {
      // Check if this engine is of social media type
      if (engine.socialMediaType !== undefined)
      {
        // If so, we need to update the engines saved in bkglobals
        // with the latest selected options from the user, as the list
        // is always kept in memory.
        if (engine.socialMediaType & this.socialMediaOption)
        {
          engine.enabled = true;
        }
        else
        {
          engine.enabled = false;
        }
      }
    }
  }

  checkEngineRegEx(url, engineRegexes)
  {
    for (let reindex = 0; reindex < engineRegexes.length; ++reindex)
    {
      const regexp = new RegExp(engineRegexes[reindex], 'i');
      const ret = regexp.test(url);
      if (ret) return true;
    }
    return false;
  }

  getSecureEngineName(engineRegexesString)
  {
    if (engineRegexesString.includes('yahoo'))
    {
      return 'yahoo';
    }

    if (engineRegexesString.includes('bing'))
    {
      return 'bing';
    }

    if (engineRegexesString.includes('yandex'))
    {
      return 'yandex';
    }

    return '';
  }

  getEngineForUrl(url)
  {
    const SECURE_SEARCH_REGEXES = this.secureSearchRegExes.value;
    let secureEngineName = null;
    if (SECURE_SEARCH_REGEXES.length > 0)
    {
      secureEngineName = this.getSecureEngineName(SECURE_SEARCH_REGEXES[0]);
    }

    const engineData = this.engines.find((engine) =>
    {
      if (!engine.enabled)
      {
        return false;
      }

      if (engine.secureurlre)
      {
        const ssRegEx = secureEngineName === engine.name ? SECURE_SEARCH_REGEXES : engine.secureurlre;

        const ret = this.checkEngineRegEx(url, ssRegEx);
        if (ret)
        {
          engine.issecuresearch = true;
          return true;
        }
      }

      const ret = this.checkEngineRegEx(url, engine.urlre);
      if (ret)
      {
        engine.issecuresearch = false;
        return true;
      }
      return false;
    });

    return engineData;
  }

  /**
   * Gets the list of all selectors. This include disabled selectors as well.
   */
  getEngines()
  {
    return this.engines;
  }
}
