// Background
import { ContentCommands, SideBarCommands } from '@extension/common/commands';
import InjectBase from '@extension/background/content_injector/inject_base';
import BackgroundUtils from '@extension/background/background_utils';
import { Logger } from '@extension/common/logger';

export default class InjectSidebarMain extends InjectBase
{
  constructor({ url, tabId })
  {
    super('content_sidebar_main.js');
    this.url = url;
    this.tabId = tabId;
    this.pingCommand = ContentCommands.PING_CONTENT_SIDEBAR_MAIN;

    // Upon first failure to inject (on non-content script page)
    // we should not continue to try and just simply show the foreground page
    this.keepInjecting = false;
  }

  check()
  {
    // Ignore browser action to trigger foreground sidebar page
    // since user is already in the page
    if (this.url.includes('html/foreground_sidebar_main.html'))
    {
      return false;
    }

    return true;
  }

  runAfter(success, previouslyInjected)
  {
    // If it has been previosly injected we can simply just call a send Message
    if (previouslyInjected)
    {
      this.triggerSidebarMain();
      return;
    }

    if (!success)
    {
      this.triggerForegroundPage();
    }
  }

  triggerForegroundPage()
  {
    BackgroundUtils.focusOrCreateTab(chrome.runtime.getURL('html/foreground_sidebar_main.html'));
  }

  triggerSidebarMain()
  {
    const request = {
      command: ContentCommands.SIDEBAR,
      payload: {
        subcommand: SideBarCommands.MAIN,
        componentData: { sidebarMainData: {} },
      }
    };

    chrome.tabs.sendMessage(this.tabId, request, (response) =>
    {
      if (chrome.runtime.lastError)
      {
        Logger.log(chrome.runtime.lastError.message);
      }

      if (!response)
      {
        this.triggerForegroundPage();
      }
    });
  }
}
