import Logger from './logger';
import Transport from './transport';
import { normalizePayload } from './utils';
import { TelemetryGlobals } from './telemetry_globals';

export default class EventHandler
{
  constructor()
  {
    this.transport = new Transport();
  }

  handleEvent(event)
  {
    // TODO: find out what event type should be
    event.type = 'UWP_Event';

    Logger.debug(`process Analytics.v1.AddRecord event: ${JSON.stringify(event)}`);

    const eventName = event.unique_id || event.UniqueIdentifier; // Latter option for API.v2
    let payload = normalizePayload(event.payload || event.data);

    if (!payload)
    {
      Logger.warn(`event ${eventName}: Unable to extract message payload; using an empty one`);
      payload = {};
    }

    if (event.type)
    {
      payload['Tracker.Type'] = event.type; // This is for backwards compatibility with Orion
    }

    // Different versions of the API produces different cases for the tracker type
    if (payload['Tracker.Type'] && (typeof payload['Tracker.Type'] === 'string'))
    {
      payload['Tracker.Type'] = payload['Tracker.Type'].toLowerCase();
    }

    // Add record creation time if it doesn't exist (case of MsgBus API)
    if (payload['__record.created'] === null)
    {
      payload['__record.created'] = new Date().toISOString();
    }

    payload.hit_event_id = eventName;

    Logger.debug(`ready to transmit ${JSON.stringify(payload)}`);
    Logger.logRawTrack(eventName, JSON.stringify(payload));

    const profileNames = TelemetryGlobals.eventManager.getProfileNames(eventName);

    if (!profileNames)
    {
      Logger.log(`Rejected event ${eventName}: missing configuration`);
      // Don't send event, just log it since it's a warning
      Logger.logAutomationError(eventName,
        JSON.stringify(payload),
        JSON.stringify({
          level: 'warning',
          type: { rejected: `${eventName} is missing configuration/profile` }
        }));
      return;
    }

    this.transport.transmit(eventName, payload, profileNames, TelemetryGlobals.eventManager.getDataSetNames(eventName));
  }
}
