// Background
import { GTIConstants } from './gti_contants';
import GTIResponse from './gti_response';

/**
 * Parse the GTI http response data.
 */
export default class GTIResponseBuilder
{
  constructor(cacheStore)
  {
    this.response = new GTIResponse(); // The response serialization object.
    this.gtiResponse = null; // The response array received from GTI Server.
    this.requestData = null; // The request data from content page.
    this.gtiCache = cacheStore;
    this.cachedItems = [];
  }

  init(gtiResponse, requestdata)
  {
    this.gtiResponse = gtiResponse; // The response array received from GTI Server.
    this.requestdata = requestdata; // The request data from content page.
  }

  addCachedItem(item)
  {
    this.cachedItems.push(item);
  }

  buildLinkInfo(url, linkInfo)
  {
    this.response.annotationMap[url] = linkInfo;
  }

  /**
   * convert GTI response to new HTI response object
  */
  convertGTItoHTI(res, url)
  {
  // map GTI rep value to HTI trust value
    if (res.rep < 15)
    {
      res.rep = 70; // Green
    }
    else if (res.rep < 30)
    {
      res.rep = 0; // Gray
    }
    else if (res.rep < 50)
    {
      res.rep = 40; // Yellow
    }
    else
    {
      res.rep = 1; // Red
    }

    const categories = [];

    if (res.cat !== null && res.cat !== undefined)
    {
      res.cat.forEach((cat) =>
      {
        if (GTIConstants.categoryMapGtiToHti[cat])
        {
          categories.push(GTIConstants.categoryMapGtiToHti[cat]);
        }
      });
    }

    return {
      category: categories, // here cat is an number, convert to category
      trust: res.rep,
      ttl: 3600, // assign default ttl, since GTI response has no ttl property
      url,
      flag: res.ufg
    };
  }

  prepareFromCacheResponse()
  {
    for (let index = 0; index < this.cachedItems.length; index++)
    {
      const cacheInfo = this.cachedItems[index];
      this.buildLinkInfo(cacheInfo.uri, cacheInfo.value.value);
    }
  }

  prepareFromGTIResponse()
  {
    if (this.gtiResponse.length === 0)
    {
      return;
    }

    const respObj = this.gtiResponse[0] === '' ? this.gtiResponse[1] : this.gtiResponse[0];

    if (Object.keys(respObj).length === 0 && respObj.constructor === Object)
    {
      return; // something wrong
    }

    for (let respIndex = 0; respIndex < respObj.a.length; respIndex++)
    {
      let linkInfo = respObj.a[respIndex];

      // Reputation specific safe mode flag is set, so skip the reputation for this particular item.
      if (undefined !== linkInfo.aflag && linkInfo.aflag === GTIConstants.globalSafeModeFlag)
      {
        continue;
      }

      const url = this.requestdata[respIndex];

      // convert GTI response to HTI compatible response Object
      linkInfo = this.convertGTItoHTI(linkInfo, url);
      linkInfo.url = url;

      this.gtiCache.setReputation(url, linkInfo);
      this.buildLinkInfo(url, linkInfo);
    }
  }

  prepareResponse(fncallback)
  {
    this.prepareFromGTIResponse();
    this.prepareFromCacheResponse();
    fncallback(this.response);
  }
}
