import Logger from './logger';
import BaseTransmitter from './base_transmitter';
import TransportError from './transport_error';
import { TelemetryGlobals } from './telemetry_globals';
import { mergeObjects, lowerCaseObjectKeyValuePair, isoDateToDate } from './utils';

export default class Transport extends BaseTransmitter
{
  /**
   * Constructs the finalized message to be sent to the analytics server
   * @param {String} eventName - Event name
   * @param {Object} payload - Info to be sent to the analytics server
   * @param {String[]} profileNames - Associated profile names to the event
   * @param {String[]} eventDataSetNames - Associated dataset to the event (can be null or empty)
   */
  async transmit(eventName, payload, profileNames, eventDataSetNames)
  {
    Logger.debug(`analyticstransport.transmit message=${JSON.stringify(payload)}, profileNames=${JSON.stringify(profileNames)}, datasetNames=${JSON.stringify(eventDataSetNames)}`);

    // Check for event-level throttling
    if (this.isEventThrottled(eventName))
    {
      Logger.debug(`Event ${eventName} was event-level throttled`);
      Logger.logAutomationError(eventName,
        JSON.stringify(payload),
        JSON.stringify({
          level: 'info',
          type: { eventThrottled: `${eventName} is event throttled` }
        }));
      return;
    }

    for (const profileName of profileNames)
    {
      // Check for profile-level throttling
      // Do not throttle for critical events
      const eventPriority = TelemetryGlobals.eventManager.getPriority(eventName);
      const isPriorityCritical = eventPriority.toLowerCase() === 'critical';

      if (this.isProfileThrottled(eventName, profileName) && !isPriorityCritical)
      {
        Logger.debug(`Event ${eventName} was profile-level throttled by ${profileName}`);
        Logger.logAutomationError(eventName,
          JSON.stringify(payload),
          JSON.stringify({
            level: 'info',
            type: { profileThrottled: `${eventName} is profile throttled for ${profileName}` }
          }));
        continue;
      }

      const emitter = this.getEmitter(profileName);
      if (!emitter)
      {
        Logger.error(`Failed to locate an emitter for ${profileName}; aborting send`);
        continue;
      }

      let outbound = {};
      const isForcingLowerKeys = emitter.transportConfiguration.forceLowerCaseKeys === false;
      mergeObjects(outbound, payload, isForcingLowerKeys); // Shallow copy payload to outbound

      // Apply first and second layers of mapping
      outbound = TelemetryGlobals.dictionaryMapper.getEventMap(outbound, eventName);
      outbound = TelemetryGlobals.dictionaryMapper.getGlobalMap(outbound);
      lowerCaseObjectKeyValuePair(outbound, true, false); // Lowercase the keys

      // Apply attribute rules
      const rulesApplied = this.applyAttributeRules(outbound, eventName, profileName);
      outbound = rulesApplied;

      // Send rule failure events
      if (rulesApplied.error)
      {
        const transportError = new TransportError();
        transportError.transmit(eventName, payload, rulesApplied.error);
      }

      // Add event day index
      const eventDayIndex = TelemetryGlobals.rulesManager.getDailyMaxTableCounter(profileName);
      outbound['__event.day.index'] = eventDayIndex;

      // Add throttle
      outbound.__throttle_user_multiplier = emitter.throttleMultiplier || 1;

      // Add default elements (UserID, contentVersion, etc)
      mergeObjects(outbound, await TelemetryGlobals.engine.getDefaultDictionary(), true); // Lowercase keys

      // C++ plugins (CSP, GA) log the event by looking for Hit.UniqueID
      outbound['Hit.UniqueID'] = eventName;

      // Add dataset names
      this.addDataSetNames(outbound, emitter, eventDataSetNames);

      // Add queue time
      const now_time = new Date();
      let timeStamp = unescape(outbound['__record.created']);
      timeStamp = timeStamp.split(' ').join('T');

      const create_time = isoDateToDate(timeStamp);

      if (now_time && create_time)
      {
        outbound['__queue.time.milliseconds'] = `${now_time - create_time}`;
      }

      Logger.debug(`ready to send ${JSON.stringify(outbound)}`);

      const result = await emitter.send(outbound); // HTTP request (send data off to analytics server)

      Logger.debug(`ProfileName: ${profileName}. Result: ${result}`);
      Logger.debug('******************************* done');

      // TODO: Impelement queue for offline purposes, process when connetion is re-established
    }
  }

  /**
   * Apply attribute rules
   * @param {Object} message - Outbound payload
   * @param {String} eventName - The event
   * @param {String} profileName - The corresponding profile to the event
   */
  applyAttributeRules(message, eventName, profileName)
  {
    const attributeRules = TelemetryGlobals.eventManager.getAttributeRules(eventName);
    if (!attributeRules)
    {
      return message;
    }

    // Deep copy the attribute rules and map them
    let mappedRules = {};
    mergeObjects(mappedRules, attributeRules);

    // Normalizing the rules to post global mappings and then apply the rules after normalization.
    // Message should be normalized prior to reaching here
    mappedRules = TelemetryGlobals.dictionaryMapper.getEventMap(mappedRules, eventName);
    mappedRules = TelemetryGlobals.dictionaryMapper.getGlobalMap(mappedRules);
    lowerCaseObjectKeyValuePair(mappedRules, true, false); // Lowercase the keys only

    let defaults = ['uniqueid', 'hit.uniqueid', 'event.category', 'event.action', 'tracker.type', '__transport.name', '__record.created', '__caller.name'];
    defaults = defaults.concat(this.getProfileDefaultAttribute(profileName));

    let cleanMessage = {}; // Variable to contain cleansed data

    // Ignoring strict attribute rule implementation for profile_csis_ga
    if (this.getProfile(profileName).ignoreAttributeRules)
    {
      // Fixed shallow copy object to deep copy
      // For error / automation logging, it should be a deep copy
      cleanMessage = JSON.parse(JSON.stringify(message));
    }
    else
    {
      // Create a new object (cleanMessage) that includes default and rule defined items only
      Object.keys(message).forEach((messageKey) =>
      {
        const isDefault = defaults.includes(messageKey);

        if (isDefault || mappedRules[messageKey])
        {
          cleanMessage[messageKey] = message[messageKey];
        }
        else if (messageKey.substring(0, 2) !== '__')
        {
          cleanMessage[messageKey] = '[ruleMismatch]';
        }
      });
    }

    Logger.log(`cleanMessage: ${JSON.stringify(cleanMessage)}`);

    // Apply event rules
    Object.keys(mappedRules).forEach((ruleKey) =>
    {
      const rule = mappedRules[ruleKey];

      if (rule.optional && (cleanMessage[ruleKey] === null))
      {
        Logger.log(`applyAttributeRules: optional field ${ruleKey} was skipped`);
        return;
      }

      cleanMessage[ruleKey] = TelemetryGlobals.rulesManager.apply(cleanMessage[ruleKey], rule);
    });

    return cleanMessage;
  }
}
