import { ContentCommands } from '@extension/common/commands';
import InjectBase from '@extension/background/content_injector/inject_base';
import UrlParser from '@extension/common/url_parser';
import { BackgroundGlobals } from '@extension/background/background_globals';

export default class InjectSiteListener extends InjectBase
{
  constructor({ url })
  {
    super('content_site_listener.js');
    this.url = url;
    this.pingCommand = ContentCommands.PING_CONTENT_SITE_LISTENER;
  }

  check()
  {
    const domain = UrlParser.getDomain(this.url);
    return BackgroundGlobals.SUPPORTED_WEBSITE_LIST.includes(domain);
  }
}
