/*
 *This is the Background Global object passed to content script. The full BackgroundGlobal object
 * is heavy and takes 180KB hence that consume large memory usage when passed as value to each tab
 * and every frame and for every call from content script. Hence this new object which internally
 * references the relevant objects from Background Globals. Any new object from Background Globals
 * that need to be made available in content script through GET_BK_GLOBALS has to go here. Make
 * sure that you donot include large objects as the purpose of this is to reduce size of the object
 * passed.
 */

class ContentGlobals
{
  constructor()
  {
    this.isTrustCheckRequired = true;
    this.shadowMode = null;
    this.browserType = 0;
    this.nativeSettings = null;
    this.WA_IMAGE_SECRET = null;
    this.apsAnimationTimeout = 0;
    this.searchEngines = null;
    this.IDPS_IS_ENTITLED = false;
    this.ALLOW_LOCALHOST = false;
    this.apsUrlList = {};
    this.isHTI = false;
  }
}

export default class ContentGlobalManager
{
  static get(bkGlobals, bIncludeSearchEngines)
  {
    const cGlobals = new ContentGlobals();
    const contentKeys = Object.keys(cGlobals);
    contentKeys.forEach((key) =>
    {
      if (Object.prototype.hasOwnProperty.call(bkGlobals, key))
      {
        cGlobals[key] = bkGlobals[key];
      }
    });

    if (!bIncludeSearchEngines)
    {
      cGlobals.searchEngines = null;
    }
    const globals = JSON.stringify(cGlobals);
    return globals;
  }
}
