// Background
import { ContentCommands } from '@extension/common/commands';
import InjectBase from '@extension/background/content_injector/inject_base';
import Utils from '@extension/utilities/utils';
import { BackgroundGlobals } from '@extension/background/background_globals';

export default class InjectFormCheck extends InjectBase
{
  constructor()
  {
    super('iframe_form_check.js');
    this.pingCommand = ContentCommands.PING_IFRAME_FORM_CHECK;
  }

  check()
  {
    const { IDPS_FEATURE_ENABLED, APS_COHORT } = BackgroundGlobals.nativeSettings;
    const idpsFeatureEnabled = BackgroundGlobals.IDPS_IS_ENTITLED && IDPS_FEATURE_ENABLED.value === 2;

    const isSupported = Utils.isChromeBrowser() && Utils.isBrowserEN();
    if (idpsFeatureEnabled && isSupported)
    {
      return true;
    }

    if (APS_COHORT.value > 0 && Utils.isChromeBrowser())
    {
      return true;
    }

    return false;
  }
}
