// Common
import { extension } from '@extension/common/webextension';
import BackgroundUtils from '@extension/background/background_utils';
import WebRequestDispatcher from '@extension/background/dispatchers/webrequest_dispatcher';
import BackgroundCommon from '@extension/common/background_common';
import { BackgroundGlobals } from '@extension/background/background_globals';

//////////////////////////////////////////////////////////
const extensionURL = `chrome-extension://${extension.runtime.id}`;
//////////

const howToWorkUrls = [
  'https://www.mcafee.com/consumer/v/wa-chrome-how.html',
  'https://www.mcafee.com/consumer/v/wa-firefox-how.html',
  'https://www.mcafee.com/consumer/v/wa-edge-how.html',
];

const redirectPage = 'html/foreground_sidebar_main.html';

/**
 * TODO: Comment
 */
export default class WebRequestListener
{
  constructor()
  {
    this.onBeforeSendHeaders = this.onBeforeSendHeaders.bind(this);
    this.onSendHeaders = this.onSendHeaders.bind(this);
    this.onNavigateComplete = this.onNavigateComplete.bind(this);
    this.onHeadersReceived = this.onHeadersReceived.bind(this);
    this.onErrorOccurred = this.onErrorOccurred.bind(this);

//////////////////////////////////////////////////////////////
    this.onBeforeRequest = this.onBeforeRequest.bind(this);
//////////////
  }

  onSendHeaders(details)
  {
    const { frameId, tabId } = details;
    const isTopFrame = frameId <= 0;
    if (isTopFrame)
    {
      BackgroundGlobals.tabData.reset(tabId);
    }
  }

  onBeforeSendHeaders(details)
  {
    if (!BackgroundCommon.isPersonalPolicyEnabled())
    {
      return;
    }

    const { url, tabId, requestId, requestHeaders } = details;

    if (howToWorkUrls.includes(url.toLowerCase()))
    {
      extension.tabs.update(tabId, { url: `${extension.runtime.getURL(redirectPage)}` });
    }

    if (!requestHeaders) return;
    for (const { name, value } of requestHeaders)
    {
      if (name.toLowerCase() === 'referer')
      {
        const requestData = { referer: value.toLowerCase() };
        BackgroundUtils.setRequestData(tabId, requestId, requestData);
      }
    }
  }

  onNavigateComplete(details)
  {
    if (!BackgroundCommon.isPersonalPolicyEnabled())
    {
      return;
    }

    const { url, frameId, tabId } = details;
    WebRequestDispatcher.onNavigateComplete(url, tabId, frameId);
  }

  onHeadersReceived(details)
  {
    if (!BackgroundCommon.isPersonalPolicyEnabled())
    {
      return;
    }

    const {
      url, responseHeaders, tabId,
      requestId, frameId,
    } = details;

    // Skip URL reputation for downloads/attachments if native is installed as native can then go and do a file reputation check .
    // In the instance where native application is not available we will check for URL reputation as that will provide maybe over aggressive, but still protection.
    if (BackgroundGlobals.isNativeConnected)
    {
      // Check if this request is an executable. We dont need to Run GTI
      // request for executables. This will be handled by Download Scanning
      for (const { name, value } of responseHeaders)
      {
        const lname = name.toLowerCase();
        const lval = value.toLowerCase();

        if (lname === 'content-disposition' && lval.includes('attachment'))
        {
          return;
        }

        if (lname === 'content-type')
        {
          for (let index = 0; index < BackgroundGlobals.permittedContentTypes.length; ++index)
          {
            if (lval.includes(BackgroundGlobals.permittedContentTypes[index]))
            {
              return;
            }
          }
        }
      }
    }

    // Get the Referer from onBeforeSendHeaders
    const requestData = BackgroundUtils.getRequestData(tabId, requestId);
    BackgroundUtils.deleteRequestData(tabId, requestId);
    WebRequestDispatcher.onHeadersReceived(url, tabId, frameId, requestData.referer);
  }

  // If error occurs ie. the page doesn't exist anymore, call GTI and see the reputation of the url anyways. If malicious, show block page.
  onErrorOccurred(details)
  {
    if (!BackgroundCommon.isPersonalPolicyEnabled())
    {
      return;
    }

    const { url, tabId, requestId, frameId } = details;

    BackgroundUtils.deleteRequestData(tabId, requestId);
    WebRequestDispatcher.onHeadersReceived(url, tabId, frameId, null);
  }

////////////////////////////////////////////////////////////
  onBeforeRequest(details)
  {
    const deconstructUrl = new URL(details.url);

    // as long as request is made by our extension, shown by who the initator is
    if ((details.initiator && details.initiator.startsWith(`${extensionURL}`))
    // ignore all requests not for images
      || details.type !== 'image'
    // make sure the images contain our secret
      || (details.url.startsWith(`${extensionURL}`) && details.type === 'image' && deconstructUrl.searchParams.get('secret') === BackgroundGlobals.WA_IMAGE_SECRET))
    {
      return;
    }
    BackgroundCommon.blockRequest(details.tabId, details.url, 'Blocked unauthorized access to extension resources:');
  }
////////////

  init()
  {
    const urls = ['http://*/*', 'https://*/*', 'ftp://*/*'];
    const requestFilters = ['requestHeaders'];

////////////////////////////////////
    requestFilters.push('extraHeaders');
//////////////

    extension.webRequest.onBeforeSendHeaders.addListener(this.onBeforeSendHeaders, { urls, types: ['main_frame', 'sub_frame'] }, requestFilters);
    extension.webRequest.onSendHeaders.addListener(this.onSendHeaders, { urls, types: ['main_frame'] });
    extension.webRequest.onHeadersReceived.addListener(this.onHeadersReceived, { urls, types: ['main_frame', 'sub_frame'] }, ['responseHeaders']);
    extension.webRequest.onCompleted.addListener(this.onNavigateComplete, { urls, types: ['main_frame', 'sub_frame'] });
    extension.webRequest.onErrorOccurred.addListener(this.onErrorOccurred, { urls, types: ['main_frame', 'sub_frame'] });

//////////////////////////////////////////////////////////////
    extension.webRequest.onBeforeRequest.addListener(this.onBeforeRequest, { urls: [`${extensionURL}/*`] });
//////////////
  }
}
