import { Logger } from '@extension/common/logger';

export default class EventManager
{
  constructor(events)
  {
    this.events = events.data;
  }

  getProfileNames(id)
  {
    if (!this.events[id])
    {
      return null;
    }

    return this.events[id].profileNames || null;
  }

  getAttributeRules(id)
  {
    return this.events[id].attributeRules || null;
  }

  getPriority(id)
  {
    return this.events[id].priority.toLowerCase() || 'normal';
  }

  getDataSetNames(id)
  {
    return this.events[id].datasets || [];
  }

  // Used for automation
  setEvent(id, config)
  {
    this.events[id] = config;
  }

  getThrottleRule(id)
  {
    if (!('throttleRule' in this.events[id]))
    {
      Logger.warn(`getThrottleRule: failed, cannot find throttle rule attached to ${id}`);
      return null;
    }

    return this.events[id].throttleRule;
  }
}
