import { BackgroundGlobals } from '@extension/background/background_globals';
import { LocalStorageVariables } from '@extension/common/cache_store_variables';
import { BuildSettings } from '@extension/common/constants';
import { Logger } from '@extension/common/logger';
import BackgroundGTIRequestor from '@mcafee/gti/background_gti_requestor';
import BackgroundHTIRequestor from '@mcafee/hti/background_hti_requestor';

export default class MTIInitializer
{
  static async initGtiPackage()
  {
    const options = {
      clientIdGuid: MTIInitializer.getClientId(),
      serverUrl: BackgroundGlobals.gtiServer,
      productVersion: BuildSettings.EXTENSION_VERSION,
      cspId: BackgroundGlobals.nativeSettings.CSP_ID_SETTING.value,
    };
    BackgroundGlobals.mtiRequestor = new BackgroundGTIRequestor(BackgroundGlobals.wasm, options);
    await BackgroundGlobals.mtiRequestor.init();
  }

  static async initHtiPackage()
  {
    const options = {
      serverUrl: BackgroundGlobals.htiServer,
      productVersion: BuildSettings.EXTENSION_VERSION,
      cspClientId: BackgroundGlobals.cspClientId,
      clientIdGuid: MTIInitializer.getClientId(),
    };
    BackgroundGlobals.mtiRequestor = new BackgroundHTIRequestor(BackgroundGlobals.wasm, options);
    await BackgroundGlobals.mtiRequestor.init();
  }

  static getClientId()
  {
    const clientIdGuid = BackgroundGlobals.nativeSettings.MTI_CLIENT_ID.value || BackgroundGlobals.cacheStore.get(LocalStorageVariables.EXTN_UUID2.name, '');
    return clientIdGuid;
  }

  static async init()
  {
    const shouldUseHTIPackage = MTIInitializer.shouldUseHTIPackage();

    // update flags to reflect what package is initialized
    MTIInitializer.updateHTIFlag(shouldUseHTIPackage);

    if (shouldUseHTIPackage)
    {
      // only initialize HTI package if:
      // - we pass HTI checks
      // and either:
      // - the mti requestor object has not yet been initialized or
      // - if it was initialized with the GTI Package, we proceed to re-initialize it as HTI
      if (!BackgroundGlobals.mtiRequestor || BackgroundGlobals.mtiRequestor.getType() === 'GTI')
      {
        Logger.log('MTI requestor: initialize as HTI package.');
        await MTIInitializer.initHtiPackage();
      }
      else
      {
        Logger.log('HTI package has been previously initialized. Will not initialize again.');
      }
    }

    if (!shouldUseHTIPackage)
    {
      // only initialize GTI package if:
      // - we failed the HTI checks
      // and either:
      // - the mti requestor object has not yet been initialized or
      // - if it was initialized with the HTI Package, we proceed to re-initialize it as GTI
      if (!BackgroundGlobals.mtiRequestor || BackgroundGlobals.mtiRequestor.getType() === 'HTI')
      {
        Logger.log('MTI requestor: initialize as GTI package.');
        await MTIInitializer.initGtiPackage();
      }
      else
      {
        Logger.log('GTI package has been previously initialized. Will not initialize again.');
      }
    }

    // Update existing mti requestor with latest values retrieved from native
    if (BackgroundGlobals.mtiRequestor)
    {
      const clientIdGuid = MTIInitializer.getClientId();
      const cspid = BackgroundGlobals.nativeSettings.CSP_ID_SETTING.value;
      BackgroundGlobals.mtiRequestor.setGlobals({ cspid, clientIdGuid });
    }
  }

  static updateHTIFlag(value = null)
  {
    BackgroundGlobals.cacheStore.set(LocalStorageVariables.IS_HTI.name, value);
    BackgroundGlobals.isHTI = value;
  }

  static shouldUseHTIPackage()
  {
    const isHTI = BackgroundGlobals.cacheStore.get(LocalStorageVariables.IS_HTI.name, false);

    // Process the following check, after we know the background globals were updated
    // Otherwise, we just rely on what we know based on isHTI flag
    if (BackgroundGlobals.updatedByNative)
    {
      // Always HTI if WPS is installed
      if (BackgroundGlobals.isWPSInstalled)
      {
        Logger.log('MTI requestor WPS Installed: true - shall use HTI package.');
        return true;
      }

      // If WSS installed the do not turn on HTI
      if (BackgroundGlobals.WSS_INSTALLED)
      {
        Logger.log('MTI requestor WSS Installed: true - shall not use HTI package.');
        return false;
      }

      // if throttle is 0, feature is disabled
      if (BackgroundGlobals.nativeSettings.HTI_THROTTLE.value === 0)
      {
        BackgroundGlobals.cacheStore.set(LocalStorageVariables.HTI_THROTTLE.name, BackgroundGlobals.nativeSettings.HTI_THROTTLE.value);
        Logger.log('MTI requestor: HTI throttle at 0 percent (feature disabled) - shall not use HTI package.');
        return false;
      }

      // otherwise, lets try to process the throttle value
      // If WPS is not installed or WSS is not installed and if throttle is set to more than 0%
      if (BackgroundGlobals.nativeSettings.HTI_THROTTLE.value > 0)
      {
        // If previously throttle value is same as current value then stick to earlier HTI Flag value (based on previous calculations)
        const prevHTIThrottle = BackgroundGlobals.cacheStore.get(LocalStorageVariables.HTI_THROTTLE.name);
        if (BackgroundGlobals.nativeSettings.HTI_THROTTLE.value === prevHTIThrottle)
        {
          Logger.log(`MTI requestor throttle check pass (no change) - isHTI set to ${isHTI}`);
          return isHTI;
        }

        // otheriwse, pick the new value and recalculate.
        BackgroundGlobals.cacheStore.set(LocalStorageVariables.HTI_THROTTLE.name, BackgroundGlobals.nativeSettings.HTI_THROTTLE.value);
        const randVal = Math.random();
        const percent = Math.ceil(randVal * 100);
        // Initializing HTI package and storing current flag whether this is HTI or not. Subsequent run of this
        // extension instance will stick to the value set till there is a change in the throttle.
        if (percent <= BackgroundGlobals.nativeSettings.HTI_THROTTLE.value)
        {
          Logger.log('MTI requestor throttle check passed - shall use HTI package.');
          return true;
        }

        Logger.log('MTI requestor throttle check failed - shall not use HTI package.');
        return false;
      }
    }

    // Check if HTI was initialized before. This is to handle service-worker going to sleep and waking back up
    // And also to handle the case where Native response happens afer we initialized
    if (isHTI)
    {
      Logger.log('MTI requestor: HTI set to true - shall use HTI package.');
      return true;
    }

    Logger.log('MTI requestor - shall not use HTI package.');
    return false;
  }
}
