export default class GarbageCollector
{
  constructor()
  {
    this.collection = [];
  }

  add(cache)
  {
    this.collection.push(cache);

    if (this.collection.length === 1)
    {
      this.setCleanUpTimer();
    }
  }

  setCleanUpTimer()
  {
    setInterval(() =>
    {
      this.collection.forEach((cache) =>
      {
        cache.checkCache();
      });
    }, 1000 * 60 * 60);
  }
}
