export default class InjectBase
{
  constructor(main)
  {
    this.previouslyInjected = false;
    this.success = false;
    this.main = `scripts/${main}`;

    // This is used by the injector to determine whether to keep trying to
    // inject or not. Example, annotations should keep trying to inject if it failed to do
    // so at the beginning but not for the main sidebar since this can be triggered on a
    // non-content script site, which means we would just show the foreground sidebar page
    this.keepInjecting = true;
  }

  details()
  {
    return {
      main: this.main
    };
  }

  check()
  {
    return true;
  }

  runAfter(success, previouslyInjected)
  {
    this.previouslyInjected = previouslyInjected;
    this.success = success;
  }
}
