// Background
import { BackgroundGlobals } from '@extension/background/background_globals';
import InjectorCommon from '@extension/background/content_injector/injector_common';
// Common
import { Logger } from '@extension/common/logger';

export default class Injector
{
  static async inject(tabId, injectObjects, frameId = 0)
  {
    if (!injectObjects)
    {
      Logger.error('inject: Invalid argument details/injectObjects');
      return;
    }

    const objectsToInject = await Injector.getCheckSuccessObjects(injectObjects);
    if (objectsToInject.length === 0)
    {
      return;
    }

    // Retrieve any previously saved data about files already injected for this tab/frame
    const injectedFiles = Injector.getInjectDetailsByFrameId(tabId, frameId);

    // Collect all files that needs to be injected
    // We check with "injectedFiles" to avoid duplicate injections
    const filesToInject = [];
    objectsToInject.forEach((object) =>
    {
      const { main } = object.details();
      if (!injectedFiles.includes(main))
      {
        filesToInject.push({ file: main, object });
        injectedFiles.push(main);
      }
      else
      {
        // The main file has been injected previously
        // Therefore we do not need to inject and can directly call "runAfter"
        // with the proper values to let the inject object handle it
        const success = false;
        const previouslyInjected = true;
        Logger.debug(`File Injection PreviouslyInjected - tab[${tabId}] frame[${frameId}]: ${main}, success: ${success}, previouslyInjected: ${previouslyInjected} }`);
        object.runAfter(success, previouslyInjected);
      }
    });

    // Re-save the injectedFiles To prevent other requests
    // from trying to inject the same file
    Injector.updateInjectDetailsByFrameId(tabId, frameId, { injectedFiles });

    // Execute Script
    filesToInject.forEach(async ({ file, object }) =>
    {
      const details = {
        object,
        file,
        tabId,
        frameId,
      };

      Logger.debug(`File Injection Start - tab[${tabId}] frame[${frameId}] : ${file}`);
      const response = await InjectorCommon.retryExecuteScript(details, object.keepInjecting, 3);
      const success = response;
      const previouslyInjected = false;
      Logger.debug(`File Injection End - tab[${tabId}] frame[${frameId}]: ${file}, success: ${success}, previouslyInjected: ${previouslyInjected} }`);

      object.runAfter(success, previouslyInjected);
    });
  }

  // Get the objects that have successfully passed the check
  // set by each object's "check" criteria
  static async getCheckSuccessObjects(injectObjects)
  {
    // Since some "check" could be async, we are creating
    // a promise to wait for all to finish
    const objectPromises = [];
    injectObjects.forEach((classObject) =>
    {
      // Create a promise that will return the class object itself if the
      // check is successful otherwise null.
      const resolveObject = () => new Promise(async (resolve) =>
      {
        const result = await classObject.check();
        resolve(result ? classObject : null);
      });

      objectPromises.push(resolveObject());
    });

    const objectsToInject = await Promise.all(objectPromises);
    // Filter all objects that didnt pass the check() aka returned null
    return objectsToInject.filter((object) => object !== null);
  }

  static getInjectDetailsByFrameId(tabId, frameId)
  {
    // Retrieve any previously saved data about files already injected for this tab/frame
    const tabIdData = BackgroundGlobals.tabData.get(tabId); // retrieve tabdata
    const injectDetailsByFrameId = tabIdData.injectDetailsByFrameId || {};
    const injectDetails = injectDetailsByFrameId[frameId] || { injectedFiles: [] };
    return injectDetails.injectedFiles;
  }

  static updateInjectDetailsByFrameId(tabId, frameId, newData)
  {
    const tabIdData = BackgroundGlobals.tabData.get(tabId); // retrieve tabdata
    const injectDetailsByFrameId = tabIdData.injectDetailsByFrameId || {};
    const injectDetails = injectDetailsByFrameId[frameId] || { injectedFiles: [] };
    injectDetailsByFrameId[frameId] = { ...injectDetails, ...newData };
    BackgroundGlobals.tabData.update(tabId, { injectDetailsByFrameId });
  }
}
