import { HTIStorageVariables } from './hti_storage_variables';
import Logger from '../../Common-Utils-Package/logger/logger';
import { HTIConstants } from './hti_contants';

/**
 * This class encompass the logic for handling HTI cache on the client
 * For further clarification on this please review this wiki:
 * https://confluence.int.mcafee.com/display/MTI/URL+Client+Side+Caching
 */
export default class HTICacheLogic
{
  constructor(cacheStore)
  {
    this.cacheStore = cacheStore;
  }

  get(url)
  {
    const cacheEntries = this.cacheStore.get(HTIStorageVariables.CACHE_HTI, null);
    const everGreenUrls = this.cacheStore.get(HTIStorageVariables.EVER_GREEN_URLS, null);

    // cache storage are empty, return null cache entry
    if (!cacheEntries && !everGreenUrls)
    {
      return null;
    }

    const cachedUrlData = this.processUrl(url);

    // not found in storage
    if (!cachedUrlData)
    {
      Logger.debug(`HTI url not found in cache: ${url}`);
      return null;
    }

    // found in storage, check for expiry
    const expired = this.isExpired(cachedUrlData);
    if (expired)
    {
      Logger.debug(`HTI cached url expired: ${url}`);
      return null;
    }

    Logger.debug(`HTI url FOUND in cache: ${url}`);
    return cachedUrlData;
  }

  set(url, value)
  {
    const repCache = this.cacheStore.get(HTIStorageVariables.CACHE_HTI, []);
    if (!url && (repCache.length > HTIConstants.maxCacheRecords))
    {
      return;
    }

    // url details
    const urlObject = new URL(url);

    const hasChildPath = value.flags === 2 || value.flags === 3;

    // default FQDN url to cache
    let urlToCache = this.trimwww(urlObject.hostname);
    if (hasChildPath)
    {
      // if path has child will include the paths
      urlToCache += urlObject.pathname;
    }

    // Create new array of cached URL that doesn't contain the object matching the 'url'
    // Basically filtering out the object so that we can add it back with the latest values
    const filteredCache = repCache.filter((cacheObject) => cacheObject.url !== urlToCache);

    // Add the new url data
    filteredCache.push({
      ...value,
      timeCached: Date.now(),
      url: urlToCache
    });

    // update cache store with reputation
    this.cacheStore.set(HTIStorageVariables.CACHE_HTI, filteredCache);
  }

  // Cache object made of : { timeCached, value: { ttl, ... }}
  // Check whether a cached object has expired
  isExpired(cacheObj)
  {
    const { timeCached, ttl } = cacheObj;
    const currentTime = Date.now();
    const timeElapsedInSeconds = (currentTime - timeCached) / 1000;

    if (timeElapsedInSeconds > ttl)
    {
      return true;
    }

    return false;
  }

  trimwww(url)
  {
    if (url.startsWith('www.'))
    {
      url = url.substring(4);
    }
    return url;
  }

  processUrl(url)
  {
    // The first time we process the url we will do so with hostname and pathname (full url)
    // exclude url params/arguments
    const urlObject = new URL(url);

    const hostname = this.trimwww(urlObject.hostname);
    const cached = this.find(hostname + urlObject.pathname);

    // if found, then return
    if (cached)
    {
      return cached;
    }

    // if not found, then we continue but process only with FQDN (Check cache for FQDN)
    // FQDN: Fully Qualified Domain Name (without the path)
    return this.checkCacheForFQDN(hostname);
  }

  checkCacheForFQDN(fqdn)
  {
    const cached = this.find(fqdn);

    // if found, then return
    if (cached)
    {
      return cached;
    }

    const fqdnSplit = fqdn.split('.');
    if (fqdnSplit.length <= 2 || fqdnSplit[0] === '')
    {
      // shortest fqdn is with one dot (ie, example.com)
      // also there shouldn't be a domain like so: '.com', if so return null
      return null;
    }

    // subDomainLevelsToAdd set to 2 because this is the beginning of the index that we want to start appending subdomains
    // ie, this.is.an.example.com - 2 will take example.com
    const subDomainLevelsToAdd = 2;
    return this.checkCacheForRootDomain(fqdn, subDomainLevelsToAdd);
  }

  checkCacheForRootDomain(fqdn, level)
  {
    const domainToCheck = this.getIncrementalSubdomain(fqdn, level);
    if (domainToCheck === fqdn)
    {
      // return null. There is nothing to check
      // since the added subdomain is just the original fqdn
      // which was verify before on checkCacheForFQDN function
      return null;
    }

    const cached = this.find(domainToCheck);
    if (cached)
    {
      // if found, only return if it does not have a subdomain flag
      if (cached.flags !== 1 && cached.flags !== 3)
      {
        return cached;
      }

      // otherwise, we should continue to add the subdomain
    }

    return this.checkCacheForRootDomain(fqdn, level + 1);
  }

  /**
   * This helper functions creates a new domain url with subdomains based on the provided level
   * ie, fqdn: 'this.is.an.example.com' and level: 3
   * will return 'an.example.com'
   * @param {string} fqdn - full fqdn to be used for creating a domain
   * @param {integer} level - the subdomain levels to be added
   * @returns {string} - generated subdomains based on level
   */
  getIncrementalSubdomain(fqdn, level)
  {
    const reverseFqdn = fqdn.split('.').reverse();

    const maxLevel = reverseFqdn.length;
    if (maxLevel === level)
    {
      // No need to add subdomain as the level
      // provided matches the fqdn itself
      return fqdn;
    }

    const reverseFqdnResult = [];
    for (let i = 0; i < level; i++)
    {
      reverseFqdnResult.push(reverseFqdn[i]);
    }

    return reverseFqdnResult.reverse().join('.');
  }

  /**
   * Find and returns the cached data of the provided url from the EveryGreen and HTI Cache storage
   * @param {string} url - url to be look in the cached database
   * @returns {object|undefined} return object of the cached data url or undefined if not found
   */
  find(url)
  {
    // check if url is in the ever green cache
    const eguObject = this.cacheStore.get(HTIStorageVariables.EVER_GREEN_URLS, {});
    const { cacheTTL, timeCached, content } = eguObject;
    if (cacheTTL && timeCached && content)
    {
      const eguCachedEntry = content.find((urlRep) => urlRep.url === url);
      if (eguCachedEntry)
      {
        // converting evergreen url data to look the same as HTI cache data
        return {
          ...eguCachedEntry,
          timeCached,
          ttl: cacheTTL,
          url,
        };
      }
    }

    // check if url is in the cache
    const cacheEntries = this.cacheStore.get(HTIStorageVariables.CACHE_HTI, []);
    const cachedUrl = cacheEntries.find((urlRep) => urlRep.url === url);
    return cachedUrl;
  }
}
