/*
 *  Manages all attribute mappings, there are a couple level of mappings.
 *  Raw Event -> Event Mapping (uniqueid) -> Adjusted -> Global Mapping (legacy) -> Normalized -> Profile Mapping (transports) -> Transport Payload
 *
 *   - Event Mapping: maps uniqueid specific attributes for hot-fixes / fitting problems, etc. This should be used as last resort
 *   - Global Mapping: maps all events for handling legacy / short form / alias names
 *   - Profile Mapping: maps events specific for a profile. Each transport utilizes their own JSON names
 */
import Logger from './logger';
import { lowerCaseObjectKeyValuePair } from './utils';

export default class DictionaryMapper
{
  constructor({ event: eventMap, profile: profileMap, global: globalMap })
  {
    this.profileMapStringified = {};
    this.flippedProfileMap = null;

    // Lower case event and global dictionaries
    // Only lower case the keys for the profile dictionary
    lowerCaseObjectKeyValuePair(eventMap, true, true);
    lowerCaseObjectKeyValuePair(globalMap, true, true);

    this.eventMap = eventMap;
    this.globalMap = globalMap;

    // Profile has nested objects, their keys must be lowercased
    Object.keys(profileMap).forEach((profile) =>
    {
      lowerCaseObjectKeyValuePair(profileMap[profile], true, false);
    });

    // Cache / pre-stringify all the profiles
    Object.keys(profileMap).forEach((profile) =>
    {
      this.profileMapStringified[profile] = JSON.stringify(profileMap[profile]);
    });

    this.profileMap = profileMap;
  }

  getEventMap(obj, eventKey)
  {
    if (!this.eventMap.hasOwnProperty(eventKey))
    {
      return obj;
    }

    return this.map(this.eventMap[eventKey], obj, true);
  }

  getGlobalMap(obj)
  {
    return this.map(this.globalMap, obj, true);
  }

  getProfileMap(obj, profileKey)
  {
    if (!this.profileMap.hasOwnProperty(profileKey))
    {
      return obj;
    }

    return this.map(this.profileMap[profileKey], obj, true);
  }

  getProfileMapStringified(profileKey)
  {
    if (!this.profileMapStringified.hasOwnProperty(profileKey))
    {
      return '{}';
    }

    return this.profileMapStringified[profileKey];
  }

  getFlippedProfileMap(profileKey)
  {
    if (!this.profileMap.hasOwnProperty(profileKey))
    {
      Logger.error(`Requesting flipped table for invalid profile ${profile}`);
      return {};
    }

    // If already created, just return it
    if (this.flippedProfileMap.hasOwnProperty(profileKey))
    {
      return this.flippedProfileMap[profile];
    }

    // Table does not exist, create one
    this.flippedProfileMap[profileKey] = {};

    Object.keys(this.profileMap).forEach((key) =>
    {
      const newKey = this.profileMap[profileKey][key];
      this.flippedProfileMap[profileKey][newKey] = key;
    });

    return this.flippedProfileMap[profileKey];
  }

  map(mappingTable, toMap, inPlace)
  {
    if (!mappingTable || !toMap || (typeof toMap !== 'object'))
    {
      Logger.error('Invalid mappingTable or toMap');
      return toMap;
    }

    Logger.debug(`Executing mappings::_map ${inPlace ? 'in place' : 'in new buffer'}`);

    const newMap = {};

    for (const attribute in toMap)
    {
      const lowerAttribute = attribute.toLowerCase();
      if (lowerAttribute in mappingTable)
      {
        const newAttribute = mappingTable[lowerAttribute];
        if (inPlace)
        {
          toMap[newAttribute] = toMap[attribute];
          if (newAttribute != attribute)
          {
            delete toMap[attribute];
          }
        }
        else
        {
          newMap[newAttribute] = toMap[attribute];
        }
      }
    }

    return inPlace ? toMap : newMap;
  }
}
