// Background
import { ContentCommands } from '@extension/common/commands';
import InjectBase from '@extension/background/content_injector/inject_base';
import Utils, { digestMessage } from '@extension/utilities/utils';
import { BackgroundGlobals } from '@extension/background/background_globals';
import { CacheStorageVariables, LocalStorageVariables } from '@extension/common/cache_store_variables';
import { Logger } from '@extension/common/logger';

export default class InjectIDPS extends InjectBase
{
  constructor({ tabId })
  {
    super('content_idps.js');
    this.tabId = tabId;
    this.pingCommand = ContentCommands.PING_CONTENT_IDPS;
    this.email = null;
  }

  async check()
  {
    const { IDPS_FEATURE_ENABLED } = BackgroundGlobals.nativeSettings;
    const idpsFeatureEnabled = BackgroundGlobals.IDPS_IS_ENTITLED && IDPS_FEATURE_ENABLED.value === 2;

    const isSupported = Utils.isChromeBrowser() && Utils.isBrowserEN();
    if (!idpsFeatureEnabled || !isSupported)
    {
      Logger.debug('InjectIDPS: not supported/enabled.');
      return false;
    }

    this.email = BackgroundGlobals.cacheStore.get(CacheStorageVariables.WSS_EMAIL.name, '');
    if (!this.email)
    {
      Logger.debug('InjectIDPS: Invalid email.');
      return false;
    }

    const whitelisted = await this.isDWSWhitelisted(this.email);
    if (whitelisted)
    {
      Logger.debug('InjectIDPS: Whitelisted email.');
      return false;
    }

    // You have to sign into a page using the same email as the one used for your wss subscription, in order for this toast to show.
    const formInfoCache = this.getFormInfo(this.tabId);
    if (!formInfoCache || !this.email || this.email.toLowerCase() !== formInfoCache.username.toLowerCase())
    {
      Logger.debug('InjectIDPS: Invalid formCache or mismatch email.');
      formInfoCache && this.removeFormInfo(this.tabId);
      return false;
    }

    const dwsInfo = this.getCachedDWSInfo(this.email);
    // If dwsInfo is null, this is the first time and no data is yet available
    // so we can proceed to show the IDPS UI, if not null we need to verify some values
    if (dwsInfo !== null)
    {
      if (dwsInfo.shownCount >= 3)
      {
        Logger.debug('InjectIDPS: shownCount exceeded.');
        return false;
      }

      // 24 hours must elapse for balloon to show subsequent times (first time shown omits this check)
      const has24HoursElapsed = this.has24HoursElapsed(dwsInfo);
      if (!has24HoursElapsed)
      {
        Logger.debug('InjectIDPS: 24 hours not yet ellapsed.');
        return false;
      }
    }

    Logger.debug('InjectIDPS: success.');
    return true;
  }

  async isDWSWhitelisted(email)
  {
    const hash = await digestMessage(email);
    const whitelist = BackgroundGlobals.cacheStore.get(LocalStorageVariables.DWS_WHITELIST.name, []);
    return whitelist.includes(hash);
  }

  getCachedDWSInfo(email)
  {
    const cache = BackgroundGlobals.cacheStore.get(LocalStorageVariables.DWS_INFO.name, {});
    return cache[email] || null;
  }

  getFormInfo(tabId)
  {
    const cache = BackgroundGlobals.cacheStore.get(CacheStorageVariables.FORM_INFO.name, {});
    return cache[tabId] || null;
  }

  removeFormInfo(tabId)
  {
    const cache = BackgroundGlobals.cacheStore.get(CacheStorageVariables.FORM_INFO.name, {});
    const updatedCache = { ...cache };

    if (!updatedCache[tabId]) return;
    delete updatedCache[tabId];

    BackgroundGlobals.cacheStore.set(CacheStorageVariables.FORM_INFO.name, updatedCache);
  }

  has24HoursElapsed(info)
  {
    // Check if it has been 24h since last time we show the offer for that email.
    // Only check if the email has not been scanned before.
    const { lastShown } = info;
    if (lastShown)
    {
      const oneDayInMiliseconds = 1000 * 3600 * 24;
      const now = new Date().getTime();

      if (now < (lastShown + oneDayInMiliseconds))
      {
        return false;
      }
    }

    return true;
  }
}
