// Common
import { extension } from '@extension/common/webextension';
import { BrowseActionColors, Color } from '@extension/common/constants';
import { Logger } from '@extension/common/logger';

/**
 * Wrapper class for the extension's browserAction API. Responsible for putting the correct icon
 * on the browser's toolbar along with enabling / disabling action methods.
 * This allows data to be stored in memory for the specific tab
 */
export default class BrowserAction
{
  /**
   * Set the browser action icon based on a color (Color from constants.js)
   * @param  {number} color - color icon to be set (Color from constants.js)
   * @param  {number} tabId - id of the tab
   * @return {promise} - promise: 'badgeText' is returned after async call is resolved
   */
  static setIcon(color, tabId)
  {
    if (!extension.action) return;

    // set to specific tab
    if (!tabId)
    {
      return;
    }

    const path = BrowserAction.resolveColorToPath(color);
    const details = { path, tabId };

    try
    {
      extension.action.setIcon(details, () =>
      {
        const { lastError } = extension.runtime;
        if (lastError) Logger.error(lastError.message);
      });
    }
    catch (err)
    {
      Logger.error(`Unable to set icon, err: ${err}`);
    }
  }

  /**
   * Retrieve the corresponding image paths based on the color.
   * @param  {number} color - Resolves the image name based on the color integer value provided (Color from constant.js)
   * @return {object} - Resolve color to extension icon paths
   */
  static resolveColorToPath(color)
  {
    const filename = (BrowseActionColors[color] || BrowseActionColors[Color.DISABLED]).toLowerCase();
    return {
      16: `images/browser_action/${filename}_16.png`,
      20: `images/browser_action/${filename}_20.png`,
      32: `images/browser_action/${filename}_32.png`,
      40: `images/browser_action/${filename}_40.png`
    };
  }
}
