import Logger from './logger';

export default class RulesManager
{
  constructor()
  {
    this.dailyMaxTable = {};
  }

  notNull(value, meta) { return (value != null); }

  inRange(value, meta) { return (value >= meta.min) && (value <= meta.max); }

  equal(value, meta) { return (value == String(meta)); }

  greater(value, meta) { return (value > meta); }

  greaterEqual(value, meta) { return (value >= meta); }

  less(value, meta) { return (value < meta); }

  lessEqual(value, meta) { return (value <= meta); }

  notEqual(value, meta) { return (value != String(meta)); }

  startsWith(value, meta) { return !value.indexOf(meta); }

  endsWith(value, meta) { return value.indexOf(meta, value.length - meta.length) !== -1; }

  contains(value, meta) { return value.indexOf(meta) !== -1; }

  regex(value, meta)
  {
    try
    {
      let re = new RegExp(meta);
      // New regex suport, with flags
      if (meta.expr && meta.flags)
      {
        re = new RegExp(meta.expr, meta.flags);
      }
      return re.test(value);
    }
    catch (e)
    {
      Logger.warn(`rules.regex exception: ${e.message}`);
      return false;
    }
  }

  in(value, meta) { for (const i in meta) { if (value == String(meta[i])) return true; } return false; }

  isType(value, meta) { return (typeof value === meta); }

  isEqual(value, meta) { return value == meta; }

  not(value, meta)
  {
    return !(this[meta.ruleName](value, meta.meta));
  }

  or(value, meta)
  {
    for (const i in meta)
    {
      if (this[meta[i].ruleName](value, meta[i].meta)) return true;
    }
    return false;
  }

  and(value, meta)
  {
    for (const i in meta)
    {
      if (!this[meta[i].ruleName](value, meta[i].meta)) return false;
    }
    return true;
  }

  // value == date as ISO string or Date(), meta == delta in milliseconds
  date_earlier(value, meta)
  {
    let value_time = value;
    if (typeof value === 'string')
    {
      // convert to date
      value_time = new Date(value);
    }
    if (!(value_time instanceof Date))
    {
      Logger.debug(`date_earlier: invalid input value type is '${typeof value}'`);
      return false; // can't compare if the input is not a date
    }

    const now_time = new Date();
    Logger.debug(`date_earlier: compare (now - value = ${now_time - value_time}) < ${meta}`);
    return (now_time - value_time) < meta;
  }

  // value == date as ISO string or Date(), meta == delta in milliseconds
  date_later(value, meta)
  {
    let value_time = value;
    if (typeof value === 'string')
    {
      // convert to date
      value_time = new Date(value);
    }
    if (!(value_time instanceof Date))
    {
      Logger.debug(`date_later: invalid input value type is '${typeof value}'`);
      return false; // can't compare if the input is not a date
    }

    const now_time = new Date();
    Logger.debug(`date_later: compare (now - value = ${now_time - value_time}) > ${meta}`);
    return (now_time - value_time) > meta;
  }

  // Throttling rules, if it returns true means stop the event, false means send the event.
  // Sample - random sample / probability of being sent, meta is between 0 to 1
  sample(name, meta) { return Math.random() > meta; }

  getDailyMaxTableCounter(profileName)
  {
    const targetDailyMaxTable = this.dailyMaxTable[profileName];
    if (!targetDailyMaxTable)
    {
      return null;
    }

    return targetDailyMaxTable.counter;
  }

  // Daily - daily maximum events for each name, calculated based on a 24 hour intervals since first one
  dailyMax(name, meta)
  {
    // time interval, 24 hours == 86400000, this timeInterval is all that needs to be replaced if there is a weeklyMax, monthlyMax, etc.
    const timeInterval = 86400000;
    const currentTimeMs = Date.now();

    // If this is the first one, add entry in table and send the event OR If it's been 24 hours, reset the counter and send the event
    if ((!this.dailyMaxTable[name]) || (currentTimeMs - this.dailyMaxTable[name].startTimeMs > timeInterval))
    {
      this.dailyMaxTable[name] = {
        counter: 1,
        startTimeMs: currentTimeMs
      };
      return false;
    }

    this.dailyMaxTable[name].counter++;

    // If it's less the limit per day, then send the event
    if (this.dailyMaxTable[name].counter < meta)
    {
      return false;
    }

    Logger.warn(`dailyMax: reached the maximum for the day for ${name}, max: ${meta} current: ${this.dailyMaxTable[name].counter}`);
    return true;
  }

  /**
   * Evaluates if a value passes a rule
   * @param {Any} value - Value being evaluated
   * @param {Object} ruleConfig - Rules
   * @return true if passed, false otherwise or invalid ruleConfig
   */
  evaluate(value, ruleConfig)
  {
    try
    {
      if (!ruleConfig || !ruleConfig.ruleName)
      {
        return false;
      }

      return this[ruleConfig.ruleName](value, ruleConfig.meta);
    }
    catch (e)
    {
      Logger.error(`rules.evaluate: ${e.message}`);
      return false;
    }
  }

  // This function is a specific version of evaluate just for event attributes
  apply(value, ruleConfig)
  {
    try
    {
      if (!ruleConfig || !ruleConfig.ruleName) return value;

      // corner case rule 'override' that will return meta
      if (ruleConfig.ruleName === 'override')
      {
        // log if the override is passed something, it should be passed nothing
        if (value != null)
        {
          Logger.warn(`Using override value: replacing ${value} with ${ruleConfig.meta}`);
        }
        return ruleConfig.meta;
      }

      if (this[ruleConfig.ruleName](value, ruleConfig.meta))
      {
        return value;
      }

      if (((value === undefined) || (value === null)) && ruleConfig.optional)
      {
        return '[not assigned]';
      }

      if (ruleConfig.onMismatch === undefined)
      {
        return '[ruleMismatch]';
      }
      return ruleConfig.onMismatch;
    }
    catch (e)
    {
      if (!ruleConfig.onError)
      {
        return '[ruleError]';
      }
      return ruleConfig.onError;
    }
  }
}
