import Storage from '../../Common-Utils-Package/storage/storage';
import Logger from '../../Common-Utils-Package/logger/logger';
import HttpService from '../../Common-Utils-Package/http_service/http_service';
import ScheduleManager from '../../Common-Utils-Package/scheduler/scheduler_manager';
import HTIResponseBuilder from './hti_response_builder';
import HTICache from './hti_cache';
import { HTIStorageVariables } from './hti_storage_variables';
import { HTIGlobals } from './hti_globals';
import { generateHeaders } from './hti_headers';
import { schedulers } from './schedulers/schedulers';

export default class HTIRequestor
{
  /**
   * HTI Requestor: constructor
   * @param {object} wasm - wasm engine object
   * @param {function} wasm.get_wa_app_key - should contain function
   * @param {function} wasm.get_wa_shared_key - should contain function
   * @param {function} wasm.generate_value_for_ac - should contain function
   * @param {object} options - object containinig optional params that alter the behavior of this lib
   * @param {string} options.clientIdGuid - client id passed as part of the hti request header (default as: '87d8d1082c2f2f821f438b2359b7a5b4')
   * @param {string} options.cspClientId - csp client id
   * @param {string} options.serverUrl - url string to the hti server (default as: 'https://threat.api.mcafee.com/')
   * @param {string} options.productVersion - product version for gti call (default as: '0.0.0.0')
   */
  constructor(wasm, options)
  {
    // required param
    HTIGlobals.wasm = wasm;
    this.httpService = null;
    this.htiCache = null;
    HTIGlobals.storage = null;

    // optional params
    HTIGlobals.clientIdGuid = options.clientIdGuid || HTIGlobals.clientIdGuid;
    HTIGlobals.productVersion = options.productVersion || '0.0.0.0';
    HTIGlobals.serverUrl = options.serverUrl || HTIGlobals.serverUrl;
    HTIGlobals.cspClientId = options.cspClientId || HTIGlobals.cspClientId;
  }

  async init()
  {
    // verify wasm
    if (!HTIGlobals.wasm
      || typeof HTIGlobals.wasm.get_wa_app_key !== 'function'
      || typeof HTIGlobals.wasm.get_wa_shared_key !== 'function'
      || typeof HTIGlobals.wasm.generate_value_for_ac !== 'function')
    {
      Logger.error('Invalid wasm object given on hti requestor.');
      return false;
    }

    // initialize storage
    HTIGlobals.storage = new Storage(Object.keys(HTIStorageVariables));
    const success = await HTIGlobals.storage.init();
    if (!success)
    {
      Logger.error('Failed to initialize storage.');
      return false;
    }

    this.htiCache = new HTICache(HTIGlobals.storage);
    this.htiCache.setAlarmCacheClean();

    this.httpService = new HttpService();

    // initialize scheduler manager
    HTIGlobals.scheduleManager = new ScheduleManager(HTIGlobals.storage, schedulers);
    HTIGlobals.scheduleManager.init();

    return true;
  }

  /**
   * This function allows modifying the global variables
   * ie: globalValues = { serverUrl: 'https://someurl.com', otherGlobal: 'somedata' }
   * @param {object} globalValues
   * @param {key} globalsValues.key
   * @param {value} globalsValues.value
   */
  setGlobals(globalValues)
  {
    if (typeof globalValues !== 'object'
    || Array.isArray(globalValues)
    || globalValues === null)
    {
      Logger.error('Received invalid object (json) type when trying to call MTI setGlobals.');
      return;
    }

    for (const [key, value] of Object.entries(globalValues))
    {
      Logger.log(`HTI Global values updated: ${key}: ${value}`);
      HTIGlobals[key] = value;
    }
  }

  async request(requestUrls, headers)
  {
    const promises = [];
    requestUrls.forEach((url) =>
    {
      // remove params and anchor from url
      const urlWithoutParams = url.split(/[?#]/)[0];

      const base64URL = btoa(urlWithoutParams);
      const request = {
        method: 'GET',
        url: HTIGlobals.serverUrl + HTIGlobals.urlReputation + base64URL,
        headers,
      };

      const requestPromise = this.httpService.sendHttpRequest(request).then((res) =>
      {
        if (res.ok)
        {
          return res.json();
        }

        if (res.status === 404)
        {
          Logger.warn(`HTI reputation not found for url: ${url}, will return unrated reputation`);
          return {
            category: [],
            flags: 0,
            trust: 0,
            ttl: 3600,
          };
        }

        return {};
      });

      promises.push(requestPromise);
    });

    const response = await Promise.all(promises);

    // Attach url from requestUrls back into the hti response object
    const result = response.map((urlResult, index) =>
    {
      urlResult.url = requestUrls[index];
      return urlResult;
    });

    return result;
  }

  prepareBody(requestData, builder, checkCacheOnly = false)
  {
    const requestUrls = [];

    // loop through each request url to see whether we have cached data
    for (let startIndex = 0; startIndex < requestData.length; startIndex++)
    {
      const url = requestData[startIndex];
      const urlCacheObject = this.htiCache.getReputation(url);

      if (urlCacheObject === null)
      {
        if (checkCacheOnly)
        {
          // if we are to do cache check only, no need to prepare
          // for HTI request calls.
          continue;
        }

        // There is no data for this uri in cache so add this to the question list.
        requestUrls.push(url);
      }
      else
      {
        builder.addCachedItem({ url, value: urlCacheObject });
        // Remove cached item from requestdata.
        requestData.splice(startIndex, 1);
        startIndex--;
      }
    }

    return requestUrls;
  }

  toResponse(htiResponse, builder, requestData, fncallback)
  {
    builder.init(htiResponse, requestData); // "[]" = empty hti response.
    builder.prepareResponse(fncallback); // Concatenate the responses and then return to content.
  }

  /**
   * Does post to HTI splitting the requests as per guidelines to 5 questions per request.
   * @param {object}   requestData    - Array of request URLs whose reputation is asked to HTI
   * @param {function} fncallback     - Callback function invoked to respond to the query from content process
   * @param {object}   options        - optional params
   * @param {boolean}  options.checkCacheOnly - Check Cache only for HTI, do not make HTTP HTI request
   */
  async post(requestData, fncallback, options)
  {
    const checkCacheOnly = options.checkCacheOnly || false;

    const promises = this.htiCache.checkRequest(requestData);
    if (promises.length > 0)
    {
      await Promise.all(promises);
    }

    try
    {
      const builder = new HTIResponseBuilder(this.htiCache);

      // requestData will only contain non-cached items after this
      const requestUrls = this.prepareBody(requestData, builder, checkCacheOnly);
      if (requestUrls.length > 0)
      {
        // If there are urls that we need to request for reputation
        // then we proceed to call HTI
        try
        {
          const promise = new Promise(async (resolve) =>
          {
            const headers = generateHeaders();
            const response = await this.request(requestUrls, headers);
            this.toResponse(response, builder, requestData, fncallback);
            this.htiCache.deleteRequest(requestData);
            resolve();
          });

          this.htiCache.addRequest(requestData, promise);
        }
        catch (error)
        {
          fncallback(null);
          Logger.error(error);
        }
      }
      else
      {
        // If there are no need to call HTI for requests (i.e. Everything is cached already)
        // we can  prepare the response right away.
        const htiResponse = [];
        const fullRequestData = requestData.slice();
        this.toResponse(htiResponse, builder, fullRequestData, fncallback);
      }
    }
    catch (e)
    {
      Logger.error(e.message);
    }
  }

  getCache()
  {
    return this.htiCache;
  }
}
