import BaseTransmitter from './base_transmitter';
import Logger from './logger';
import { mergeObjects, lowerCaseObjectKeyValuePair, isoDateToDate } from './utils';
import { TelemetryGlobals } from './telemetry_globals';

export default class TransportError extends BaseTransmitter
{
  constructor()
  {
    this.profileName = 'profile_analytics_automation_mosaic_100p';
    this.eventName = 'analytics_event_error_occurrred';
    this.emitter = this.getEmitter(this.profileName);
  }

  generate(eventName, errorMessage)
  {
    const outbound = {
      'hit.uniqueid': 'analytics_event_error_occurrred',
      'event.category': 'Analytics.Event.Error',
      'hit.trigger': eventName,
      'event.action': 'Analytics.Event.Rule.Failed'
    };

    if (Object.keys(errorMessage.type.ruleMismatch).length)
    {
      outbound['hit.category.1'] = 'ruleMismatch';
      outbound['event.label'] = JSON.stringify(errorMessage);
    }
    else if (Object.keys(errorMessage.type.ruleError).length)
    {
      outbound['hit.category.1'] = 'ruleError';
      outbound['event.label'] = JSON.stringify(errorMessage);
    }
    else if (errorMessage.type.rejected)
    {
      outbound['hit.category.1'] = 'rejected';
      outbound['event.label'] = JSON.stringify(errorMessage);
    }

    const timeNow = new Date();
    outbound['__record.created'] = timeNow.toISOString();
    outbound['Tracker.Type'] = 'event';

    return outbound;
  }

  async transmit(eventName, payload, errorMessage)
  {
    Logger.debug(`CreateAnalyticsErrorlogTransport.transmit message=${JSON.stringify(errorMessage)}, profileNames=${JSON.stringify(this.profileName)}`);

    // Event-level throttle
    if (this.isEventThrottled(this.eventName))
    {
      Logger.debug(`Event ${this.eventName} was event-level throttled`);
      return;
    }

    // Profile-level throttle
    if (this.isProfileThrottled(this.eventName, this.profileName))
    {
      Logger.debug(`Event ${this.eventName} was profile-level throttled by ${this.profileName}`);
      return;
    }

    if (!this.emitter)
    {
      Logger.error(`Failed to locate an emitter for ${this.profileName}; aborting send`);
      return;
    }

    let outbound = this.generate(eventName, errorMessage);
    lowerCaseObjectKeyValuePair(outbound, true, false); // Lowercase the keys

    // Apply attribute rules
    outbound = this.applyAttributeRules(outbound, this.eventName, this.profileName);

    // Add event day index
    outbound['__event.day.index'] = TelemetryGlobals.rulesManager.getDailyMaxTableCounter(this.profileName);

    // Add throttle
    outbound.__throttle_user_multiplier = this.emitter.throttleMultiplier || 1;

    // Add default elements (UserID, contentVersion, etc)
    mergeObjects(outbound, await TelemetryGlobals.engine.getDefaultDictionary(), true); // Lowercase keys

    // Add dataset names
    this.addDataSetNames(outbound, this.emitter, null);

    // Add queue time
    const now_time = new Date();
    let timeStamp = unescape(outbound['__record.created']);
    timeStamp = timeStamp.split(' ').join('T');

    const create_time = isoDateToDate(timeStamp);

    if (now_time && create_time)
    {
      outbound['__queue.time.milliseconds'] = `${now_time - create_time}`;
    }

    Logger.debug(`ready to send ${JSON.stringify(outbound)}`);
    const result = await this.emitter.send(outbound);

    // Log error message
    Logger.logAutomationError(messageName, JSON.stringify(payload), JSON.stringify(errorMessage));
    Logger.debug(`ProfileName: ${profileName}. Result: ${result}`);
    Logger.debug('******************************* done');
  }
}
