// Background
import { BackgroundGlobals } from '@extension/background/background_globals';
import WhitelistWrapper from '@extension/background/whitelist_wrapper';
// Common
import UrlParser from '@extension/common/url_parser';
import HttpService from '@extension/common/http_service';
import BackgroundCommon from '@extension/common/background_common';
import { LocalStorageVariables } from '@extension/common/cache_store_variables';
import { Logger } from '@extension/common/logger';

/**
 * TODO: Comments
 */
export default class Typosquatting
{
  constructor()
  {
    this.whitelistStore = LocalStorageVariables.TYPOSQUATTING_WHITELIST.name;
    this.baseUrl = BackgroundGlobals.typoSquattingServer;
    this.locale = 'en_us';
    this.http = new HttpService();
  }

  async sendAndReceiveRequest({ url, headers })
  {
    let timerId;
    const expiryTimer = new Promise((resolve, reject) =>
    {
      timerId = setTimeout(() => reject(new Error('Typosquatting timed out')), 3000);
    });

    const promisesToRun = async () =>
    {
      const res = await this.http.sendHttpRequest({ method: 'GET', url, headers });
      clearTimeout(timerId);

      if (!res)
      {
        Logger.error('Invalid response for typosquatting request');
        return {};
      }

      try
      {
        const text = await res.text();
        return JSON.parse(text);
      }
      catch (error)
      {
        Logger.error(`Unexpected Error on promisesToRun, err: ${error}`);
      }

      return {};
    };

    return Promise.race([expiryTimer, promisesToRun()]).catch((err) =>
    {
      Logger.warn(`Fail to execute sendAndReceiveRequest, err: ${err}`);
      return {};
    });
  }

  parseUrlToObject(payload)
  {
    const result = {};
    payload.split('&').forEach((argument) =>
    {
      const [key, value] = argument.split('=');
      if (key === 'bdomain') result.typoDomain = value;
      if (key === 'bname') result.brandName = unescape(value).replace(/\+/g, ' ');
      if (key === 'xp') result.experience = value === 'search' ? 3 : 1;
      if (key === 'targeturl') result.typoRedirect = unescape(value.replace(/\0/g, ''));
    });

    return result;
  }

  async processResponse({ url }, domain, originalUrl)
  {
    if (url === '' || url === undefined)
    {
      BackgroundGlobals.typoCache.addEntry(domain, { experience: -1 });
      return null;
    }

    if (!BackgroundCommon.isSiteAdvisorUrl(url))
    {
      return null;
    }

    const payload = UrlParser.getParam(url, 'payload');

    if (payload === null || payload === undefined)
    {
      BackgroundGlobals.typoCache.addEntry(domain, { experience: -1 });
      return null;
    }

    try
    {
      const payloadDecrypted = await BackgroundGlobals.wasm.wasm_typosquat_decrypt(decodeURIComponent(payload));
      const item = this.parseUrlToObject(payloadDecrypted);
      item.originalUrl = originalUrl;
      BackgroundGlobals.typoCache.addEntry(domain, item);
      return domain;
    }
    catch (error)
    {
      Logger.error(error);
      return null;
    }
  }

  async lookupTypoSquatFromServer(originalUrl)
  {
    try
    {
      const domain = UrlParser.getDomain(originalUrl);

      const promises = BackgroundGlobals.typoCache.checkRequest([domain]);
      await Promise.all(promises);

      const domainData = BackgroundGlobals.typoCache.getEntry(domain);
      if (domainData)
      {
        return domainData.experience === -1 ? null : domain;
      }

      const req = `domain=${domain}&locale=${this.locale}`;
      const url = `${this.baseUrl}${req}`;
      const promise = BackgroundGlobals.wasm.wasm_typosquat_sha256(req)
        .then((checkSum) => this.sendAndReceiveRequest({ url, headers: { 'Content-Checksum': checkSum } }))
        .then((response) => this.processResponse(response, domain, originalUrl))
        .then((responseDomain) =>
        {
          BackgroundGlobals.typoCache.deleteRequest([domain]);
          return responseDomain;
        })
        .catch((err) =>
        {
          Logger.warn(`TypoSquatting promise request error: ${err}`);
          BackgroundGlobals.typoCache.deleteRequest([domain]);
          return null;
        });

      BackgroundGlobals.typoCache.addRequest([domain], promise);
      return promise;
    }
    catch (error)
    {
      Logger.warn(`TypoSquatting try/catch error: ${error}`);
    }
    return null;
  }

  isInWhitelist(domain)
  {
    return WhitelistWrapper.isDomainInWhitelist(domain, this.whitelistStore);
  }
}
