export default class NativeSetValues
{
  constructor(nativeDispatcher)
  {
    this.dispatcher = nativeDispatcher;
    this.settings = [];
  }

  add(name, value, scope, type)
  {
    this.settings.push({ name, value, scope, type });
  }

  send()
  {
    if (this.dispatcher) this.dispatcher.setValues(this.settings);
  }
}
