// Background
import { BackgroundGlobals } from '@extension/background/background_globals';
import BackgroundUtils from '@extension/background/background_utils';
import WhitelistWrapper from '@extension/background/whitelist_wrapper';
import UnifiedAuthClient from '@extension/background/UnifiedAuthClient';
import ContentGlobalManager from '@extension/background/content_globals';
import FormDetection from '@extension/background/form_detection';

// Common
import { CacheStorageVariables, LocalStorageVariables } from '@extension/common/cache_store_variables';
import { BrowserType, BuildSettings } from '@extension/common/constants';
import { BackgroundCommands } from '@extension/common/commands';
import { extension } from '@extension/common/webextension';
import { Logger } from '@extension/common/logger';
import UrlParser from '@extension/common/url_parser';
// Utilities
import { digestMessage } from '@extension/utilities/utils';

/**
 * Handles communication from content to the background process. The content process has a number of commands that it may
 * call for or invoke in the background context. This class handles those requests. A dispatcher will then be invoked to handle
 * the content's request and response data may be sent back to the content process.
 */
export default class MessageListener
{
  constructor(promisesToComplete)
  {
    this.promisesToComplete = promisesToComplete;
  }

  getDomainName(srcvideo)
  {
    let url = null;

    // If the protocol is not there as part of the video, then
    // the srcvideo is treated as relative to chrome extensions location
    if (srcvideo.startsWith('http') || srcvideo.startsWith('www'))
    {
      url = srcvideo;
    }
    else
    {
      url = `https://${srcvideo}`;
    }

    const link = UrlParser.getDomain(url);
    return link.startsWith('www.') ? link.substr(4) : link;
  }

  init()
  {
    extension.runtime.onMessage.addListener((request, sender, cb) =>
    {
      try
      {
        const sendResponse = (details = null) =>
        {
          cb(details);
        };

        this.executeCommand(request, sender, sendResponse);
      }
      catch (e)
      {
        Logger.error(e.message);
      }

      return true;
    });
  }

  async executeCommand(request, sender, cb)
  {
    // Make sure all promises that need to be initialized are completed.
    await this.promisesToComplete;

    this[request.command] && this[request.command]({ request, sender, cb });
  }

  [BackgroundCommands.IS_WHITELISTED]({ request, cb })
  {
    let bwhitelisted = false;
    const domain = UrlParser.getDomain(request.url);
    if (WhitelistWrapper.isDomainInWhitelist(domain, LocalStorageVariables.BLOCK_PAGE_WHITELIST.name))
    {
      bwhitelisted = true;
    }
    cb({ whitelisted: bwhitelisted });
  }

  [BackgroundCommands.GET_TAB_DATA]({ request, sender, cb })
  {
    let tabData = null;

    if (request.tabId)
    {
      tabData = { ...BackgroundGlobals.tabData.get(request.tabId), url: sender.url };
    }
    else
    {
      tabData = BackgroundGlobals.tabData.get(sender.tab.id);
    }

    cb(tabData);
  }

  /** TODO: comments */
  [BackgroundCommands.VIEW_SITE_REPORT]({ request, sender, cb })
  {
    const siteReportPath = 'html/site_status_site_report.html';
    const siteReportUrl = extension.runtime.getURL(siteReportPath);
    if (request.showInNewTab)
    {
      extension.tabs.create({ url: siteReportUrl }, (tabInfo) =>
      {
        BackgroundGlobals.tabData.setTabBlockPageData(tabInfo.id, { url: request.url });
      });
    }
    else
    {
      extension.tabs.update(sender.tab.id, { url: siteReportUrl });
    }

    cb(true);
  }

  /** TODO: comments */
  [BackgroundCommands.AUTO_RUN_VIDEO_SITE]({ request, sender, cb })
  {
    extension.tabs.get(sender.tab.id, (tabinfo) =>
    {
      const { ffViewports } = BackgroundGlobals;
      let x = -1;
      let y = -1;

      if (BuildSettings.BROWSER_TYPE === BrowserType.FIREFOX && ffViewports !== null)
      {
        x = ffViewports.X;
        y = ffViewports.Y;
      }

      const domain = this.getDomainName(tabinfo.url);
      BackgroundGlobals.messageDispatcher.autoPlayVideoSite(domain, request.url, x, y);
    });

    cb(true);
  }

  [BackgroundCommands.CACHE_STORE]({ request, cb })
  {
    const { action, data } = request;
    BackgroundGlobals.messageDispatcher.cacheStore(action, data, cb);
  }

  /** TODO: comments */
  [BackgroundCommands.EXECUTE_COMMAND]({ request, cb })
  {
    BackgroundGlobals.messageDispatcher.executeCommand(request.commandId, request.params);
    cb(true);
  }

  /** TODO: comments */
  [BackgroundCommands.FOCUS_OR_CREATE_TAB]({ request, cb })
  {
    BackgroundUtils.focusOrCreateTab(request.url);
    cb(true);
  }

  /** TODO: comments */
  [BackgroundCommands.REMOVE_TAB]({ sender, cb })
  {
    extension.tabs.remove(sender.tab.id, () => {});
    cb(true);
  }

  /** TODO: comments */
  [BackgroundCommands.GET_BK_GLOBALS]({ request, cb })
  {
    const globals = ContentGlobalManager.get(BackgroundGlobals, request.bIncludeSearchEngines);
    cb(globals);
  }

  /** TODO: comments */
  [BackgroundCommands.GET_CLICK_EVENT_TIME]({ sender, cb })
  {
    cb(BackgroundGlobals.tabIdToLastClickTime.get(sender.tab.id));
  }

  /** TODO: comments */
  [BackgroundCommands.GET_EXTENSION_STATUS]({ request, cb })
  {
    BackgroundGlobals.messageDispatcher.getExtensionStatus(request, cb);
  }

  /** TODO: comments */
  [BackgroundCommands.TI_REQUEST]({ request, cb })
  {
    const { requestData, referrer } = request;
    BackgroundGlobals.messageDispatcher.makeMTIRequest(requestData, referrer).then((response) => cb(response));
  }

  /** TODO: comments */
  [BackgroundCommands.GET_TYPOSQUATTING_DATA]({ request, cb })
  {
    const entry = BackgroundGlobals.typoCache.getEntry(request.domain);
    const response = entry || null;
    cb(response);
  }

  /** TODO: comments */
  [BackgroundCommands.INSTALL_EXTENSION]({ request, cb })
  {
    BackgroundGlobals.messageDispatcher.installExtension(request.extension_id, request.redirect_url);
    cb(true);
  }

  /** TODO: comments */
  [BackgroundCommands.IS_FRAME_BLOCKED]({ request, sender, cb })
  {
    const ret = BackgroundGlobals.messageDispatcher.isFrameBlocked(request.url, sender.tab.id);
    cb(ret);
  }

  /** TODO: comments */
  [BackgroundCommands.SAVE_CLICK_EVENT_TIME]({ request, sender, cb })
  {
    BackgroundGlobals.tabIdToLastClickTime.set(sender.tab.id, request.last_event);
    cb(true);
  }

  /** TODO: comments */
  [BackgroundCommands.SEND_TELEMETRY]({ request, cb })
  {
    BackgroundGlobals.messageDispatcher.sendTelemetry(request.telemetry);
    cb(true);
  }

  /** TODO: comments */
  [BackgroundCommands.SET_VIEWPORT]({ request, cb })
  {
    const { x, y } = request;
    BackgroundGlobals.messageDispatcher.SetXYViewport(x, y);
    cb(true);
  }

  /** TODO: comments */
  [BackgroundCommands.ANY_BLOCKED_IFRAMES]({ request, sender, cb })
  {
    BackgroundGlobals.messageDispatcher.anyBlockedIframes(sender.tab.id, request.frameUrls, cb);
  }

  /** TODO: comments */
  [BackgroundCommands.UNBLOCK_ALL_IFRAMES]({ sender, cb })
  {
    BackgroundGlobals.messageDispatcher.unblockAllIframes(sender.tab.id, sender.tab.url);
    cb(true);
  }

  /** For handling CryptoJacking Topbar */
  [BackgroundCommands.ANY_BLOCKED_CRYPTOSCRIPTS]({ sender, cb })
  {
    BackgroundGlobals.messageDispatcher.anyBlockedCryptoScripts(sender.tab.id, cb);
  }

  /** TODO: comments */
  [BackgroundCommands.WHITELIST]({ request, sender, cb })
  {
    const { action, type, data } = request;
    const tabId = sender.tab ? sender.tab.id : null;
    data.tabId = tabId;
    BackgroundGlobals.messageDispatcher.whitelist(action, type, data, cb);
  }

  /** TODO: comments */
  [BackgroundCommands.GET_POPUP_DATA]({ sender, cb })
  {
    BackgroundGlobals.messageDispatcher.getPopupData(sender.tab.id, sender.tab.url, cb);
  }

  /** TODO: comments */
  [BackgroundCommands.GET_SETTINGS_DATA]({ cb })
  {
    BackgroundGlobals.messageDispatcher.getSettingsData(cb);
  }

  /** TODO: comments */
  [BackgroundCommands.SEARCH_ANNOTATION]({ request, cb })
  {
    const { action, data } = request;
    BackgroundGlobals.messageDispatcher.searchAnnotation(action, data, cb);
  }

  /** TODO: comments */
  [BackgroundCommands.SOCIAL_MEDIA_ANNOTATION]({ request, cb })
  {
    const { action, data } = request;
    BackgroundGlobals.messageDispatcher.socialMediaAnnotation(action, data, cb);
  }

  /** TODO: comments */
  [BackgroundCommands.RESET_SETTINGS]({ cb })
  {
    BackgroundGlobals.messageDispatcher.resetSettings();
    cb(true);
  }

  /** TODO: comments */
  [BackgroundCommands.RESET_NATIVE_SETTING]({ request, cb })
  {
    const { setting } = request;
    BackgroundGlobals.messageDispatcher.resetNativeSetting(setting);
    cb(true);
  }

  /**
   * Update the stored engine stats.
   * @param {Object} param0
   * @param {Object} param0.request - The request object sent from the onMessage handler on the background
   */
  [BackgroundCommands.UPDATE_ENGINE_STATS]({ request, cb })
  {
    // Destructures object and array types by reference, other data types by value
    for (const { name, selectors } of BackgroundGlobals.searchEngines)
    {
      if (name === request.engine.name && selectors.length === request.engine.selectors.length)
      {
        for (let index = 0; index < selectors.length; index++)
        {
          selectors[index].matched += request.engine.selectors[index].matched;
        }
        break;
      }
    }

    cb(true);
  }

  /** TODO: comments */
  [BackgroundCommands.CONTENT_HANDLER]({ sender, request, cb })
  {
    extension.tabs.sendMessage(sender.tab.id, request.message, () =>
    {
      if (extension.runtime.lastError)
      {
        Logger.log(extension.runtime.lastError.message);
      }
    });

    cb(true);
  }

  [BackgroundCommands.UPDATE_RAT_DETECTION_SHOWING_FLAG]({ request, sender, cb })
  {
    const { showed } = request;
    BackgroundGlobals.messageDispatcher.updateRatDetectionShowingFlag(showed, sender.url, sender.tab.id);
    cb(true);
  }

  /** Request and save results for search suggestion */
  [BackgroundCommands.SEARCH_SUGGEST]({ request, cb })
  {
    const { searchTerm } = request;
    BackgroundGlobals.messageDispatcher.searchSuggest(searchTerm).then((response) => cb(response));
  }

  /**
   * Form data is saved then retrieved from content when the user is redirected & logged in to their service
   * This cached information will then be used to trigger the IDPS UI
   * @param {*} param
   * @returns
   */
  [BackgroundCommands.SAVE_FORM_INFO]({ request, sender, cb })
  {
    const tabId = sender.tab.id;
    const { username, hostname } = request;

    // username is same as email here
    digestMessage(username).then((hash) =>
    {
      const storedEmails = BackgroundGlobals.cacheStore.get(LocalStorageVariables.DWS_WHITELIST.name, []);
      const isEmailSaved = storedEmails.includes(hash);

      if (!isEmailSaved)
      {
        const cache = BackgroundGlobals.cacheStore.get(CacheStorageVariables.FORM_INFO.name, {});
        cache[tabId] = {
          username,
          hostname
        };
        BackgroundGlobals.cacheStore.set(CacheStorageVariables.FORM_INFO.name, cache);
      }
    });

    cb(true);
  }

  /**
   * Form data for multistep login forms is saved then retrieved from content when the user is redirected & logged in to their service.
   * This cached information will then be used to trigger the IDPS UI
   * @param {*} param0
   * @returns
   */
  [BackgroundCommands.SAVE_MULTISTEP_LOGIN]({ request, sender, cb })
  {
    const { completeLogin, data } = request;
    const { hostname } = data;
    const tabId = sender.tab.id;
    const savedLogins = BackgroundGlobals.cacheStore.get(CacheStorageVariables.SAVE_MULTI_STEP_LOGIN.name, {});

    if (!completeLogin)
    {
      const { username } = data;

      // username is same as email here
      digestMessage(username).then((hash) =>
      {
        const storedEmails = BackgroundGlobals.cacheStore.get(LocalStorageVariables.DWS_WHITELIST.name, []);
        const isEmailSaved = storedEmails.includes(hash);

        if (!isEmailSaved)
        {
          savedLogins[`${hostname}-${tabId.toString()}`] = username;
          BackgroundGlobals.cacheStore.set(CacheStorageVariables.SAVE_MULTI_STEP_LOGIN.name, savedLogins);
        }
      });
    }
    else
    {
      const username = savedLogins[`${hostname}-${tabId.toString()}`];
      // if the username was not an email address or it was whitelisted, then it was not saved and no need to continue.
      if (!username)
      {
        return;
      }

      const cache = BackgroundGlobals.cacheStore.get(CacheStorageVariables.FORM_INFO.name, {});
      cache[tabId] = {
        hostname,
        username
      };
      BackgroundGlobals.cacheStore.set(CacheStorageVariables.FORM_INFO.name, cache);

      // Remove multistep login username
      const updatedSavedLogins = { ...savedLogins };
      delete updatedSavedLogins[`${hostname}-${tabId.toString()}`];
      BackgroundGlobals.cacheStore.set(CacheStorageVariables.SAVE_MULTI_STEP_LOGIN.name, updatedSavedLogins);
    }

    cb(true);
  }

  /**
   * Gets the cached form info for the current tab
   * Really the info itself doesn't matter and isn't ever actually used.
   * It is just consumed as a flag to tell the content_idps banner whether or not it it should show the popup.
   * @param {*} param0
   * @returns
   */
  [BackgroundCommands.GET_FORM_INFO_CACHE]({ sender, cb })
  {
    const tabId = sender.tab.id;
    const cache = BackgroundGlobals.cacheStore.get(CacheStorageVariables.FORM_INFO.name, {});
    const response = cache[tabId] || null;
    cb(response);
  }

  // Remove cached form info for current tab
  [BackgroundCommands.CLEAR_FORM_INFO_CACHE]({ sender, cb })
  {
    const tabId = sender.tab.id;
    const cache = BackgroundGlobals.cacheStore.get(CacheStorageVariables.FORM_INFO.name, {});
    const updatedCache = { ...cache };

    if (!updatedCache[tabId]) return;
    delete updatedCache[tabId];

    BackgroundGlobals.cacheStore.set(CacheStorageVariables.FORM_INFO.name, updatedCache);
    cb(true);
  }

  [BackgroundCommands.UPDATE_DWS_WHITELIST]({ request, cb })
  {
    digestMessage(request.email).then((hash) =>
    {
      const storedEmails = BackgroundGlobals.cacheStore.get(LocalStorageVariables.DWS_WHITELIST.name, []);
      const isEmailSaved = storedEmails.includes(hash);

      if (!isEmailSaved)
      {
        storedEmails.push(hash);
        BackgroundGlobals.cacheStore.set(LocalStorageVariables.DWS_WHITELIST.name, storedEmails);
      }
    });

    cb(true);
  }

  /** Get DWS info cached if any existing */
  [BackgroundCommands.GET_CACHED_DWS_INFO]({ request, cb })
  {
    const { email } = request;
    const cache = BackgroundGlobals.cacheStore.get(LocalStorageVariables.DWS_INFO.name, {});
    const response = cache[email] || null;
    cb(response);
  }

  [BackgroundCommands.CLEAR_CACHED_DWS_INFO]({ request, cb })
  {
    const { email } = request;
    const dwsCache = BackgroundGlobals.cacheStore.get(LocalStorageVariables.DWS_INFO.name, {});

    if (!dwsCache[email]) return;

    delete dwsCache[email];
    BackgroundGlobals.cacheStore.set(LocalStorageVariables.DWS_INFO.name, dwsCache);

    cb(true);
  }

  [BackgroundCommands.UPDATE_DWS_SHOWN]({ request, cb })
  {
    const { email } = request;

    // Update the last shown timestamp and counter
    const dwsCache = BackgroundGlobals.cacheStore.get(LocalStorageVariables.DWS_INFO.name, {});
    const savedInfo = dwsCache[email] || {};

    const shownCount = savedInfo.shownCount ? savedInfo.shownCount + 1 : 1;
    const lastShown = new Date().getTime(); // time: now

    dwsCache[email] = { ...savedInfo, lastShown, shownCount };
    BackgroundGlobals.cacheStore.set(LocalStorageVariables.DWS_INFO.name, dwsCache);

    cb(true);
  }

  [BackgroundCommands.LAUNCH_IDPS_LOGIN]({ cb })
  {
    if (!BackgroundGlobals.UA_CLIENT)
    {
      BackgroundGlobals.UA_CLIENT = new UnifiedAuthClient();
    }

    BackgroundGlobals.UA_CLIENT.launchLoginFlow();
    cb(true);
  }

  [BackgroundCommands.GET_APS_DETAILS]({ sender, cb })
  {
    const tabData = BackgroundGlobals.tabData.get(sender.tab.id);
    cb(tabData.apsDetails || null);
  }

  [BackgroundCommands.FORM_PRE_CHECK_PASSED]({ sender, cb })
  {
    const { id } = sender.tab;
    BackgroundGlobals.messageDispatcher.formPreCheckPassed(id);
    cb(true);
  }

  [BackgroundCommands.SIGN_UP_FORM_DETECTED]({ sender, cb })
  {
    const { url, id } = sender.tab;
    BackgroundGlobals.messageDispatcher.signUpFormDetected(url, id);

    cb(true);
  }

  [BackgroundCommands.UPDATE_BK_NATIVE_SETTINGS]({ request, cb })
  {
    const { name, value } = request;
    if (name in BackgroundGlobals.nativeSettings)
    {
      BackgroundGlobals.nativeSettings[name].value = value;
    }

    cb(true);
  }

  [BackgroundCommands.SHOW_SIDEBAR_MAIN]({ sender, cb })
  {
    const { url, id } = sender.tab;
    BackgroundGlobals.messageDispatcher.showSidebarMain(id, url);
    cb(true);
  }

  [BackgroundCommands.SET_FF_POLICY_COLLECTION]({ request, cb })
  {
    const { personal = null, activity = null } = request;
    if (personal !== null)
    {
      BackgroundGlobals.cacheStore.set(LocalStorageVariables.FF_POLICY_PERSONAL_COLLECTION.name, personal);
    }

    if (activity !== null)
    {
      BackgroundGlobals.cacheStore.set(LocalStorageVariables.FF_POLICY_ACTIVITY_COLLECTION.name, activity);
    }

    cb(true);
  }

  [BackgroundCommands.SET_FF_POLICY_LAST_SHOWN]({ cb })
  {
    const now = new Date().getTime();
    BackgroundGlobals.cacheStore.set(LocalStorageVariables.FF_POLICY_LAST_SHOWN.name, now);

    cb(true);
  }

  [BackgroundCommands.GET_FD_WEIGHTS]({ cb })
  {
    const weights = FormDetection.getWeights();
    cb(weights);
  }

  [BackgroundCommands.GET_FD_EXCEPTIONS]({ cb })
  {
    const exceptions = FormDetection.getExceptions();
    cb(exceptions);
  }

  [BackgroundCommands.GET_FD_CRF_PARAMS]({ cb })
  {
    const crfParams = FormDetection.getCrfParams();
    cb(crfParams);
  }
}
