/* eslint-disable */
/* eslint-disable class-methods-use-this */
/* eslint-disable no-restricted-syntax */
/* eslint-disable camelcase */

const NativeBrowserTypes = {
  Unknown: -1,
  Firefox: 1,
  Chrome: 2,
  Edge: 3
};

const NativeMessages = {
  Ping: 110, // Ping from another low priority extension
  Handshake: 9, //  Handshake message send to native if this extension is connecting to native
  HighPriorityAlive: 111, // Message from another high priority extension saying that it is alive.
  Disconnected: 106 //Message send when a low priority extension is removed or disabled
};

const logType = {
  Info: 0,
  Err: 1,
  Warn: 2,
  Debug: 3,
};

const ConnectionState = {
  CONNECTING: 'CONNECTING',
  CONNECTED: 'CONNECTED',
  DISCONNECTED: 'DISCONNECTED',
};

/* eslint-disable no-unused-vars */
export default class SmartNativeClient
{
  constructor(connectionString, onExternalEventCb = null, onNativeEventCb = null, onDisconnectEventCb = null,
    logger = null, handshakeSettings = {}, onConnectHandler = null)
  {
    // Public methods
    this.onExternalMessage = onExternalEventCb; // Callback to receive external messages from another extension.
    this.onNativeMessage = onNativeEventCb; // Callback to receive native messages from native process.
    this.onDisconnectEvent = onDisconnectEventCb; // Callback to send when disconnected from High priority extension or from native.
    this.nativeConnectionString = connectionString; // The native connection string
    this.onConnectHandler = onConnectHandler; // Handler to disable chrome extension if edge extension is installed.

    // Private methods
    this._connectedToExtension = false; // If this extension is connecting to extension rather than to native this is set to true
    this._extensionId = chrome.runtime.id;
    this._callbackMap = new Map();
    this._callbackId = 5; // Running number for the message generated by this extension
    this._nativeCallbackMap = new Map(); // Callback map for messages directly sent to native
    this._forwardExtensionId = null; // The id of extension to which the messages get forwarded
    this._port = null;
    this._logger = logger;
    this._onConnectExternal = this._onConnectExternal.bind(this);
    this._onMessageExternal = this._onMessageExternal.bind(this);
    this._onMessage = this._onMessage.bind(this);
    this._onDisconnect = this._onDisconnect.bind(this);
    this._onDisconnectHighPriorityExtension = this._onDisconnectHighPriorityExtension.bind(this);
    this._connectedExtensions = [];
    this._connectedToNative = false;
    this._handshakeSettings = handshakeSettings;
    this._nativeConnectionStatus = ConnectionState.DISCONNECTED;
    this._initExtensionList();
  }

  init(callback)
  {
    chrome.runtime.onConnectExternal.addListener(this._onConnectExternal);
    chrome.runtime.onMessageExternal.addListener(this._onMessageExternal);
    this._connect(0, callback);
  }

  // Checks whether the passed extension is a supported one.
  _isSupported(extensionId)
  {
    return this.extensions.includes(extensionId);
  }

  // Checks whether the passed extension id is of a supported extension.
  _isHigherPriority(extensionid)
  {
    for (let index = 0; index < this.extensions.length; ++index)
    {
      // The extension is in list higher than current extension.
      if (extensionid === this.extensions[index])
      {
        return true;
      }

      if (this._extensionId === this.extensions[index]) // We have hit current extension id so break
      {
        break;
      }
    }
    return false;
  }

  _addConnectedExtension(extensionid)
  {
    this._connectedExtensions.push(extensionid);
  }

  _removeConnectedExtension(extensionid)
  {
    const index = this._connectedExtensions.indexOf(extensionid);
    if (index !== -1)
    {
      this._connectedExtensions.splice(index, 1);
    }
  }

  //  If an unauthorized extension connects to us then disconnect immediately. External extensions connect
  //  to just track and identify when this extension is removed or disabled
  _onConnectExternal(port)
  {
    this._log(logType.Info, `External entity ${port.sender.id} connected`);
    if (!this._isSupported(port.sender.id))
    {
      this._log(logType.Info, `Unsupported external entity ${port.sender.id} is forcefully disconnected`);
      port.disconnect();
      return;
    }
    
    this.onConnectHandler && this.onConnectHandler(this._extensionId, port.sender.id);

    /// We donot do anything or connect back as this smartclient object is not expected to connect back.
    /// this was intended for another smartclient object in this extension.
    if (this.nativeConnectionString.toLowerCase() !== port.name.toLowerCase())
    {
      return;
    }

    // If a low priority extension connected to us, then connect back so that we are notified if that extension
    // is disabled.
    if (!this._isHigherPriority(port.sender.id))
    {
      this._addConnectedExtension(port.sender.id);
      const lport = chrome.runtime.connect(port.sender.id);
      if (lport !== null)
      {
        lport.onDisconnect.addListener(() =>
        {
          if (chrome.runtime.lastError)
          {
            this._log(`Error connecting to native host ${extension.runtime.lastError.message}`);
          }

          this._removeConnectedExtension(port.sender.id);
          // The low priority extension disconnected so now send message to native that lp extension is not active.
          this._sendDisconnect(port.sender.id);
        });
      }
    }
  }

  // Internal callback id which is auto incremented for each request send to native
  _incrementCallbackId()
  {
    const MAX_SIGNED_INT32 = 2147483647;

    if (this._callbackId < 0 || this._callbackId >= MAX_SIGNED_INT32)
    {
      this._callbackId = 5;
    }
    else
    {
      this._callbackId += 1;
    }
    return this._callbackId;
  }

  // Wait for messages from extension.
  _onMessageExternal(message, sender, sendResponse)
  {
    let bRet = false;

    try
    {
      if (!this._isSupported(sender.id))
      {
        this._log(logType.Info, `Message received from unsupported entity ${sender.id}. Message is ${JSON.stringify(message)}`);
        return;
      }

      if (message === null || !('request_type' in message))
      {
        this._log(logType.Err, `Message received does not have request_type field : ${JSON.stringify(message)}`);
        return;
      }

      //If this message is not intended to the native that this smartclient object is connected to, then ignore the message.
      if (message.nativeConnectionString && message.nativeConnectionString.toLowerCase() !== this.nativeConnectionString.toLowerCase()) 
      {
        return;
      }

      this._log(logType.Info, `Message received from external extension ${sender.id} ${JSON.stringify(message)}`);

      // Will forward only if this smart client is connected to same native as expected by low priority extension
      if (message.request_type === NativeMessages.Ping) //  This is initial ping, so send response.
      {
        sendResponse({ forwardingsupported: true, connectedToNative: this._connectedToNative });
        this._log(logType.Info, `Ping message from low priority extension ${sender.id}`);
        return;
      }
      
      // A high priority extension is live so disconnect and reconnect.
      if (message.request_type === NativeMessages.HighPriorityAlive)
      {
        this._nativeConnectionStatus = ConnectionState.DISCONNECTED;
        this._log(logType.Info, `Message from high priority extension ${sender.id} broadcasting its presence`);

        if (this._port !== null)
        {
          this._log(logType.Info, 'Disconnection the direct native connection');
          this._port.disconnect();
          this._port = null;
        }

        this._log(logType.Info, `Attempting reconnection to the high priority extension  ${sender.id} which broadcasted its presence`);
        this._connect(0, null);
      }

      let forwardMessage = true;

      if (this.onExternalMessage !== null) // If external message handler is available then
      {
        this._log(logType.Info, `Passing the message ${JSON.stringify(message)} from ${sender.id} to external message listener of this extension`);
        const response = this.onExternalMessage(message, sender, sendResponse);

        if (response !== null && response !== undefined)
        {
          forwardMessage = response.forwardMessage || true;

          // If message is not intended to be forwarded, check what should be the return from this function
          if (forwardMessage !== true)
          {
            bRet = response.ret;
          }
        }
      }

      // If connected to native then forward the message else skip it.
      if (forwardMessage && this._port !== null && this._connectedToNative)
      {
        // If the message had Id then it is expecting a callback so generate a unique callback id for this forwarded message
        // So store the callback id from another extension, generate this extensions callback id and then forward message.
        if ('id' in message)
        {
          const callbackId = this._incrementCallbackId();
          this._callbackMap.set(callbackId, { extensionId: sender.id, id: message.id, callback: sendResponse });
          message.id = callbackId;
          bRet = true;
        }

        this._log(logType.Info, `Message ${JSON.stringify(message)} is forwarded to native`);
        this._nativePostMessage(message);
      }
    }
    catch (exception)
    {
      this._logException(exception);
    }

    return bRet;
  }

  // This is message from native.
  _onMessage(message)
  {
    if (message === null || !('request_type' in message))
    {
      this._log(logType.Err, `Message received from native does not have request_type. Hence skipping the message. ${JSON.stringify(message)}`);
      return;
    }

    this._nativeConnectionStatus = ConnectionState.CONNECTED;
    if (message.id)
    {
      // If a native callback exists then just fire that and return. This is the callback registered for this particular message in this extension.
      const nCallback = this._nativeCallbackMap.get(message.id);
      if (nCallback !== null && nCallback !== undefined)
      {
        this._log(logType.Info, `Callback exists for message in this extension itself, hence firing that for message : ${JSON.stringify(message)}`);
        nCallback(message);
        this._nativeCallbackMap.delete(message.id);
        return;
      }
    }

    // The message has to be forwarded
    if ('id' in message) // A message to forward as a return for another message sent needs to have an id
    {
      const ret = this._callbackMap.get(message.id); // Get the cached message sender details.
      if (ret !== undefined)
      {
        this._log(logType.Info, `Forwarding message ${JSON.stringify(message)} to callback extension ${ret.extensionId}`);
        this._callbackMap.delete(message.id);
        // Replace with original callbackid
        message.id = ret.id;
        ret.callback(message); // send message to extension
        return;
      }
    }
    else if ('forwardid' in message) // Forward id is identifier of the destination extension to which to forward the message
    {
      chrome.runtime.sendMessage(message.forwardid, message, null, null);
      return;
    }
    
    if (this.onNativeMessage !== null)
    {
      this._log(logType.Info, `Calling native generic callback for message received from native ${JSON.stringify(message)}`);
      this.onNativeMessage(message);
    }
  }

  //Fired when disconnection happens. Disconnection can be from the high priority extension or
  //it can be from native if this extension had directly connected to native. But not both as
  //this extension could either be connecting to native or connecting to another extension.
  _onDisconnect(eventPort)
  {
    const tempConnectionStatus = this._nativeConnectionStatus;
    this._nativeConnectionStatus = ConnectionState.DISCONNECTED;

    if (chrome.runtime.lastError)
    {
      this._log(logType.Warn, `Connection to native/extension failed Error is: ${chrome.runtime.lastError.message}`);
    }

    this._log(logType.Debug, 'Disconnected from native. Trying to connect loop again');
    this._port = null;

    if (this.onDisconnectEvent !== null)
    {
      this.onDisconnectEvent(eventPort);
    }

    // This is the fallback and if this has failed there is nothing to do. Maybe native is not available.
    if (this._connectedToNative)
    {
      this._connectedToNative = false;
      return;
    }

    // If this extension tried to connect to native but failed,
    // we can ignore calling connect again, since native was not there
    // and no other extension had higher priority (otherwise we wouldn't have
    // tried to connect to native to begin with)
    if (tempConnectionStatus !== ConnectionState.CONNECTING)
    {
      this._connect(0, null);
    }
  }

  /// Initialize the list of supported extension. The highest priority is at 0th index and the lowest is the last item.
  _initExtensionList()
  {
    const browserType = this._toBrowserType();

    switch (browserType)
    {
      case NativeBrowserTypes.Chrome:
        this.extensions = [
          'fheoggkfdfchfphceeifdbepaooicaho', // WA
          'cpaibbcbodhimfnjnakiidgbpiehfgci', // TK
          'klekeajafkkpokaofllcadenjdckhinm', // WB
          'enppghjcblldgigemljohkgpcompnjgh', // SS
          'kanjcmmieblbpbihaafnedamppkhfadn', // NT
          'ciahhpibjeonlihjdefecmhminjpmfkk', // TK
          'nbeldjopgciegccabfohnefghfpinncn'  // TK
        ];
        break;
      case NativeBrowserTypes.Edge:
        this.extensions = [
          'fdhgeoginicibhagdmblfikbgbkahibd', // WA
          'gnnbmcifkkjgjdbkilfglpdpmidkgefn', // TK
          'lamehkegphbbfdailghaeeleoajilfho',  // WB
          'fheoggkfdfchfphceeifdbepaooicaho', // WA_CH
        ];
        break;
      case NativeBrowserTypes.Firefox:
        this.extensions = [
          '{4ED1F68A-5463-4931-9384-8FFF5ED91D92}', // WA
          '@true-key'
          
        ];
        break;
      default:
        break;
    }
  }

  /// Internal method to detect current browser type. This is used primarily to initiatize the extension list.
  _toBrowserType()
  {
    if (navigator.userAgent.indexOf('Edg') >= 0)
    {
      return NativeBrowserTypes.Edge;
    }
    if (navigator.userAgent.indexOf('Chrome') >= 0)
    {
      return NativeBrowserTypes.Chrome;
    }
    if (navigator.userAgent.indexOf('Gecko') >= 0)
    {
      return NativeBrowserTypes.Firefox;
    }
    return NativeBrowserTypes.Unknown;
  }

  /// Logging helper function
  _log(logtype, msg)
  {
    if (!this._logger)
    {
      return;
    }
     
    if (logtype === logType.Info)
    {
      this._logger.log(msg, 'SMART_NATIVE_CLIENT', '#004d7c');
    }
    else if (logType === logType.Err)
    {
      this._logger.error(msg, 'SMART_NATIVE_CLIENT');
    }
    else if (logType === logType.Warn)
    {
      this._logger.warn(msg, 'SMART_NATIVE_CLIENT', '#004d7c');
    }
    else if (logType === logType.Debug)
    {
      this._logger.debug(msg, 'SMART_NATIVE_CLIENT', '#004d7c');
    }
  }

  /// Logging helper function to log exceptions.
  _logException(exception)
  {
    let msg = exception.message;
    if (chrome.runtime.lastError && chrome.runtime.lastError.message)
    {
      msg += `LastError :${chrome.runtime.lastError.message}`;
    }
    this._log(logType.Err, msg);
  }

  _toDisconnectMessage(extensionid)
  {
    const discmessage = {};
    discmessage.request_type = NativeMessages.Disconnected;
    discmessage.payload = {};
    discmessage.payload.browser_type = this._toBrowserType();
    discmessage.payload.extn_id = extensionid;
    return discmessage;
  }

  /// Send disconnect message to native.
  _sendDisconnect(extensionId)
  {
    try
    {
      const disconnectmsg = this._toDisconnectMessage(extensionId);
      this._nativePostMessage(disconnectmsg);
      this._log(logType.Info, `Sent disconnect message directly to native ${JSON.stringify(handshake)}`);
    }
    catch (exception)
    {
      this._logException(exception);
    }
  }

  /// Generate the handshake message for current extension
  _toHandshakeMessage()
  {
    return {
      request_type: NativeMessages.Handshake,
      id: this._incrementCallbackId(),
      payload: {
        extn_id: this._extensionId,
        extn_version: chrome.runtime.getManifest().version,
        browser_type: this._toBrowserType(),
        user_agent: navigator.userAgent,
        ...this._handshakeSettings,
      }
    };
  }

  /// Send handshake message to native.
  _sendhandshake()
  {
    try
    {
      const handshake = this._toHandshakeMessage();
      this._nativeConnectionStatus = ConnectionState.CONNECTING;
      if (this._nativePostMessage(handshake))
      { 
        this._log(logType.Info, `Sent handshake message directly to native ${JSON.stringify(handshake)}`);
        return true;
      }
    }
    catch (exception)
    {
      this._logException(exception);
    }
    return false;
  }

  // We create a function that checks every 0.3s for up to 3s
  // to see if connection to Native was successful or not
  // DISCONNECTED - happens onDisconnect listener
  // CONNECTED - happens when a handshake response is returned from native
  _isNativeConnected()
  {
    return new Promise ((resolve) =>
    {
      const check = (tries) =>
      {
        if (tries >= 11 || this._nativeConnectionStatus === ConnectionState.DISCONNECTED)
        {
          resolve(false);
        }
        else if (this._nativeConnectionStatus === ConnectionState.CONNECTED)
        {
          resolve(true);
        }
        else
        {
          setTimeout(() =>
          {
            check(tries + 1);
          }, 300);
        }
      };

      check(0);
    });
  }

  // Attempts to connect to the extension in the list at the index passed. If such a connection could  not be
  // established it will attempt to connect to the next priority extension till it reaches this extensions id.
  // Upon that, it is assumed that
  async _connect(index, callback = null)
  {
    if (index >= this.extensions.length)
    {
      callback && callback({ connected: false });
      return;
    }

    // If attempts to connect to all high priority extensions failed connect to native directly
    if (this.extensions[index] === this._extensionId)
    {
      // We try to connect to native, only if we dont have a higher priority extension to connect to
      // And only if this it is not connected (DISCONNECTED)
      if (this._nativeConnectionStatus === ConnectionState.DISCONNECTED)
      {
        this._log(logType.Info, 'No other high priority extension available, hence connecting directly to native');
        this._port = chrome.runtime.connectNative(this.nativeConnectionString);
        this._connectedToExtension = false;
        this._port.onMessage.addListener(this._onMessage);
        this._port.onDisconnect.addListener(this._onDisconnect);
        this._forwardExtensionId = null;
        this._sendhandshake();
      }

      const nativeConnected = await this._isNativeConnected();
      if (nativeConnected)
      {
        this._connectedToNative = true;
        // Notify all lower priority extensions that I am there and I have connected to native so that they
        // can disconnect and route through me now. Do this only if handshake was successfully sent.

        const hpAliveMsg = {};
        hpAliveMsg.request_type = NativeMessages.HighPriorityAlive;
        hpAliveMsg.payload = {};

        for (let ilower = index + 1; ilower < this.extensions.length; ++ilower)
        {
          chrome.runtime.sendMessage(this.extensions[ilower], hpAliveMsg, () =>
          {
            if (chrome.runtime.lastError)
            {
              this._log(logType.Warn, `Failed to sendMessage to extension id: ${this.extensions[ilower]}, got err: ${chrome.runtime.lastError.message}`);
            }
          });
        }
        callback && callback({ connected: true });
        return;
      }
      // Failed to connect to native
      callback && callback({ connected: false });
      return;
    }

    const pingMsg = {};
    pingMsg.request_type = NativeMessages.Ping;
    pingMsg.nativeConnectionString = this.nativeConnectionString;
    pingMsg.payload = {};

    chrome.runtime.sendMessage(this.extensions[index], pingMsg, (response) =>
    {
      if (chrome.runtime.lastError)
      {
        this._log(logType.Warn, `Message failed to send: ${chrome.runtime.lastError.message}`);
        this._connect(index + 1, callback);
        return;
      }

      // Extension did not respond. Now just try next priority extension.
      if (response === null || response === undefined) 
      {
        this._log(logType.Info, `High priority extension ${this.extensions[index]} did not respond with appropriate response. Hence trying the next high priority available extension`);
        this._connect(index + 1, callback);
        return;
      }

      // We received a response from high priority extension.
      if (!response.forwardingsupported)
      {
        // Forwarding not supported by extension, attempt next priority extension
        this._log(logType.Info, `High priority extension ${this.extensions[index]} responded but it does not support forwarding. Hence trying next priority extension`);
        this._connect(index + 1, callback);
        return;
      }

      this._log(logType.Info, `High priority extension ${this.extensions[index]} responded and supports forwarding.`);

      if (response.connectedToNative)
      {
        this._log(logType.Info, `High priority extension ${this.extensions[index]} is connected to native, hence we are forwarding to it`);
        this._port = chrome.runtime.connect(this.extensions[index], { name: this.nativeConnectionString });
        this._connectedToExtension = true;
        this._port.onMessage.addListener(this._onMessage);
        this._port.onDisconnect.addListener(this._onDisconnectHighPriorityExtension);
        this._forwardExtensionId = this.extensions[index];

        const handshake = this._toHandshakeMessage();
        this._postMessageInternal(handshake);

        callback && callback({ connected: true });
      }
      else
      {
        this._log(logType.Info, `High priority extension ${this.extensions[index]} supports forwarding, but is not connected to native`);
        callback && callback({ connected: false });
      }
    });
  }

  _onDisconnectHighPriorityExtension()
  {
    this._log(logType.Info, 'Disconnected from high priority extension. Hence trying to connect to next priority extension');
    this._forwardExtensionId = null;
    this._nativeConnectionStatus = ConnectionState.DISCONNECTED;
    this._connect(0, null);
  }

  _nativePostMessage(message)
  {
    try
    {
      this._port.postMessage(message);
      return true;
    }
    catch (exception)
    {
      if (chrome.runtime.lastError)
      {
        this._log(logType.Err, `Post message failed with last Error ${chrome.runtime.lastError.message}`);
      }

      this._logException(exception);
      return false;
    }
  }

  _postMessageInternal(message, callback = null)
  {
    message.id = this._incrementCallbackId();

    if (this._forwardExtensionId !== null)
    {
      message.nativeConnectionString = this.nativeConnectionString;
      chrome.runtime.sendMessage(this._forwardExtensionId, message, (response) =>
      {
        if (chrome.runtime.lastError)
        {
          return;
        }

        if (callback !== null)
        {
          callback(response);
        }
      });
    }
    else // This is connecting to native directly
    {
      if (this._port === null) return;

      // If callback is specified the caller is expecting a response so append message id
      if (callback !== null)
      {
        this._nativeCallbackMap.set(message.id, callback); // Callback function mapped to callback id to fire response
      }

      this._nativePostMessage(message);
    }
  }

  getConnectedExtensions()
  {
    return this._connectedExtensions;
  }

  isConnected()
  {
    return this._connectedToExtension || this._connectedToNative;
  }

  isConnectedToExtensionIndex(index)
  {
    return this._forwardExtensionId === this.extensions[index];
  }

  postMessage(message, callback = null)
  {
    try
    {
      // If request type is null or is reserved then return false
      if (
        message === null ||
        message.request_type === null ||
        message.request_type === NativeMessages.Handshake ||
        message.request_type === NativeMessages.HighPriorityAlive ||
        message.request_type === NativeMessages.Ping
      )
      {
        return false;
      }

      // If the message has to be forwarded to another extension then do so
      this._postMessageInternal(message, callback);
      return true;
    }
    catch (exception)
    {
      this._logException(exception);
      return false;
    }
  }
}
