// Background
import { BackgroundGlobals } from '@extension/background/background_globals';
// WASM loading script
import init, { initiate,
  wasm_gti_hmac_sha1,
  wasm_typosquat_sha256,
  wasm_typosquat_decrypt,
  generate_value_for_ac,
  get_product_key,
  get_wa_app_key,
  get_wa_shared_key,
  uauth_aes_encrypt } from '@webassembly/wasm-lib/pkg/wasm_lib';

/**
 * Generating WASM module in run-time 
 */
export default class WasmGenerator
{
  static async generateWASM()
  {
    if (BackgroundGlobals.wasm) 
    {
      return BackgroundGlobals.wasm;
    }

    // generate a new wasm
    const wasmBinary = chrome.runtime.getURL('wasm_lib.wasm');

    const wasm = {};
    wasm.binary = await init(wasmBinary);
    wasm.init = initiate;
    wasm.wasm_gti_hmac_sha1 = wasm_gti_hmac_sha1;
    wasm.wasm_typosquat_sha256 = wasm_typosquat_sha256;
    wasm.wasm_typosquat_decrypt = wasm_typosquat_decrypt;
    wasm.generate_value_for_ac = generate_value_for_ac;
    wasm.get_product_key = get_product_key;
    wasm.get_wa_app_key = get_wa_app_key;
    wasm.get_wa_shared_key = get_wa_shared_key;
    wasm.uauth_aes_encrypt = uauth_aes_encrypt;

    return wasm;
  }
}