// Background
import { ContentCommands } from '@extension/common/commands';
import InjectBase from '@extension/background/content_injector/inject_base';
import Utils from '@extension/utilities/utils';
import { BackgroundGlobals } from '@extension/background/background_globals';
import SearchEngine from '@core-engine/search_engine';
import { LocalStorageVariables } from '@extension/common/cache_store_variables';

export default class InjectAPSToast extends InjectBase
{
  constructor({ url })
  {
    super('content_aps_toast.js');
    this.pingCommand = ContentCommands.PING_CONTENT_APS_TOAST;
    this.url = url;
  }

  check()
  {
    const apsToggleSetting = BackgroundGlobals.cacheStore.get(LocalStorageVariables.APS_TOGGLE_SETTING.name, true);
    if (!apsToggleSetting)
    {
      return false;
    }

    const { APS_COHORT } = BackgroundGlobals.nativeSettings;
    const isSupported = Utils.isChromeBrowser();
    if (APS_COHORT.value === 0 || !isSupported)
    {
      return false;
    }

    const searchEngine = new SearchEngine(BackgroundGlobals.searchEngines, null, null, BackgroundGlobals.nativeSettings.SECURE_SEARCH_REGEXES);
    const engine = searchEngine.getEngineForUrl(this.url);
    const isSSEngine = engine && engine.issecuresearch;
    if (!isSSEngine)
    {
      return false;
    }

    // Check if this is the first time showing the APS toast
    const apsToastFirstTime = BackgroundGlobals.cacheStore.get(LocalStorageVariables.APS_TOAST_FIRST_TIME.name, true);
    if (!apsToastFirstTime)
    {
      // Return as the toast has been shown previously
      return false;
    }

    return true;
  }

  runAfter(success)
  {
    if (!success)
    {
      return;
    }

    // Update Global Native settings SS_DEF set to 1
    BackgroundGlobals.nativeSettings.SS_DEF.value = 1;

    // Update first time variable
    BackgroundGlobals.cacheStore.set(LocalStorageVariables.APS_TOAST_FIRST_TIME.name, false);
  }
}
