// Background
import { BackgroundGlobals } from '@extension/background/background_globals';
// Common
import UrlParser from '@extension/common/url_parser';
import { Logger } from '@extension/common/logger';

/**
 * TODO: Comment
 */
export class WhitelistWrapper
{
  /**
   * Checks whether the specified domain is already in the whitelist
   * @param  {string} domain - check if this domain exists in the whitelist db
   * @param  {string} storeVariable - the whitelist db name
   * @returns {bool}  whether a site is in the whitelist
   */
  static isDomainInWhitelist(domain, storeVariable)
  {
    const cleanDomain = UrlParser.getCleanURI(domain);
    if (!cleanDomain)
    {
      Logger.error('Trying to put an empty URL into whitelist!');
      return false;
    }

    const whitelist = BackgroundGlobals.cacheStore.get(storeVariable, []);
    if (!Array.isArray(whitelist))
    {
      Logger.error('Can not fetch whitelist properly!');
      return false;
    }

    for (let index = 0; index < whitelist.length; index += 1)
    {
      const inputDomain = cleanDomain.toLowerCase();
      const storageDomain = whitelist[index].toLowerCase();
      if (inputDomain.includes(storageDomain) || storageDomain.includes(inputDomain)) return true;
    }

    return false;
  }

  /**
   * Adds the domain to the whitelist
   * @param  {array} domains - array of domains to be added to the whitelist
   * @param  {string} storeVariable - the whitelist db name
   */
  static addDomainsToWhitelist(domains, storeVariable)
  {
    if (!Array.isArray(domains) || domains.length === 0)
    {
      return;
    }

    const domainsToBeAdded = [];
    domains.forEach((domain) =>
    {
      const cleanDomain = UrlParser.getCleanURI(domain);
      if (cleanDomain && !this.isDomainInWhitelist(cleanDomain, storeVariable))
      {
        domainsToBeAdded.push(cleanDomain);
      }
    });

    if (domainsToBeAdded.length > 0)
    {
      const whitelist = BackgroundGlobals.cacheStore.get(storeVariable, []);
      if (!Array.isArray(whitelist)) return;

      const updatedList = whitelist.concat(domainsToBeAdded);
      BackgroundGlobals.cacheStore.set(storeVariable, updatedList);
    }
  }

  /**
   * Remove domains from the whitelist
   * @param  {array} domains - domain of the site to be removed from the whitelist
   * @param  {string} storeVariable - the whitelist db name
   */
  static removeDomainFromWhitelist(domains, storeVariable)
  {
    if (!Array.isArray(domains) || domains.length === 0)
    {
      return;
    }

    const domainsToBeRemoved = [];
    domains.forEach((domain) =>
    {
      const cleanDomain = UrlParser.getCleanURI(domain);
      if (this.isDomainInWhitelist(cleanDomain, storeVariable))
      {
        domainsToBeRemoved.push(cleanDomain.toLowerCase());
      }
    });

    if (domainsToBeRemoved.length > 0)
    {
      const whitelist = BackgroundGlobals.cacheStore.get(storeVariable, []);
      if (!Array.isArray(whitelist)) return;

      const updatedList = whitelist.filter((whitelistedDomain) => !domainsToBeRemoved.includes(whitelistedDomain.toLowerCase()));
      BackgroundGlobals.cacheStore.set(storeVariable, updatedList);
    }
  }

  /**
   * Get array of whitelisted items
   * @param  {string} storeVariable - the whitelist db name
   * @returns {array}  array of whitelisted items, if any
   */
  static getDomainsFromWhitelist(storeVariable)
  {
    return BackgroundGlobals.cacheStore.get(storeVariable, []);
  }

  /**
   * clear the entire whitelist db
   * @param  {string} storeVariable - the whitelist db name
   */
  static clearWhitelist(storeVariable)
  {
    BackgroundGlobals.cacheStore.clear(storeVariable);
  }
}

export default WhitelistWrapper;
