import { TelemetryGlobals } from './telemetry_globals';
import { generateGUIDFromHash, processMD5Hash } from './utils';
import DataCollector from './data_collector';
import DictionaryMapper from './dictionary_mapper';
import DataSetManager from './dataset_manager';
import EventManager from './event_manager';
import EventHandler from './event_handler';
import Logger from './logger';
// JSON data files
import data_items from './Data/data_items.json';
import dictionary from './Data/dictionary.json';
import events from './Data/events.json';
import profiles from './Data/profile.json';
import datasets_catalog from './Data/datasets_catalog.json';
import RulesManager from './rules_manager';

export default class engine
{
  constructor(jsonLocation, context = {}, externalLogger = null)
  {
    this.initCalled = true;
    this.userID = null;
    this.geoInfo = null;

    // TODO: properly set these
    if (typeof jsonLocation === 'object')
    {
      // use it directly
    }
    else if (typeof jsonLocation === 'string')
    {
      // fetch json
    }

    // Start data collectors and other start up functions
    if (externalLogger) TelemetryGlobals.externalLogger = externalLogger;
    TelemetryGlobals.dataCollector = new DataCollector(data_items);
    TelemetryGlobals.dictionaryMapper = new DictionaryMapper(dictionary);
    TelemetryGlobals.eventManager = new EventManager(events);
    TelemetryGlobals.datasetManager = new DataSetManager(datasets_catalog);
    TelemetryGlobals.eventHandler = new EventHandler();
    TelemetryGlobals.rulesManager = new RulesManager();
    TelemetryGlobals.profiles = profiles;
    TelemetryGlobals.engine = this;

    // Update initial context
    for (const contextKey of Object.keys(context))
    {
      this.updateContext(contextKey, context[contextKey]);
    }
  }

  setUrl(url)
  {
    TelemetryGlobals.url = url;
  }

  sendEvent(unique_id, payload)
  {
    if (!this.initCalled) return;
    TelemetryGlobals.eventHandler.handleEvent({ unique_id, payload });
  }

  updateContext(item, value)
  {
    TelemetryGlobals.storage.set(item, value);
    TelemetryGlobals.dataCollector.markDataExpired(item);
  }

  getContextId()
  {
    // step1: check if we already loaded the value
    if (this.userID && this.userID !== '[unknown]')
    {
      return this.userID;
    }

    // step2: if not loaded, check if stored in registry
    this.userID = TelemetryGlobals.storage.get('Analytics.ContextId');
    if (this.userID) return this.userID;

    // Reaching this line means that we have not created the user id
    // step3: create user id by hashing mircrosoft machine guid

    // The Microsoft Machine Guid must have been collected first
    const microsoftMachineGuid = TelemetryGlobals.storage.get('Device.Microsoft.Machine.Guid');
    let isUsingMicrosoftMachineGuid = false;

    if (microsoftMachineGuid && microsoftMachineGuid !== '[ruleMismatch]')
    {
      try
      {
        const hash = processMD5Hash(microsoftMachineGuid);
        this.userID = generateGUIDFromHash(hash);
        isUsingMicrosoftMachineGuid = true;
        Logger.log(`microsoftMachineGuid was hashed to create uid: ${this.userID}`);
      }
      catch (e)
      {
        Logger.error(`An error was caught when hashing microsoftMachineGuid: ${e.message}`);
      }
    }
    else
    {
      Logger.warn('Machine Guid is missing');
    }

    // step4: send analytics event
    // Only send the event if value was stored fine
    const eventLabel = isUsingMicrosoftMachineGuid ? 'Hashed microsoft machine guid' : 'Created a new GUID';

    // Send event
    Logger.log('Sending analytics_new_user_created event');

    TelemetryGlobals.eventHandler.handleEvent({
      UniqueIdentifier: 'analytics_new_user_created',
      type: 'event',
      payload: {
        'Event.Action': 'ClientId.Created',
        'Event.Category': 'Analytics.Content',
        'Event.Label': eventLabel,
        'Sub.Category': 'LifeCycle',
        'Tracker.Type': 'event',
      }
    });

    return this.userID;
  }

  async getDefaultDictionary()
  {
    // Not using geo info for now
    // const geoInfo = this.geoInfo || await this.queryGeoInfo(); // TODO: refetch geo info when it expires
    // const { geoid, isQA } = geoInfo;

    return {
      'Application.Name': 'Analytics.Content',
      'Hit.UserID': this.userID,
      'Analytics.Content.Version': '1.0.1',
      'Analytics.bootstrapFileVersion': null,
      'device.geo.id': null,
      'device.env.qa': null,
      'analytics.sdk.version': null,
      product_analytics_dataitems_intest: null,
    };
  }

  async queryGeoInfo()
  {
    try
    {
      const funcUrl = 'https://prd-mcafee-mosaic-pub.azurewebsites.net/api/geoiplookup?code=rXirGSARC5aMI8vaT2FIgrd9xrI/y2rtWqKNdjmHccR3n8oZEmdkMA==';
      const res = await fetch(funcUrl, {
        headers: {
          method: 'GET',
        }
      });
      const geoId = res.headers.get('x-geoid');
      const isQA = res.headers.get('x-qae');

      const geoInfo = {
        queryDate: (new Date()).toISOString(),
        geoid: geoId ? geoId.replace(/_/g, '') : '',
        isQA: isQA ? isQA.replace(/_/g, '') : '',
      };

      // To be used next time
      this.geoInfo = geoInfo;

      return geoInfo;
    }
    catch (e)
    {
      Logger.error(`queryGeoInfoUWP:  ${e.message}`);
      return {};
    }
  }
}
