// Background
import { BackgroundGlobals } from '@extension/background/background_globals';
import WhitelistWrapper from '@extension/background/whitelist_wrapper';
import FeatureStats, { featureStatsTypes } from '@extension/background/feature_stats';
// Common
import { extension } from '@extension/common/webextension';
import { LocalStorageVariables } from '@extension/common/cache_store_variables';
import UrlParser from '@extension/common/url_parser';
import { WPSBlockPageType } from '@extension/common/constants';
import BlockPageCommon from '@extension/background/block_page_common';
import { Logger } from '@extension/common/logger';
// Injector
import Injector from '@extension/background/content_injector/injector';
import InjectCryptoBlock from '@extension/background/content_injector/inject_crypto_block';
import BackgroundCommon from '@extension/common/background_common';

/**
 * Responsible for all crypto jacking related features, listening on web requests, whitelisting and blocking malicious urls.
 */
export default class CryptojackingListener
{
  /**
   * Handler invoked before a web request is made based
   * @param {Object} details       - Details about the request being made
   * @param {Number} details.tabId - ID of the tab that has the request take place
   * @param {String} details.url   - Target url of the request
   * @returns {Object} - A "webRequest.BlockingResponse" object
   */
  onBeforeRequest({ tabId, url })
  {
    if (!BackgroundCommon.isPersonalPolicyEnabled())
    {
      return;
    }

    if (BackgroundGlobals.nativeSettings.CRYPTOJACKING_DISABLED.value === true)
    {
      return;
    }

    if (tabId === -1)
    {
      Logger.log(`Crypto mining check not done : ${url} ${tabId}`);
      return;
    }

    extension.tabs.get(tabId, (tData) =>
    {
      const tabURL = tData.url;
      const domainURL = UrlParser.getDomain(tabURL);
      const isWhiteListed = WhitelistWrapper.isDomainInWhitelist(domainURL, LocalStorageVariables.CRYPTO_WHITELIST.name);

      if (isWhiteListed)
      {
        BlockPageCommon.sendBlockPageEvent(WPSBlockPageType.Crypto, tabURL, url, true);
        return;
      }

      FeatureStats.add(featureStatsTypes.CRYPTO_BLOCKED);
      this.notifyCryptoBlock(tabId, domainURL, url);
      BackgroundCommon.blockRequest(tabId, url, 'Blocked crypto mining script/connection:');
      BlockPageCommon.sendBlockPageEvent(WPSBlockPageType.Crypto, tabURL, url, false);
    });
  }

  init()
  {
    const types = ['websocket', 'script', 'xmlhttprequest'];

    // Assign handler for before creating web request based on a filter and some pre-conditions
    extension.webRequest.onBeforeRequest.addListener(
      this.onBeforeRequest.bind(this),
      { urls: BackgroundGlobals.cryptoBlacklist, types }
    );
  }

  uninit()
  {
    extension.webRequest.onBeforeRequest.removeListener(this.onBeforeRequest.bind(this));
  }

  /**
   * Notify content script and native that cryptojacking has been detected
   * @param  {Number} tabId - ID of the tab to be reload after adding to whitelist
   * @param  {String} domain - Domain of the cryptojacking site
   * @param  {String} cryptoMinerScript - Full url of the cryptojacking site
   */
  notifyCryptoBlock(tabId, domain, cryptoMinerScript)
  {
    // Notify content script
    const miner = cryptoMinerScript.split('').reverse().join('');
    const cryptoBlockData = { domain, miner };

    BackgroundGlobals.tabData.update(tabId, { cryptoBlockData });
    const contentToInject = [new InjectCryptoBlock()];
    Injector.inject(tabId, contentToInject);
  }

  /**
   * Add Cryptojacking site to the whitelist
   * @param  {string} url - url of the site to be whitelisted
   * @param  {integer} tabId - id of the tab to be reload after adding to whitelist
   */
  addToWhitelist(url, tabId)
  {
    const domainURL = UrlParser.getDomain(url);

    if (!domainURL)
    {
      return;
    }

    WhitelistWrapper.addDomainsToWhitelist([domainURL], LocalStorageVariables.CRYPTO_WHITELIST.name);

    // Reload tab
    extension.tabs.reload(tabId, { bypassCache: true });
  }
}
