import Logger from './logger';
import OperationsManager from './operations_manager';
import { TelemetryGlobals } from './telemetry_globals';
import { arrayIncludes, getCommonElements, removeItemFromArray } from './utils';

export default class DataCollector
{
  /**
   * Starts the data_collector:
   * - Loads JSON file from data_items.json
   * - Marks all items as expired
   * - Creates farmers and refreshers objects
   * @param {Object} data_items - JSON data
   */
  constructor(data_items)
  {
    this.farmers = null; // Farmers are responsible of harvesting data points. Gets defined at run time, since it requires the data_collector object
    this.refreshers = {}; // Refreshers can set a timeout to recollect the datapoint or reapply an opration. Gets defined at run time, since it requires the data_collector object
    this.harvestQueue = []; // List of items being harvested. Used to eliminate circular dependencies
    this.expiredItems = []; // A list of expired items and need to be collected again
    this.expiryQueue = []; // List of items being marked expired. Used to eliminate circular dependencies
    this.operationExpiredItems = []; // A list of expired items and need to be collected again
    this.rawDataStore = {}; // A collection of items harvested, but not operated on yet (raw data)
    this.cache = {}; // Contains loaded values (post operations)
    this.definitions = data_items; // Definitions of how to collect each datapoint
    this.subscribedMsgs = {}; // Has a list of msg bus subscribed msgs and the corresponding datapoints

    try
    {
      Logger.debug('data_collector: Setup Started.');

      this.farmers = this.createFarmers();
      this.refreshers = this.createRefreshers();

      if (!this.farmers || !this.refreshers || !this.definitions)
      {
        Logger.error(`data_collector: Setup failed: farmers(${this.farmers}). refreshers(${this.refreshers}). definitions(${this.definitions})`);
        return;
      }

      // Mark all items as expired
      const keys = [...Object.keys(this.definitions)];
      this.markDataExpired(keys);

      Logger.debug('data_collector: Setup Done.');
    }
    catch (e)
    {
      Logger.error(`data_collector: Setup failed: ${e.message}`);
    }
  }

  /**
   * Retrieves data points from different sources (full or partial data collection on harvested data maybe applied)
   * Reference: https://confluence-lvs.prod.mcafee.com/display/CA/Data_Collector
   * @param {String[]} itemsList - Array of keys which denote the items being requested for
   * @returns - JSON object with the key-value pairs { "key1":"value1", "key2":"value2" ... }
   */
  get(itemsList)
  {
    try
    {
      let buff = null;

      if (typeof itemsList === 'string')
      {
        buff = itemsList;
        itemsList = [itemsList];
      }

      if (!(itemsList instanceof Array))
      {
        Logger.warn(`data_collector: get: itemsList not an array (${itemsList}).`);
        return {};
      }

      // Load expired data items
      const expiredDataPoints = getCommonElements(this.expiredItems, itemsList);
      this.updateCache(expiredDataPoints, false);

      // Perfom operations on operation expired data items
      const operationExpiredDatapoints = getCommonElements(this.operationExpiredItems, itemsList);
      this.updateCache(operationExpiredDatapoints, true);

      const collection = this.createCollection(itemsList);
      return buff ? collection[buff] : collection;
    }
    catch (e)
    {
      Logger.error(`data_collector: get: ${e.message}`);
      return {};
    }
  }

  /* Marks data points as expired
      *
      * PARAMS
      *  itemsList - an array of item names or a single item name to be marked as expired
      *
      */
  markDataExpired(itemsList)
  {
    try
    {
      Logger.debug(`data_collector: Marking items as expired: ${JSON.stringify(itemsList)}`);

      if (typeof itemsList === 'string')
      {
        itemsList = [itemsList];
      }

      if (!(itemsList instanceof Array))
      {
        Logger.warn(`data_collector: markDataExpired: itemsList not an array (${itemsList}).`);
        return;
      }

      // Check for circular dependency
      for (const item of itemsList)
      {
        if (arrayIncludes(this.expiryQueue, item))
        {
          Logger.warn(`data_collector: Circular dependency detected for ${itemsList[idx]}`);
          return;
        }
      }

      // Load expired data items
      for (const item of itemsList)
      {
        if (!arrayIncludes(this.expiredItems, item))
        {
          this.expiryQueue.push(item);
          this.expiredItems.push(item);
          removeItemFromArray(this.expiryQueue, item);
        }
      }
    }
    catch (e)
    {
      Logger.error(`data_collector: markDataExpired: ${e.message}`);
    }
  }

  /* Marks data points as expired if equal to a value
      *
      * PARAMS
      *  itemName - single item name to be retrieved
      *  value   - value to compare
      *
      */
  markDataExpiredIfEq(itemName, value) // single itmes
  {
    if (this.cache[itemName] === value)
    {
      this.markDataExpired(itemName);
    }
  }

  /* Marks data points operations as expired
      *
      * PARAMS
      *  itemsList - an array of item names or a single item name to be retrieved
      *
      */
  markDataOperationExpired(itemsList)
  {
    try
    {
      Logger.debug(`data_collector: Marking items as operation expired: ${JSON.stringify(itemsList)}`);

      if (typeof itemsList === 'string')
      {
        itemsList = [itemsList];
      }

      if (!(itemsList instanceof Array))
      {
        Logger.warn(`data_collector: markDataOperationExpired: itemsList not an array (${itemsList}).`);
        return;
      }

      // Load expired data items
      for (const idx of itemsList)
      {
        const key = itemsList[idx];
        if (!arrayIncludes(this.operationExpiredItems, key))
        {
          this.operationExpiredItems.push(key);
        }
      }
    }
    catch (e)
    {
      Logger.error(`data_collector: markDataOperationExpired: ${e.message}`);
    }
  }

  /**
   * Check if items are expired
   * @param {String[]} itemsList - An array of item names or a single item name to be checked
   * @returns {Boolean} - True if one or more elements are expired, false otherwise
   */
  isExpired(itemsList)
  {
    try
    {
      if (typeof itemsList === 'string')
      {
        itemsList = [itemsList];
      }

      if (!(itemsList instanceof Array))
      {
        Logger.error(`data_collector: isExpired: itemsList not an array (${itemsList}).`);
        return false;
      }

      // Load expired data items
      const expiredDataPoints = getCommonElements(this.expiredItems, itemsList);
      return expiredDataPoints.length !== 0;
    }
    catch (e)
    {
      Logger.error(`data_collector: isExpired: ${e.message}`);
      return false;
    }
  }

  /**
   * Creates a collection of key-value parameters by retrieving data points from the data_collector
   * @param {String[]} itemsList - An array of item names
   */
  createCollection(itemsList)
  {
    try
    {
      const collection = {};
      for (const idx in itemsList)
      {
        const itemName = itemsList[idx];
        collection[itemName] = (this.cache[itemName] !== undefined) ? this.cache[itemName] : '[unknown]';
      }

      return collection;
    }
    catch (e)
    {
      Logger.error(`createCollection: ${e.message}`);
      return {};
    }
  }

  /**
   * Collects the data points and processes them
   * @param {String[]} itemsList - An array of items
   * @param {Boolean} operationOnly - If true, the last harvest value is used instead of reharvesting
   */
  updateCache(itemsList, operationOnly)
  {
    if (!(itemsList instanceof Array))
    {
      Logger.warn(`data_collecter: loadItem: No item definition found for (${itemsList})`);
      return;
    }

    if (itemsList.length === 0) return;

    for (const item of itemsList)
    {
      try
      {
        this.loadSingleItem(item, operationOnly);
      }
      catch (e)
      {
        Logger.error(`data_collector: updateCache: Issue with loading singleItem for (${item})`);
      }
    }
  }

  /* Collects a single data point and processes it
  *       Items are stored in the data_collector
  *
  * PARAMS:
  *   itemName: target data_item name
  *   operationOnly: If true, the last harvest value is used instead of reharvesting
  */
  loadSingleItem(itemName, operationOnly)
  {
    const itemDefinition = this.definitions[itemName];

    if (!itemDefinition)
    {
      Logger.error(`data_collecter: loadItem: No item definition found for (${itemName})`);
      this.cache[itemName] = '[unknown]';
      return;
    }

    if (!operationOnly)
    {
      this.harvest(itemName, itemDefinition);
    }

    // Apply operation
    const postOperationValue = this.processOperation(itemName, itemDefinition.operationConfig);

    // Apply rule
    const postRuleValue = TelemetryGlobals.rulesManager.apply(postOperationValue, itemDefinition.rule);

    // Store
    this.cache[itemName] = postRuleValue;

    // Process Refresh
    this.setRefreshers(itemName, itemDefinition.refresh);
  }

  /* Reads a value based on it's definition
  *   Will call the farmers to do the harvesting
  *   No operations are done in this stage
  *   Detects circular dependency for items already being collected
  *
  * PARAMS:
  *   itemName: target data_item name
  *   itemDefinition: item definition retrieved from data_items.json
  */
  harvest(itemName, itemDefinition)
  {
    try
    {
      Logger.debug(`data_collector: harvest: Harvesting(${itemName})`);

      // Since we can collect data from the current datastore, we need to ensure no circular dependency
      // If the itemName is already being collected, we have a circular dependency
      if (arrayIncludes(this.harvestQueue, itemName))
      {
        this.rawDataStore[itemName] = null;
        removeItemFromArray(this.expiredItems, itemName); // Don't mark it expired
        Logger.warn(`data_collector: harvest: Circular dependency detected for (${itemName})`);
        return;
      }

      if (this.farmers[itemDefinition.source] === null)
      {
        Logger.warn(`data_collector: harvest: Unknown harvesting function (${itemDefinition.source})`);
        this.rawDataStore[itemName] = null;
        return;
      }

      // Add item to harvestQueue (Which contains list of items being collected)
      this.harvestQueue.push(itemName);

      const harvestValue = this.farmers[itemDefinition.source](itemDefinition.params);
      this.rawDataStore[itemName] = harvestValue;

      removeItemFromArray(this.expiredItems, itemName); // Item is no longer expired
      removeItemFromArray(this.harvestQueue, itemName); // Item is no longer being harvested
    }
    catch (e)
    {
      Logger.error(`data_collector: harvest: ${e.message}`);
    }
  }

  /* Apply operation on a value based on it's definition
  *   Will call the operations singleton to apply the operation
  *
  * PARAMS:
  *   itemName: target data_item name
  *   operationConfig: item operationConfig definition retrieved from data_items.json - Can be null
  */
  processOperation(itemName, operationConfig)
  {
    const harvestValue = this.rawDataStore[itemName];

    const postOperationValue = OperationsManager.apply(harvestValue, operationConfig);
    removeItemFromArray(this.operationExpiredItems, itemName);

    return postOperationValue;
  }

  /* Apply the refresh function for the target datapoint
  *   (Harvest again if value doesn't exist yet. Reload after time out. Apply operation every time)
  *
  * PARAMS:
  *   itemName: target data_item name
  *   refreshConfig: item refreshConfig definition retrieved from data_items.json - Can be null
  */
  setRefreshers(itemName, refreshConfig)
  {
    if (!refreshConfig)
    {
      // Item doesn't have any refresh config. Normal..
      return;
    }

    for (const action of refreshConfig)
    {
      try
      {
        // Apply refresh action
        this.refreshers[action](itemName, refreshConfig[action]);
      }
      catch (e)
      {
        Logger.error(`data_collector: setRefreshers: ${e.message}`);
      }
    }
  }

  retrieveFromDataStore(params)
  {
    try
    {
      Logger.debug(`data_collector: Requesting item from data_collector: ${JSON.stringify(params)}`);

      if (typeof params === 'string')
      {
        return this.get(params);
      }

      const result = [];
      const dataIems = this.get(params);
      if (!dataIems)
      {
        return [];
      }

      for (const idx of params)
      {
        const itemName = params[idx];
        result.push(dataIems[itemName]);
      }

      return result;
    }
    catch (e)
    {
      Logger.error(`data_collector: retrieveFromDataStore: ${e.message}`);
    }
  }

  createRefreshers(data_collector_engine)
  {
    const refreshers = {
      harvestIfEq(itemName, value)
      {
        if (data_collector_engine.cache[itemName] === value)
        {
          data_collector_engine.markDataExpired(itemName);
        }
      },
      harvestTimeout(itemName, value)
      {
        setTimeout(() =>
        {
          data_collector_engine.markDataExpired(itemName);
        }, value);
      },
      operationTimeout(itemName, value)
      {
        setTimeout(() =>
        {
          data_collector_engine.markDataOperationExpired(itemName);
        }, value);
      },
      harvestIfEqWithTimeout(itemName, value)
      { // New refresher harvestIfEq with timeout
        const { timeout, value: inputVal } = value;
        setTimeout(() => { data_collector_engine.markDataExpiredIfEq(itemName, inputVal); }, timeout);
      }
    };
    return refreshers;
  }

  createFarmers()
  {
    const farmers = {
      UWPStorage(params)
      {
        return TelemetryGlobals.storage.get(params);
      },
    };
    return farmers;
  }
}
