import Logger from '../logger/logger';
/**
 * Wrapper class making the http request call (using the built-in Fetch api).
 * Handling normal and exception cases.
 */
export default class HttpService
{
  onHttpResponse(response)
  {
    if (!response.ok)
    {
      const message = `HttpService Status: ${response.status}\nError Text: ${response.statusText}`;
      Logger.warn(message);
    }

    return response;
  }

  async sendHttpRequest({ url, headers, body, method = 'GET' })
  {
    const fetchReponse = await fetch(url, { headers, method, body });
    const response = await this.onHttpResponse(fetchReponse);
    return response;
  }
}
