// Background
import { BackgroundGlobals } from '@extension/background/background_globals';
// Common
import { extension } from '@extension/common/webextension';
import { BrowserType, BuildSettings } from '@extension/common/constants';
import BackgroundCommon from '@extension/common/background_common';

/**
 * This class is for helping in download scanning. Upon download complete this class triggers native message.
 * Native application then initiates scanning of the downloaded files.
 */
export default class OnChangedDownloadListener
{
  onChanged(downloadDelta)
  {
    if (!BackgroundCommon.isPersonalPolicyEnabled())
    {
      return;
    }

    // state is an optional argument returned by the onChanged API
    if (!downloadDelta.state) return;

    const { previous, current } = downloadDelta.state;
    if (previous === 'in_progress')
    {
      if (current === 'complete')
      {
        extension.downloads.search({ id: downloadDelta.id }, (items) =>
        {
          // For each downloaded item, tell native application that download has happened.
          // Native application can initiate scanning for each of the downloaded items.
          items.forEach((item) =>
          {
            BackgroundGlobals.messageDispatcher.onDownloadFinished(item);
          });
        });
        BackgroundGlobals.downloadUrls.delete(downloadDelta.finalUrl);
      }
      else if (current === 'interrupted')
      {
        BackgroundGlobals.downloadUrls.delete(downloadDelta.finalUrl);
      }
    }
  }

  onCreated(downloadItem)
  {
    BackgroundGlobals.downloadUrls.add(downloadItem.finalUrl);
  }

  init()
  {
    // Not supported on Edge Browser
    if (BuildSettings.BROWSER_TYPE === BrowserType.EDGE) return;
    extension.downloads.onChanged.addListener(this.onChanged);
    extension.downloads.onCreated.addListener(this.onCreated);
  }
}
