// Background
import { ContentCommands } from '@extension/common/commands';
import InjectBase from '@extension/background/content_injector/inject_base';
import Utils from '@extension/utilities/utils';
import { BackgroundGlobals } from '@extension/background/background_globals';
import BackgroundCommon from '@extension/common/background_common';
import { LocalStorageVariables } from '@extension/common/cache_store_variables';

export default class InjectAPSObserver extends InjectBase
{
  constructor({ url })
  {
    super('content_aps_observer.js');
    this.pingCommand = ContentCommands.PING_CONTENT_APS_OBSERVER;
    this.url = url;
  }

  check()
  {
    const apsToggleSetting = BackgroundGlobals.cacheStore.get(LocalStorageVariables.APS_TOGGLE_SETTING.name, true);
    if (!apsToggleSetting)
    {
      return false;
    }

    // Url Check
    if (!BackgroundCommon.isProperWebURL(this.url) || BackgroundCommon.isSiteAdvisorUrl(this.url))
    {
      return false;
    }

    const { APS_COHORT, SS_DEF } = BackgroundGlobals.nativeSettings;
    const isSupported = Utils.isChromeBrowser();
    if (APS_COHORT.value === 0 || !isSupported)
    {
      return false;
    }

    if (SS_DEF.value !== 1)
    {
      return false;
    }

    if (this.isSupportedEmail(this.url))
    {
      return true;
    }

    return false;
  }

  // If url matches an email we will create a mutation observer
  // Gmail re-renders the dom element on demand, therefore we need to observe for changes
  isSupportedEmail(url)
  {
    const apsEmailList = BackgroundGlobals.apsUrlList.email;
    const emailObjectKeys = Object.keys(apsEmailList);

    const emailUrl = emailObjectKeys.find((emailUrl) => url.includes(emailUrl));

    return emailUrl;
  }
}
