import { LocalStorageVariables } from '@extension/common/cache_store_variables';
import { BackgroundGlobals } from '@extension/background/background_globals';

export default class TyposquattingCache
{
  constructor()
  {
    this.cacheTyposquatting = LocalStorageVariables.CACHE_TYPOSQUATTING.name;
    this.urlMap = new Map();
  }

  checkCache()
  {
    const cache = BackgroundGlobals.cacheStore.get(this.cacheTyposquatting, {});

    if (Object.keys(cache).length >= BackgroundGlobals.TYPOSQUATTING_MAX_CACHE)
    {
      this.cleanTypoCache(cache);
    }
  }

  cleanTypoCache(cache)
  {
    for (const [keys, value] of Object.entries(cache))
    {
      if (value.timeOfExpiry <= Date.now())
      {
        delete (cache[keys]);
      }
    }
    BackgroundGlobals.cacheStore.set(this.cacheTyposquatting, cache);
  }

  addEntry(domain, value)
  {
    const cache = BackgroundGlobals.cacheStore.get(this.cacheTyposquatting, {});
    const timeOfExpiry = Date.now() + BackgroundGlobals.TYPOSQUATTING_EXPIRY;
    cache[domain] = { ...value, timeOfExpiry };
    BackgroundGlobals.cacheStore.set(this.cacheTyposquatting, cache);
  }

  getEntry(domain)
  {
    const cache = BackgroundGlobals.cacheStore.get(this.cacheTyposquatting, {});
    if (cache[domain])
    {
      const updatedTime = Date.now() + BackgroundGlobals.TYPOSQUATTING_EXPIRY;
      cache[domain].timeOfExpiry = updatedTime;
      BackgroundGlobals.cacheStore.set(this.cacheTyposquatting, cache);
    }
    return cache[domain];
  }

  clearEntries()
  {
    BackgroundGlobals.cacheStore.clear(this.cacheTyposquatting);
  }

  getAllEntries()
  {
    return BackgroundGlobals.cacheStore.get(this.cacheTyposquatting, {});
  }

  checkRequest(requestdata)
  {
    const listOfPromises = [];
    requestdata.forEach((value) =>
    {
      if (this.urlMap.has(value))
      {
        listOfPromises.push(this.urlMap.get(value));
      }
    });
    return listOfPromises;
  }

  addRequest(requestdata, promise)
  {
    requestdata.forEach((value) =>
    {
      this.urlMap.set(value, promise);
    });
  }

  deleteRequest(requestdata)
  {
    requestdata.forEach((value) =>
    {
      this.urlMap.delete(value);
    });
  }
}
