// Background
import { BackgroundGlobals } from '@extension/background/background_globals';
import CryptojackingListener from '@extension/background/listeners/cryptojacking_listener';
import ExternalMessageListener from '@extension/background/listeners/external_message_listener';
import MessageListener from '@extension/background/listeners/message_listener';
import OnChangedDownloadListener from '@extension/background/listeners/onchanged_download_listener';
import TabListener from '@extension/background/listeners/tab_listener';
import WebRequestListener from '@extension/background/listeners/webrequest_listener';
import BrowserActionListener from '@extension/background/listeners/browser_action_listener';

export const ListenerTypes = {
  TAB: 0,
  MESSAGE: 1,
  WEB_REQUEST: 2,
  DOWNLOAD: 3,
  BROWSER_ACTION: 4,
  EXTERNAL_MESSAGE: 5,
  CRYPTO: 6,
};
/**
 * Create and initialize the various listeners used in the extension.
 */
export default class ListenerManager
{
  constructor()
  {
    this.listeners = new Map();
  }

  getListener(listenerType)
  {
    return this.listeners.get(listenerType);
  }

  /**
   * Returns a list of all the supported listeners for a particular browser.
   * @returns {Object[]} - List of initialized listeners
   */
  getListeners()
  {
    if (this.listeners.size === 0)
    {
      this.listeners.set(ListenerTypes.TAB, new TabListener());// To listen for active tabs.
      this.listeners.set(ListenerTypes.MESSAGE, new MessageListener());// For IPC between content chrome process.
      this.listeners.set(ListenerTypes.WEB_REQUEST, new WebRequestListener());// Web Request listener is for all web requests from pages.
      this.listeners.set(ListenerTypes.DOWNLOAD, new OnChangedDownloadListener());// To listen for download events
      this.listeners.set(ListenerTypes.BROWSER_ACTION, new BrowserActionListener());// Listen to browser Action events
      this.listeners.set(ListenerTypes.EXTERNAL_MESSAGE, new ExternalMessageListener());// External msg listner between extensions
      if (!BackgroundGlobals.nativeSettings.CRYPTOJACKING_DISABLED.value)
      {
        this.listeners.set(ListenerTypes.CRYPTO, new CryptojackingListener());
      }
    }

    return this.listeners.values();
  }
}
