// Background
import { BackgroundGlobals } from '@extension/background/background_globals';
import ListenerManager from '@extension/background/listeners/listener_manager';
import WasmGenerator from '@extension/background/wasm/wasm_generator';
import LocalDispatcher from '@extension/background/dispatchers/local_dispatcher';
import NativeMessageDispatcherV1 from '@extension/background/dispatchers/native/native_message_dispatcher_v1';
import NativeWSSMessageDispatcher from '@extension/background/dispatchers/native/native_wss_message_dispatcher';
import ScheduleManager from '@extension/background/schedulers/schedulemanager';
import { schedulers } from '@extension/background/schedulers/schedulers';
import { CacheStore } from '@extension/background/cache_store';
import GarbageCollector from '@extension/background/garbage_collector';
import WebRequestDispatcher from '@extension/background/dispatchers/webrequest_dispatcher';
import { EngineDownloadHelper } from '@extension/background/schedulers/enginedownloadtask';
import StandAloneTelemetryWrapper from '@extension/background/standAloneTelemetryWrapper';
// Common
import { extension } from '@extension/common/webextension';
import { BrowserType } from '@extension/common/constants';
import { Logger } from '@extension/common/logger';
import BackgroundCommon from '@extension/common/background_common';
import BrowserAction from '@extension/background/browser_action';
import UrlParser from '@extension/common/url_parser';
import SearchSuggest from '@mcafee/wa-search-suggestion/index';
import HttpService from '@extension/common/http_service';

import { LocalStorageVariables } from '@extension/common/cache_store_variables';
import { v4 as uuidv4 } from 'uuid';
import { getBrowserInfo } from '@extension/utilities/utils';
// Typosquatting Cache
import TyposquattingCache from '@extension/background/typosquatting_cache';
import BackgroundUtils from '@extension/background/background_utils';
import MTIInitializer from '@extension/background/mti_initializer';

// Background script to be running in the chrome process. This takes care of making GTI requests.

/**
 * TODO: Comment
 */
class Background
{
  processActiveTab()
  {
    /**
     * Handles all returned tabs from the "tabs" API call.
     * @param {Object[]} tabs - Array of "Tab" objects
     */
    function processTabCb(tabs)
    {
      if (!tabs.length) return;

      const [originTab] = tabs;
      const { url, id } = originTab;

      if (BackgroundCommon.isProperWebURL(url))
      {
        WebRequestDispatcher.onHeadersReceived(url, id, 0, undefined);
        WebRequestDispatcher.onNavigateComplete(url, id, 0);
      }
    }

    const queryInfo = {
      active: true,
      currentWindow: true
    };

    extension.tabs.query(queryInfo, processTabCb);
  }

  toUUIDBrowserType(browserType)
  {
    let bType = '';

    switch (browserType)
    {
      case BrowserType.FIREFOX: bType = 'X';
        break;
      case BrowserType.CHROME: bType = 'Y';
        break;
      case BrowserType.EDGE: bType = 'Z';
        break;
      default: bType = 'U';
        break;
    }
    return bType;
  }

  /**
   * Setup standalone telemetry
   */
  async initStandaloneTelemetry()
  {
    const deviceId = BackgroundGlobals.cacheStore.get(LocalStorageVariables.EXTN_UUID2.name, '');
    const affid = BackgroundGlobals.cacheStore.get(LocalStorageVariables.WA_AFFID.name, 0);
    const arch = await new Promise((resolve) => chrome.runtime.getPlatformInfo((info) => resolve(info.arch.replace('-', '_'))));
    const browserInfo = getBrowserInfo();
    const contextVars = {
      device_id: deviceId,
      hit_session_id: uuidv4(),
      hit_session_start_time: new Date().toISOString(),
      operating_system: browserInfo.platform,
      locale: extension.i18n.getUILanguage().toLowerCase(),
      wss_installed: 'unknown',
      browser_version: browserInfo.version,
      browser_type: browserInfo.browser,
      device_cpu_architecture: arch,
      affid,
    };

    BackgroundGlobals.telemetryEngine = new StandAloneTelemetryWrapper(null, contextVars, Logger);
  }

  /**
   * Register all the listeners created for the background script.
   */
  initListeners()
  {
    BackgroundGlobals.listenerManager = new ListenerManager();
    const listeners = BackgroundGlobals.listenerManager.getListeners();

    for (const listener of listeners)
    {
      listener.init();
    }
  }

  async submain()
  {
    BackgroundGlobals.scheduleManager = new ScheduleManager();

    const nativeDispatcher = new NativeMessageDispatcherV1();
    nativeDispatcher.init();

    const wssNativeDispatcher = new NativeWSSMessageDispatcher();
    wssNativeDispatcher.init();

    // Local dispatcher dispatches most of the calls locally and delegates few calls such as telemetry,
    // settings, native logging to native if nativeDispatcher is available.
    BackgroundGlobals.messageDispatcher = new LocalDispatcher(nativeDispatcher);

    // settings, WSS native logging to native if wssNativeDispatcher is available.
    BackgroundGlobals.wssMessageDispatcher = new LocalDispatcher(wssNativeDispatcher);
    // Scheduler
    BackgroundGlobals.schedules = schedulers;
    BackgroundGlobals.scheduleManager.init();

    this.initListeners();

    // The initial tab at startup is not firing the navigate event with tab Id.
    // This is required to get the icon set for the browserAction.
    // Below getting the active tab at startup and then firing the beforeNavigate and Navigate events to the dispatcher.
    this.processActiveTab();
  }

  onInstall()
  {
    // Trigger FF Policy page on Firefox browser
    chrome.runtime.onInstalled.addListener(({ reason }) =>
    {
      if (reason === 'install')
      {
        BackgroundUtils.focusOrCreateTab(chrome.runtime.getURL('html/ff_policy.html'));
      }
    });
  }

  onBrowserStartUp()
  {
    chrome.runtime.onStartup.addListener(() =>
    {
      BackgroundGlobals.cacheStore.clearSessionVariables();
    });
  }

  // The entry point for background script. Here all listeners are registered.
  // The message dispatcher is initialized. This will take care of initializing the native communication layer or the standalone layer.
  async main()
  {
/////////////////////////////////////
/////////////////////
//////////////

    BackgroundGlobals.cacheStore = new CacheStore();
    await BackgroundGlobals.cacheStore.init();

    // Initialize Extension UUID
    if (!BackgroundGlobals.cacheStore.get(LocalStorageVariables.EXTN_UUID2.name, ''))
    {
      const browser = this.toUUIDBrowserType(BackgroundGlobals.browserType);
      BackgroundGlobals.cacheStore.set(LocalStorageVariables.EXTN_UUID2.name, `${browser}${uuidv4()}`);
    }

    BackgroundGlobals.tldParser = new UrlParser();
    BackgroundGlobals.userAgentInBase64 = btoa(navigator.userAgent);
    BackgroundGlobals.searchSuggest = new SearchSuggest(new HttpService());

    this.onBrowserStartUp();

    // Fetch Engine list
    EngineDownloadHelper.getEngines();

    // Init standalone telemetry
    this.initStandaloneTelemetry();

    // Init Typosquatting Cache
    BackgroundGlobals.typoCache = new TyposquattingCache();

    // Add all caches to the garbage collector
    BackgroundGlobals.garbageCollector = new GarbageCollector();
    BackgroundGlobals.garbageCollector.add(BackgroundGlobals.searchSuggest);
    BackgroundGlobals.garbageCollector.add(BackgroundGlobals.typoCache);

    // Browser Action icon
    BackgroundGlobals.browserAction = new BrowserAction();

    // AMWA-11850 CPSIR-83 - Secret code that is appended to image requests to not allow external access to image files
    BackgroundGlobals.WA_IMAGE_SECRET = Math.floor(Math.random() * 982451653 + 982451653).toString(36);

    const wasm = await WasmGenerator.generateWASM();
    BackgroundGlobals.wasm = wasm;
    BackgroundGlobals.firefoxInternalUUID = BackgroundGlobals.browserType === BrowserType.FIREFOX ? extension.i18n.getMessage('@@extension_id') : '';

    try
    {
      const result = await BackgroundGlobals.wasm.init(BackgroundGlobals.firefoxInternalUUID);
      if (result === '0')
      {
        Logger.log(`WASM initiation: ${result}`);
        await MTIInitializer.init();
        this.submain();
      }
    }
    catch (error)
    {
      Logger.error(error);
    }
  }
}

const bkobj = new Background();
bkobj.main();
