import { BackgroundGlobals } from '@extension/background/background_globals';
import ScheduledTaskBase from '@extension/background/schedulers/scheduledtaskbase';

export default class TelemetryHeartBeatTask extends ScheduledTaskBase
{
  init()
  {
  }

  // Send telemetry now
  run()
  {
    if (BackgroundGlobals.isNativeConnected) // If connected to native then donot send the telemetry event
    {
      return;
    }

    if (BackgroundGlobals.telemetryEngine)
    {
      BackgroundGlobals.telemetryEngine.setUrl(BackgroundGlobals.ExtensionTelemetryUrl);
      BackgroundGlobals.telemetryEngine.sendEvent('wa_heartbeat');
    }
  }
}
