/* eslint-disable camelcase */
// c++ is sending in snakecase, change to camel case here later

// Background
import { BuildSettings, BrowserType } from '@extension/common/constants';
import WhitelistWrapper from '@extension/background/whitelist_wrapper';
import NativeSetValues from '@extension/background/dispatchers/native/native_set_values';
import FeatureStats from '@extension/background/feature_stats';
import BackgroundUtils from '@extension/background/background_utils';
import NativeGenericRequest from '@extension/background/dispatchers/native/native_generic_request';
import { NativeConstants, SETTINGS_TYPE, SCOPE } from '@extension/background/dispatchers/native/native_settings';
import FeatureSettings from '@extension/background/feature_settings';
// Common
import { BackgroundGlobals } from '@extension/background/background_globals';
import { extension } from '@extension/common/webextension';
import { CacheStorageVariables, LocalStorageVariables } from '@extension/common/cache_store_variables';
import BackgroundCommon from '@extension/common/background_common';
import { SocialMediaAllTypes } from '@extension/common/sma_variables';
import ExternalExtensions from '@extension/common/external_extensions';
import { Logger } from '@extension/common/logger';
import MTIInitializer from '@extension/background/mti_initializer';

import SmartNativeClient from '@mcafee/browser-host/smartnativeclient';

const ExtensionCommands = {
  GET_VALUES_RESPONSE: 0,
  EXECUTE_COMMAND: 1,
  REDIRECT_URL: 3,
  REFRESH: 4,
  RESTART: 5,
  LOGMSG: 6,
  HANDSHAKE_GREETING: 8,
  EXTENSION_STATE_RESPONSE: 9,
  GENERIC_NATIVE_REQUEST: 12,
  DOWNLOAD_SCAN_RESULT: 13,
  DELETE_FILE_RESULT: 14
};

export const NativeCommands = {
  GET_VALUES: 0,
  SET_VALUES: 1,
  EXECUTE_COMMAND: 2,
  LOG_MSG: 6,
  ON_DOWNLOAD_FINISHED: 7,
  HANDSHAKE_GREETING: 9,
  GET_EXTENSION_STATUS: 12,
  INSTALL_EXTENSION: 13,
  TELEMETRY: 14,
  EXTENSION_STATUS_UPDATE: 15,
  AUTO_PLAY_VIDEO_SITE: 16,
  GET_CACHE_REQUEST: 18,
  ADD_CACHE_REQUEST: 19,
  SHOULD_SHOW_TOAST: 20,
  MFW_REQUEST: 21,
  DOWNLOAD_SCANNING_DELETE_FILE: 22,
  SHOW_TOAST: 23,
  EFFICACY_DETAILS: 25,
  SUBSCRIBER_INFO_REQUEST: 26,
};

// Native settings
const { INT, BOOL, STRING } = SETTINGS_TYPE;
const { SYSTEM } = SCOPE;
const {
  SMA_ANNOTATION_OPTIONS,
  SMA_USER_TOGGLE,
  BROWSER_TYPE,
  USER_AGENT,
  EXTENSION_VERSION_CH,
  EXTENSION_VERSION_ED,
  EXTENSION_VERSION_FF,
  ANNOTATION_JOURNEY_COHORT
} = BackgroundGlobals.nativeSettings;

/**
 * Dispatcher class handling all communication between extension and native.
 * Both extension and native maintain a persistent connection.
 */
export default class NativeMessageDispatcherV1
{
  constructor()
  {
    this.browserStartSent = false;
    this.nativeClient = null;
    this.portNames = ['webadvisor.mcafee.chrome.extension', 'siteadvisor.mcafee.chrome.extension'];
    this.portIndex = 0;
    this.port = null;
  }

  /**
   * TODO: Comments
   */
  sendInitialSettings()
  {
    const browserMap = {
      [BrowserType.CHROME]: EXTENSION_VERSION_CH.name,
      [BrowserType.FIREFOX]: EXTENSION_VERSION_FF.name,
      [BrowserType.EDGE]: EXTENSION_VERSION_ED.name,
    };

    const settings = new NativeSetValues(this);
    settings.add(BROWSER_TYPE.name, BackgroundGlobals.browserType.toString(), SYSTEM, STRING);
    settings.add(USER_AGENT.name, navigator.userAgent, SYSTEM, STRING);
    settings.add(browserMap[BackgroundGlobals.browserType], BuildSettings.EXTENSION_VERSION.toString(), SYSTEM, STRING);
    settings.send();

    // Update native settings with what is in browser local storage
    const cacheStorage = BackgroundGlobals.cacheStore;
    const updateNativeSettings = async () =>
    {
      try
      {
        const userToggleOption = cacheStorage.get(LocalStorageVariables.SMA_USER_TOGGLE.name, true);
        const smaOption = cacheStorage.get(LocalStorageVariables.SMA_ANNOTATION_OPTIONS.name, SocialMediaAllTypes);

        const updateSettings = new NativeSetValues(this);
        updateSettings.add(SMA_USER_TOGGLE.name, userToggleOption, SYSTEM, BOOL);
        updateSettings.add(SMA_ANNOTATION_OPTIONS.name, smaOption, SYSTEM, INT);
        updateSettings.send();
      }
      catch (error)
      {
        Logger.error(`updateNativeSettings error: ${error}`);
      }
    };

    updateNativeSettings();
  }

  toNativeSettingNames()
  {
    const map = new Map();
    const bkNativeSettingNames = Object.getOwnPropertyNames(BackgroundGlobals.nativeSettings);
    bkNativeSettingNames.forEach((name) =>
    {
      map.set(BackgroundGlobals.nativeSettings[name].name, name);
    });
    return map;
  }

  /**
   * Update BackgroundGlobals Settings
   */
  updateBackgroundGlobals(settings)
  {
    const names = Object.getOwnPropertyNames(settings);

    const map = this.toNativeSettingNames();

    names.forEach((name) =>
    {
      let value = settings[name];
      if (value !== undefined)
      {
        const actualName = map.get(name);
        if (actualName !== undefined) // Check whether the property exists
        {
          const nativeObj = BackgroundGlobals.nativeSettings[actualName];
          if (nativeObj.preprocess !== undefined) // If there is a pre-processing of values
          {
            value = nativeObj.preprocess(value);
          }
          nativeObj.value = value;
        }
      }
    });

    if (ANNOTATION_JOURNEY_COHORT.value !== 0)
    {
      const ajAPSEnable = BackgroundGlobals.cacheStore.get(LocalStorageVariables.AJ_APS_ENABLE.name, null);
      if (ajAPSEnable !== null)
      {
        BackgroundGlobals.nativeSettings.APS_COHORT.value = ajAPSEnable;
      }
    }

    BackgroundGlobals.updatedByNative = true;
  }

  /**
   * Handle the response of get Settings
   */
  handleNativeSettings(settings)
  {
    // Set BackgroundGlobal feature flags
    this.updateBackgroundGlobals(settings);

    // If HTI is supposed to be initialized it will be done from the init call based
    // on the flags and values retrieved from native.
    MTIInitializer.init();

    // Setup features based on Native settings
    FeatureSettings.setupFeatures();

    // Populate feature statistics from native counters
    const { USER_NATIVE_STATS } = LocalStorageVariables;
    const useNativeStats = BackgroundGlobals.cacheStore.get(USER_NATIVE_STATS.name, true);
    if (useNativeStats)
    {
      FeatureStats.init();
      BackgroundGlobals.cacheStore.set(USER_NATIVE_STATS.name, false);
    }

    // Save AFFID value from native to localstorage and update extension telemetry context
    const { WA_AFFID } = LocalStorageVariables;
    BackgroundGlobals.cacheStore.set(WA_AFFID.name, BackgroundGlobals.nativeSettings.AFF_ID.value);
    BackgroundGlobals.telemetryEngine.updateContext('affid', BackgroundGlobals.nativeSettings.AFF_ID.value);

    // Populate whitelist from native cache
    this.cacheWhitelist(BackgroundGlobals.nativeSettings.BLOCKPAGE_WHITELIST.value, BackgroundGlobals.nativeSettings.TS_WHITELIST.value);
  }

  /**
   * Grab the app settings from native then initialize the background globals.
   */
  async getInitialSettings()
  {
    const jsettings = Object.values(BackgroundGlobals.nativeSettings).filter((setting) => setting.writeOnly !== true);
    const response = await this.getValues(jsettings);
    this.handleNativeSettings(response);
  }

  connectToPort()
  {
    if (this.portIndex >= this.portNames.length)
    {
      // If we failed to connect to Native, then we use the default values
      // from our extension to initialize the features
      FeatureSettings.setupFeatures();
      BackgroundGlobals.isNativeConnected = false;
      const task = BackgroundGlobals.scheduleManager.getTask(LocalStorageVariables.TELEMETRY_HEARTBEAT_TASK.name);
      if (task !== null)
      {
        task.enabled = true;
      }

      // Failed to connect to native so setting background globals and extension telemetry affid value to the one from local storage
      const localStorageAffidValue = BackgroundGlobals.cacheStore.get(LocalStorageVariables.WA_AFFID.name, 0);
      BackgroundGlobals.nativeSettings.AFF_ID.value = localStorageAffidValue;
      BackgroundGlobals.telemetryEngine.updateContext('affid', localStorageAffidValue);

      // Ask schedule manager to recalculate
      BackgroundGlobals.scheduleManager.reinit();
      return;
    }

    const application = this.portNames[this.portIndex++];
    Logger.log(`Connecting to native host ${application}`);

    /**
     * When external extension or native disconnected from long-lived connection
     */
    const onDisconnectEvent = (port) =>
    {
      if (port.error)
      {
        Logger.warn(`Error connecting to native host or external extension: ${port.error.message}`);
      }
      this.connectToPort();
    };

    /**
     * Incoming messages from native
     * @returns {Boolean} Return false to disallow forwarding of message to other extensions
     */
    const onNativeMessage = (message) =>
    {
      this.processMessage(message);
      return false;
    };

    const onConnectHandler = (currentExtId, requestedExtId) =>
    {
      if (currentExtId === ExternalExtensions.get('ChromeWebAdvisorExtension') && requestedExtId === ExternalExtensions.get('EdgeWebAdvisorExtension'))
      {
        chrome.management.uninstallSelf();
      }
    };

    // regex: seperate the each setting with '|'
    const handshakeSettings = { setting_subscription_regex: `\\${BackgroundGlobals.nativeSettings.DOWNLOADS_BLOCKED.name}|\\${BackgroundGlobals.nativeSettings.ANNOTATION_JOURNEY_TOAST.name}` };
    this.nativeClient = new SmartNativeClient(application, null, onNativeMessage, onDisconnectEvent, Logger, handshakeSettings, onConnectHandler);
    this.nativeClient.init((status) =>
    {
      if (!status.connected)
      {
        Logger.error(`Failed to connect to native host ${application}`);
        this.connectToPort();
      }
      else
      {
        // Once established connection after that, if it's disconnected then it should retry all port again
        this.portIndex = 0;

        this.sendInitialSettings();

        this.getSubscriberInfo().then((wssEmail) =>
        {
          BackgroundGlobals.cacheStore.set(CacheStorageVariables.WSS_EMAIL.name, wssEmail.toLowerCase());
        });
      }
    });
  }

  /**
   * Initialize the dispatcher class, setting up the event listeners and initializing setting variables
   * @param {Object} port - Runtime port object
   * @param {String} initialMessage - TODO: Comment
   */
  init()
  {
    try
    {
      this.connectToPort();
    }
    catch (e)
    {
      Logger.error(e.message);
    }
  }

  /**
   * Add whiltelisting of mcafee domains
   */
  autoPlayVideoSite(msg)
  {
    this.nativeClient.postMessage(msg);
  }

  /**
   * Obtain the whitelisted urls from native. The urls that were whitelisted earlier are stored in native and require
   * the extension to send a request to obtain this information. The whitelisted information obtained are globally stored.
   */
  cacheWhitelist(blockPageArray, typosquatArray)
  {
    const { BLOCK_PAGE_WHITELIST, TYPOSQUATTING_WHITELIST, WHITELIST_FLAG } = LocalStorageVariables;
    const whiteListFlag = BackgroundGlobals.cacheStore.get(WHITELIST_FLAG.name);

    if (whiteListFlag) return;

    const blockPageWhitelist = blockPageArray !== undefined ? blockPageArray.split(';') : [];
    const typosquatWhitelist = typosquatArray !== undefined ? typosquatArray.split(';') : [];

    // cache items into local storage
    WhitelistWrapper.addDomainsToWhitelist(blockPageWhitelist, BLOCK_PAGE_WHITELIST.name);
    WhitelistWrapper.addDomainsToWhitelist(typosquatWhitelist, TYPOSQUATTING_WHITELIST.name);
    BackgroundGlobals.cacheStore.set(WHITELIST_FLAG.name, true);
  }

  /**
   * Obtain the extension status from the native app.
   * @param {Object}   extension_id - Destructured property referring to the extension id
   * @param {Function} onExtStatusRetrievedCb - Invoke after recieving information from the native app
   */
  getExtensionStatus({ extension_id }, onExtStatusRetrievedCb)
  {
    if (!ExternalExtensions.isWhiteListed(extension_id))
    {
      return;
    }

    const message = {
      request_type: NativeCommands.GET_EXTENSION_STATUS,
      payload: { extension_id }
    };

    this.nativeClient.postMessage(message, (response) =>
    {
      const { payload } = response;

      if (typeof onExtStatusRetrievedCb === 'function')
      {
        onExtStatusRetrievedCb(payload.extension_state);
      }
    });
  }

  /**
   * Push logged messages to the native app to log.
   * @param {Object} logDetails - Log details information
   */
  logger(details)
  {
    try
    {
      const {
        browserType,
        extensionType,
        logLevel,
        message,
        processType
      } = details;

      const postMessage = {
        request_type: NativeCommands.LOG_MSG,
        id: 0,
        payload: {
          process_type: processType,
          browser_type: browserType,
          extension_type: extensionType,
          log_level: logLevel,
          msg: message
        }
      };

      this.nativeClient.postMessage(postMessage);
    }
    catch (error)
    {
      Logger.setNativeLogging(false);
      Logger.error(`Failed to log with details: ${JSON.stringify(details)}. Exception: ${error.message}`);
    }
  }

  /**
   * The values retrieved from native after the first initial communication step.
   * @param {Object} payload - The payload retrieved from the native app
   */
  handleNativeHandshake(payload)
  {
    // Settings related responses from native
    const { settings } = payload;

    if (!settings)
    {
      return;
    }

    // Set logger to native logger if needed
    const useNativeLogger = settings[NativeConstants.USE_NATIVE_LOGGER];

    if (useNativeLogger)
    {
      Logger.setNativeLogging(true);
    }

    BackgroundGlobals.AUTO_PLAY_NOTIFICATION_ENABLED = settings[NativeConstants.WEBBOOST_AUTO_PLAY_NOTIFICATION] || false;

    // Update browser with native settings
    this.getInitialSettings();

    this.getNativeCache();
  }

  /**
   * TODO: Comments
   */
  SetXYViewport(xView, yView)
  {
    const args = { X: xView, Y: yView };
    BackgroundGlobals.ffViewports = args;

    if (this.browserStartSent)
    {
      return;
    }

    this.executeCommand({ command_id: 0, args });
    this.browserStartSent = true;
  }

  /**
   * Messages being sent from native to the extension.
   * @param {Object} payload - eg. [{"request_type": "action", "name": "ss_toast", "value": <JSON string>}]
   */
  genericNativeRequest(payload)
  {
    const genericRequest = new NativeGenericRequest(this);
    genericRequest.processMessage(payload);
  }

  /**
   * Parse the incoming JSON serialized message from the native app.
   * @param {Object} nativeMessage - JSON serialized object which was sent from native
   */
  processInwardMessage(nativeMessage)
  {
    const { payload, request_type } = nativeMessage;
    const commandName = Object.keys(ExtensionCommands).find((key) => ExtensionCommands[key] === request_type);
    Logger.log(`Processing message from binary: ${request_type} (${commandName})\n${JSON.stringify(payload)}`);

    switch (request_type)
    {
      case ExtensionCommands.HANDSHAKE_GREETING:
        this.handleNativeHandshake(payload);
        break;
      case ExtensionCommands.RESTART:
        extension.runtime.reload();
        break;
      case ExtensionCommands.GENERIC_NATIVE_REQUEST:
        this.genericNativeRequest(payload);
        break;
      case ExtensionCommands.REDIRECT_URL:
      {
        const { redirect_url } = payload;
        if (redirect_url && BackgroundCommon.isPropertRedirectUrl(redirect_url)) BackgroundUtils.focusOrCreateTab(redirect_url);
        break;
      }
    }
  }

  /**
   * Handles the default behaviour for messages being returned or captured by the "onMessage" event listener.
   * @param {Object} nativeResponse - JSON serialized object containing information send from the native app
   */
  processMessage(nativeResponse)
  {
    const message = JSON.stringify(nativeResponse);

    if (!('request_type' in nativeResponse && 'id' in nativeResponse && 'payload' in nativeResponse))
    {
      Logger.log(`Invalid native json message: ${message} `);
      return;
    }

    try
    {
      this.processInwardMessage(nativeResponse);
    }
    catch (error)
    {
      Logger.log(`Native json message may be missing some keys: ${message} + ${error.message} `);
    }
  }

  /**
   * Set the fields on the database settings used by the native app.
   * @param {Object} settings - The fields to set in the db
   */
  setValues(settings)
  {
    this.nativeClient.postMessage({
      request_type: NativeCommands.SET_VALUES,
      payload: {
        VERB: 'set-setting',
        PAYLOAD: settings
      }
    });
  }

  /**
   * Obtain the values that are stored in the db by the native app.
   * @param {Object} getList - Payload specifying the information to be retrieved from the native
   */
  getValues(getList)
  {
    const messageOptions = {
      request_type: NativeCommands.GET_VALUES,
      payload: {
        VERB: 'get-setting',
        PAYLOAD: getList
      }
    };

    const postMessage = (resolve) =>
    {
      this.nativeClient.postMessage(messageOptions, (jsonMsg) =>
      {
        const { response } = jsonMsg.payload;
        const responseObject = {};
        if (!Array.isArray(response))
        {
          resolve(responseObject);
          return;
        }

        for (const item of response)
        {
          if (item.name !== undefined && item.value !== undefined)
          {
            responseObject[item.name] = item.value;
          }
        }

        resolve(responseObject);
      });
    };

    return new Promise(postMessage);
  }

  /**
   * TODO: Comments
   */
  onDownloadFinished(msg)
  {
    this.nativeClient.postMessage(msg);
  }

  /**
   * TODO: Comments
   */
  executeCommand({ command_id, args })
  {
    this.nativeClient.postMessage({
      request_type: NativeCommands.EXECUTE_COMMAND,
      id: 0,
      payload: {
        command_id,
        args
      }
    });
  }

  /**
   * TODO: Comments
   */
  installExtension(extension_id, redirectUrl)
  {
    this.nativeClient.postMessage({
      request_type: NativeCommands.INSTALL_EXTENSION,
      id: 0,
      payload: {
        extension_id,
        redirect_url: redirectUrl,
      }
    });
  }

  postMessage(message)
  {
    this.nativeClient.postMessage(message);
  }

  /**
   * TODO: Comments
   */
  sendTelemetry(message)
  {
    if (!BackgroundCommon.isPersonalPolicyEnabled() || !BackgroundCommon.isActivityPolicyEnabled())
    {
      return;
    }

    this.nativeClient.postMessage(message);
  }

  /**
   * TODO: Comments
   */
  extensionStatusUpdate({ extensionId })
  {
    this.nativeClient.postMessage({
      request_type: NativeCommands.EXTENSION_STATUS_UPDATE,
      id: 0,
      payload: {
        extension_id: extensionId
      }
    });
  }

  /**
   * TODO: Comments
   */
  getNativeCache(lastUrl = '')
  {
    const messageOptions = {
      request_type: NativeCommands.GET_CACHE_REQUEST,
      payload: {
        last_url: lastUrl
      }
    };

    this.nativeClient.postMessage(messageOptions, (response) =>
    {
      try
      {
        if (!response || !response.payload)
        {
          Logger.warn('Native Client response payload is empty for GET_CACHE_REQUEST');
          return;
        }

        const resultArray = JSON.parse(response.payload);

        if (!Array.isArray(resultArray) || resultArray.length !== 1)
        {
          return;
        }

        const responseElement = resultArray[0].a;

        if (!responseElement || !Array.isArray(responseElement) || responseElement.length === 0)
        {
          return;
        }

        let nextUrl = '';

        for (const cacheItem of responseElement)
        {
          nextUrl = cacheItem.url;
          delete cacheItem.url;

          if (!BackgroundGlobals.mtiRequestor.setReputation(nextUrl, cacheItem))
          {
            // Cache reached maximum size, no point adding
            return;
          }
        }

        this.getNativeCache(nextUrl);
      }
      catch (e)
      {
        Logger.error(`Exception in getNativeCache: ${e.message}`);
      }
    });
  }

  /**
   * Send cache data to the native app.
   * @param {String} gtiString - Cache information
   */
  pushCacheToNative(gtiString)
  {
    const messageOptions = {
      request_type: NativeCommands.ADD_CACHE_REQUEST,
      payload: {
        cache_data: gtiString
      }
    };

    this.nativeClient.postMessage(messageOptions);
  }

  getSubscriberInfo(product, attribute)
  {
    const payload = {};
    if (product)// if null - vso (virus scan) will be used
    {
      payload.product = product;
    }

    if (attribute)// if null - e_mail will be used
    {
      payload.attribute = attribute;
    }

    const messageOptions = {
      request_type: NativeCommands.SUBSCRIBER_INFO_REQUEST,
      payload
    };

    const postMessage = (resolve) =>
    {
      this.nativeClient.postMessage(messageOptions, (response) =>
      {
        // response = {
        // id,
        // payload: {valid, value, error_message},
        // request_type
        // }
        if (!response || !response.payload || !response.payload.valid)
        {
          const err = response.payload ? response.payload.error_message : 'unknown_error';
          Logger.warn(`[native_message_dispatcher] Error retrieving WSS Subscriber information, error: ${err}`);
          resolve('');
        }

        resolve(response.payload.value);
      });
    };

    return new Promise(postMessage);
  }

  onTabClose()
  {
    const payload = {
      name: 'event_on_tab_close',
      data: {}
    };

    this.nativeClient.postMessage({
      request_type: NativeCommands.MFW_REQUEST,
      payload
    });
  }

  mfwResetNativeSettings()
  {
    const payload = {
      name: 'event_on_reset_settings',
      data: {}
    };

    this.nativeClient.postMessage({
      request_type: NativeCommands.MFW_REQUEST,
      payload
    });
  }
}
