import { extension } from '@extension/common/webextension';
import { BackgroundGlobals } from '@extension/background/background_globals';

export default class BackgroundUtils
{
  static focusOrCreateTab(urlToFocus)
  {
    extension.tabs.query({ currentWindow: true }, (tabs) =>
    {
      for (const { id, url } of tabs)
      {
        if (url.includes(urlToFocus) || urlToFocus.includes(url))
        {
          extension.tabs.update(id, { active: true, url: urlToFocus }, () => { });
          return;
        }
      }

      extension.tabs.create({ url: urlToFocus });
    });
  }

  /**
   * Get the WebRequest related data based on tabId and requestId
   * @param {number} tabId - id of the tab
   * @param {number} requestId - id of the request
   * @return {object} request data based on the tabID and requestId
   */
  static getRequestData(tabId, requestId)
  {
    if (tabId === null || requestId === null) return {};

    const tabIdData = BackgroundGlobals.tabData.get(tabId);
    const requestData = tabIdData.requestData || {};
    const requestIdData = requestData[requestId] || {};
    return requestIdData;
  }

  /**
   * Set the WebRequest related data based on tabId and requestId
   * @param {number} tabId - id of the tab
   * @param {number} requestId - id of the request
   * @param {object} newRequestData - new request data to be set on the tabId > requestId object
   */
  static setRequestData(tabId, requestId, newRequestData)
  {
    if (tabId === null || requestId === null) return;

    const tabIdData = BackgroundGlobals.tabData.get(tabId);
    const requestData = tabIdData.requestData || {};
    const requestIdData = requestData[requestId] || {};
    requestData[requestId] = { ...requestIdData, ...newRequestData };
    BackgroundGlobals.tabData.set(tabId, { ...tabIdData, requestData });
  }

  /**
   * Delete the WebRequest related data based on tabId and requestId
   * @param {number} tabId - id of the tab
   * @param {number} requestId - id of the request
   */
  static deleteRequestData(tabId, requestId)
  {
    if (tabId === null || requestId === null) return;

    const tabIdData = BackgroundGlobals.tabData.get(tabId);
    const requestData = tabIdData.requestData || {};
    delete requestData[requestId];
    BackgroundGlobals.tabData.set(tabId, { ...tabIdData, requestData });
  }
}
