import HTICacheLogic from './hti_cache_logic';
import { HTIConstants } from './hti_contants';
import { HTIStorageVariables } from './hti_storage_variables';

/**
 * TODO: Comments
 */
export default class HTICache
{
  constructor(cacheStore)
  {
    this.urlQueryMap = new Map();
    this.cacheStore = cacheStore;
    this.cacheLogic = new HTICacheLogic(this.cacheStore);
  }

  clear()
  {
    this.urlQueryMap = new Map();
    this.cacheStore.set(HTIStorageVariables.CACHE_HTI, null);
  }

  setAlarmCacheClean()
  {
    // clear any pass created alarms
    chrome.alarms.clear(HTIConstants.reputationAlarmName);

    // create new alarm
    chrome.alarms.create(HTIConstants.reputationAlarmName, { delayInMinutes: 60 });

    // setup alarm listener
    chrome.alarms.onAlarm.addListener((alarmInfo) =>
    {
      if (alarmInfo.name !== HTIConstants.reputationAlarmName)
      {
        return;
      }

      // Clean reputation cache when it is over half full
      const cache = this.cacheStore.get(HTIStorageVariables.CACHE_HTI, []);
      if (Object.keys(cache).length > HTIConstants.maxCacheRecords * 0.5)
      {
        this.cleanReputationCache();
      }
    });
  }

  // Look through the reputation cache and remove the expired entries
  cleanReputationCache()
  {
    const repCache = this.cacheStore.get(HTIStorageVariables.CACHE_HTI, []);

    const currentTime = Date.now();

    const filteredCache = repCache.filter((cacheObject) =>
    {
      const { ttl, timeCached } = cacheObject;
      const timeElapsed = (currentTime - timeCached) / 1000;

      // return the objects which has not expired;
      return timeElapsed < ttl;
    });

    this.cacheStore.set(HTIStorageVariables.CACHE_HTI, filteredCache);
  }

  setReputation(url, value)
  {
    this.cacheLogic.set(url, value);
  }

  // Get item from cache
  getReputation(url)
  {
    return this.cacheLogic.get(url);
  }

  checkRequest(requestdata)
  {
    const listOfPromises = [];
    requestdata.forEach((url) =>
    {
      if (this.urlQueryMap.has(url))
      {
        listOfPromises.push(this.urlQueryMap.get(url));
      }
    });
    return listOfPromises;
  }

  addRequest(requestdata, promise)
  {
    requestdata.forEach((url) =>
    {
      this.urlQueryMap.set(url, promise);
    });
  }

  deleteRequest(requestdata)
  {
    requestdata.forEach((url) =>
    {
      this.urlQueryMap.delete(url);
    });
  }
}
