// Background
import { ContentCommands } from '@extension/common/commands';
import InjectBase from '@extension/background/content_injector/inject_base';
import Utils from '@extension/utilities/utils';
import { BackgroundGlobals } from '@extension/background/background_globals';
import BackgroundCommon from '@extension/common/background_common';
import { CacheStorageVariables, LocalStorageVariables } from '@extension/common/cache_store_variables';
import { BALLOON_TRIGGER_TYPE } from '@extension/ui/balloon_message/advanced_protection_signal';
import UrlParser from '@extension/common/url_parser';
import { Color } from '@extension/common/constants';
import { Logger } from '@extension/common/logger';

export default class InjectAPSBalloon extends InjectBase
{
  constructor({
    url, tabId, categories, color, isSignUp
  })
  {
    super('content_aps_balloon.js');
    this.pingCommand = ContentCommands.PING_CONTENT_APS_BALLOON;
    this.url = url;
    this.color = color;
    this.tabId = tabId;
    this.categories = categories;
    this.isSignUp = false;
    this.ratBlacklist = BackgroundGlobals.ratBlacklist;
    this.domain = UrlParser.getDomain(this.url);

    // set locally
    this.balloonTriggerType = null;
    this.apsEmailLinkIndexFound = -1;

    // This is to handle request for Sign up page
    // Triggered by the form detection sign up page observer
    if (isSignUp)
    {
      this.isSignUp = true;
      this.balloonTriggerType = BALLOON_TRIGGER_TYPE.SIGNUP;
    }
  }

  check()
  {
    const apsToggleSetting = BackgroundGlobals.cacheStore.get(LocalStorageVariables.APS_TOGGLE_SETTING.name, true);
    if (!apsToggleSetting)
    {
      return false;
    }

    // Url Check
    if (!BackgroundCommon.isProperWebURL(this.url) || BackgroundCommon.isSiteAdvisorUrl(this.url))
    {
      Logger.log(`InjectAPSBalloon: return false - not proper url ${this.url}`);
      return false;
    }

    const { APS_COHORT, SS_DEF } = BackgroundGlobals.nativeSettings;
    const isSupported = Utils.isChromeBrowser();
    if (APS_COHORT.value === 0 || !isSupported)
    {
      Logger.log('InjectAPSBalloon: return false - feature not supported');
      return false;
    }

    if (SS_DEF.value !== 1)
    {
      Logger.log('InjectAPSBalloon: return false - SS off');
      return false;
    }

    if (this.isRemoteAccessSite())
    {
      Logger.log('InjectAPSBalloon: return false - remote access site');
      return false;
    }

    // Check if color is green or grey
    if (this.color !== Color.GREEN && this.color !== Color.UNKNOWN)
    {
      Logger.log('InjectAPSBalloon: return false - invalid color');
      return false;
    }

    if (!this.isShoppingSite(this.url) && !this.isFinancialSite(this.categories) && !this.isPageRedirectedFromEmail(this.url) && !this.isSignUp)
    {
      Logger.log(`InjectAPSBalloon: return false - not supported aps site ${this.url}`);
      return false;
    }

    this.saveAPSDetails(this.tabId, this.color, this.url, this.balloonTriggerType);
    Logger.log(`InjectAPSBalloon: return true tabId : ${this.tabId} , ${this.url}`);
    return true;
  }

  isRemoteAccessSite()
  {
    // check if category is Remote Access site
    if (this.categories.includes('ra'))
    {
      return true;
    }

    // inside RAT Blacklist: show
    for (const site of this.ratBlacklist)
    {
      if (site.domain === this.domain)
      {
        if (site.urls.length === 0)
        {
          return true;
        }

        if (site.urls.includes(UrlParser.getCleanURI(this.url)))
        {
          return true;
        }
      }
    }

    return false;
  }

  // Handle shopping sites
  isShoppingSite(url)
  {
    const urlObject = new URL(url);
    const apsShoppingList = BackgroundGlobals.apsUrlList.shopping;
    const shoppingObjectKeys = Object.keys(apsShoppingList);

    if (shoppingObjectKeys.includes(urlObject.hostname))
    {
      const siteAPSPaths = apsShoppingList[urlObject.hostname].paths;
      const pathFound = siteAPSPaths.find((path) => urlObject.pathname.includes(path));
      if (pathFound)
      {
        this.balloonTriggerType = BALLOON_TRIGGER_TYPE.ECOMMERCE;
        return true;
      }
    }
    return false;
  }

  // Handle Financial Site
  isFinancialSite(categories)
  {
    if (categories.includes('fi'))
    {
      this.balloonTriggerType = BALLOON_TRIGGER_TYPE.FINANCIAL;
      return true;
    }

    return false;
  }

  // Handle Pages redirected from email pages
  isPageRedirectedFromEmail(url)
  {
    // Check the array of domains to see if the
    // site provided is part of the array
    const apsEmailLinks = BackgroundGlobals.cacheStore.get(CacheStorageVariables.APS_EMAIL_LINKS.name, []);
    for (let index = 0; index < apsEmailLinks.length; index++)
    {
      const domain = apsEmailLinks[index].split('www.').pop();
      if (url.includes(domain) || domain.includes(url))
      {
        this.balloonTriggerType = BALLOON_TRIGGER_TYPE.EMAIL;
        this.apsEmailLinkIndexFound = index;
        return true;
      }
    }

    return false;
  }

  runAfter(success)
  {
    if (!success)
    {
      return;
    }

    if (this.balloonTriggerType === BALLOON_TRIGGER_TYPE.EMAIL)
    {
      // Remove the url from the APS email link
      const apsEmailLinks = BackgroundGlobals.cacheStore.get(CacheStorageVariables.APS_EMAIL_LINKS.name, []);
      apsEmailLinks.splice(this.apsEmailLinkIndexFound, 1);
      BackgroundGlobals.cacheStore.set(CacheStorageVariables.APS_EMAIL_LINKS.name, apsEmailLinks);
    }
  }

  saveAPSDetails(tabId, color, uri, triggerType)
  {
    const apsInfo = { rating: 'GREY', fullMsgShown: true, triggerType };

    // Check if this has been shown previously
    const domain = UrlParser.getDomain(uri);
    const apsDomainsShownFM = BackgroundGlobals.cacheStore.get(LocalStorageVariables.APS_FULL_MSG_SHOWN_ARRAY.name, []);
    const fullMsgShown = apsDomainsShownFM.includes(domain);

    // find which balloon should be displayed (full or minimized)
    if (!fullMsgShown)
    {
      apsInfo.fullMsgShown = false;
      apsDomainsShownFM.push(domain);
      BackgroundGlobals.cacheStore.set(LocalStorageVariables.APS_FULL_MSG_SHOWN_ARRAY.name, apsDomainsShownFM);
    }
    // Allocate appropriate rating
    if (color === Color.GREEN)
    {
      apsInfo.rating = 'GREEN';
    }
    // Save this details into the tabdata to be retrieved later by the content script
    BackgroundGlobals.tabData.update(tabId, { url: uri, apsDetails: apsInfo });
  }
}
