import Emitter from './emitter';
import Logger from './logger';
import { arrayRemoveDuplicates, mergeObjects } from './utils';
import { priorityConfig } from './priority_config';
import { TelemetryGlobals } from './telemetry_globals';

export default class BaseTransmitter
{
  constructor()
  {
    this.emitters = {};
  }

  isEventThrottled(eventName)
  {
    const { eventThrottle } = priorityConfig.thresholds;
    const eventPriority = TelemetryGlobals.eventManager.getPriority(eventName);

    if (eventPriority < eventThrottle)
    {
      const throttleRule = TelemetryGlobals.eventManager.getThrottleRule(eventName);
      return this.applyThrottle(eventName, throttleRule);
    }

    return false;
  }

  isProfileThrottled(eventName, profileName)
  {
    const { profileThrottle } = priorityConfig.thresholds;
    const eventPriority = TelemetryGlobals.eventManager.getPriority(eventName);

    if (eventPriority < profileThrottle)
    {
      const { throttleRule } = this.getProfile(profileName);
      return this.applyThrottle(profileName, throttleRule);
    }

    return false;
  }

  applyThrottle(name, rule)
  {
    try
    {
      if (!rule) return false;
      return TelemetryGlobals.rulesManager.evaluate(name, rule);
    }
    catch (e)
    {
      Logger.error(`applyThrottle: ${e.message}`);
      return false;
    }
  }

  getEmitter(profileName)
  {
    return this.emitters[profileName] || this.addEmitter(profileName);
  }

  addEmitter(profileName)
  {
    const profile = this.getProfile(profileName);
    if (!profile) return null;

    Logger.debug(`Configure the emitter ${profileName}`);

    this.emitters[profileName] = new Emitter(profile, profileName);
    return this.emitters[profileName];
  }

  getProfile(profileName)
  {
    const transportProfile = TelemetryGlobals.profiles[profileName];

    if (!transportProfile)
    {
      Logger.error(`No such profile: ${name}`);
      return null;
    }

    return transportProfile;
  }

  getProfileDefaultAttribute(profileName)
  {
    const defaults = [];

    if (profileName.indexOf('da') >= 0)
    {
      defaults.push('hit.trigger', 'hit.feature');
    }

    return defaults;
  }

  /**
   * Add / merge dataset names to the outbound object
   * @param {Object} outbound - Message going out to analytics server
   * @param {Object} emitter - Corresponding profile to the event triggered
   * @param {String[]} dataSetNames - Dataset belonging to the profile
   */
  addDataSetNames(outbound, emitter, dataSetNames)
  {
    let allDataSetNames = [];

    if (emitter.datasets)
    {
      allDataSetNames = allDataSetNames.concat(emitter.datasets);
    }

    if (dataSetNames)
    {
      allDataSetNames = allDataSetNames.concat(dataSetNames);
    }

    // Remove duplicates (could happen if the event and profile configs specify the same one)
    allDataSetNames = arrayRemoveDuplicates(allDataSetNames);
    Logger.debug(`emitter profileName: ${emitter.profileName}, allDataSetNames: ${JSON.stringify(allDataSetNames)}`);
    this.mergeDataSets(outbound, allDataSetNames);
  }

  mergeDataSets(outbound, dataSetNames)
  {
    if (!dataSetNames) return null;

    Logger.debug(`merge datasets: ${JSON.stringify(dataSetNames)}`);

    for (const datasetName of dataSetNames)
    {
      const dataset = TelemetryGlobals.datasetManager.get(datasetName);
      if (!dataset)
      {
        Logger.warn(`Could not find dataset ${datasetName}; skipping`);
        continue;
      }

      Logger.debug(`merge the ${datasetName} dataset`);
      const datasetContent = dataset.getContent();
      mergeObjects(outbound, datasetContent);
    }
  }
}
