import Logger from '../logger/logger';
import { cloneObject } from '../utils/utils';

export const STORAGE_TYPE = {
  local: 'local',
  session: 'session',
};

// The local storage for user settings
export default class Storage
{
  /**
   * contructor class to initialize storage
   * @param {array}     storageVariables - Array of storage variable names
   * @param {string}    storageType - STORAGE_TYPE: either 'local' (default) or 'session'
   * @returns {void}
   */
  constructor(storageVariables, storageType = STORAGE_TYPE.local)
  {
    this.storageType = storageType;
    this.storageVariables = storageVariables;
  }

  /**
   * initialize
   * @param {function}  onChangeListener - callback function when a storage variable has been changed
   * @returns {void}
   */
  async init(onChangeListener = null)
  {
    this.onChangeListener = onChangeListener;

    // Initialize store variables as member variables to null
    if (!this.storageVariables || !Array.isArray(this.storageVariables))
    {
      Logger.error('Storage variables provided is not of type array.');
      return false;
    }

    // Check Storage Type
    if (!STORAGE_TYPE[this.storageType])
    {
      Logger.error(`Invalid Storage type provided, ${this.storageType}`);
      return false;
    }

    // Initialize each storage variable as null in cache/memory
    this.storageVariables.forEach((variableName) =>
    {
      this[variableName] = null;
    });

    // populate cache variables with storage values
    await this.populateCacheValues();

    // Initialize Storage Listeners
    this.initListeners();
    return true;
  }

  async populateCacheValues()
  {
    const promises = [];
    // Query storage for values
    this.storageVariables.forEach((variableName) =>
    {
      promises.push(StorageHelper.get(variableName, this.storageType).then((result) =>
      {
        const response = {};
        response.variableName = variableName;
        response.result = result;
        return response;
      }));
    });

    const results = await Promise.all(promises);
    // Resolve promise and assign to cache/memory variable
    results.forEach(({ variableName, result }) =>
    {
      if (typeof result !== 'boolean' && typeof result !== 'number')
      {
        result = result || null;
      }
      this[variableName] = result;
    });
  }

  initListeners()
  {
    if (typeof this.onChangeListener !== 'function')
    {
      return;
    }

    if (!chrome.storage[this.storageType].onChanged.hasListener(this.onChangeListener))
    {
      chrome.storage.onChanged.addListener(this.onChangeListener);
    }
  }

  /**
   * Get value(s) from the storage/cache
   * @param  {string|object} db - can be either a string (db key) or an object { key : defaultValue }
   *                         db   string  - store variable name
   *                         db   {object}:
   *                              db.key            - db name to set
   *                              db.defaultValue   - default value to be returned
   *                              Ex, object = { "SOME_DB_VALUE_NAME": true }
   * @param  {any}  defaultValue - (optional) return default value given by the caller (if db value is null) - this only applies to 'string' input
   * @returns {value} - value of what was saved on the db
   */
  get(db, defaultValue = null)
  {
    if (db === null || db === undefined)
    {
      Logger.warn(`Invalid db key when calling 'get' on storage, got: ${db}`);
      return null;
    }

    if (typeof db === 'string')
    {
      return this.getInternal(db, defaultValue);
    }

    if (typeof db === 'object' && !Array.isArray(db))
    {
      const response = {};
      for (const prop in db)
      {
        response[prop] = this.getInternal(prop, db[prop]);
      }

      return response;
    }

    Logger.warn(`Invalid db type when calling 'get', from key: ${db}`);
    return null;
  }

  /**
   * Get the value from the cache
   * @param  {string} db - store variable name
   * @param  {any}  defaultValue - return default value given by the caller (if db is null)
   * @returns {value} - value of what was saved on the db
   */
  getInternal(db, defaultValue)
  {
    const cachedValue = cloneObject(this[db]);

    // if a defaultValue is not provided, then return the cached value
    if (arguments.length < 2) return cachedValue;

    // If a default value is given, then perform a value check for undefined
    // or null, if so then return defaultValue, otherwise, return the cached value
    return (cachedValue === null || cachedValue === undefined) ? defaultValue : cachedValue;
  }

  /**
   * Sets value(s) to local storage and cache
   * @param  {string|object} db - can be either a string (db key) or an object { key : value }
   *                         db   string  - storage variable name
   *                         db   {object}:
   *                              db.key     - db key to set
   *                              db.value   - value to be set
   *                              Ex, object = { "SOME_DB_VALUE_NAME": true }
   * @param  {any}  value - the value of the key to be set (this value will be ignore if first input is an object)
   */
  set(db, value = null)
  {
    if (db === null || db === undefined)
    {
      this.warn(`Invalid db key when calling 'set' on storage, got: ${db}`);
      return null;
    }

    if (typeof db === 'string')
    {
      this.setInternal(db, value);
      return true;
    }

    if (typeof db === 'object' && !Array.isArray(db))
    {
      for (const prop in db)
      {
        this.setInternal(prop, db[prop]);
      }

      return true;
    }

    this.warn(`Invalid db type when calling 'set', from key: ${db}`);
    return null;
  }

  /**
   * Sets the value to local storage and cache
   * @param  {string} db - store variable name
   * @param  {value}  value - value to be set
   */
  setInternal(db, value)
  {
    this[db] = cloneObject(value);
    if (this.storageVariables.includes(db)) StorageHelper.set(db, this.storageType, value);
  }

  /**
   * Removes the db from local storage and sets cache db to null
   * @param  {string | string[]} db - store variable name
   */
  remove(db)
  {
    if (typeof db === 'string' && this[db] !== undefined)
    {
      this[db] = null;
      StorageHelper.remove(db, this.storageType);
    }

    if (Array.isArray(db) && db.length > 0)
    {
      db.forEach((key) => { this[key] = null; });
      StorageHelper.remove(db, this.storageType);
    }
  }

  /**
   * clear all variables, and sets all varibles to null
   * @param  {string} db - store variable name
   */
  clear()
  {
    this.storageVariables.forEach((variableNames) =>
    {
      this[variableNames] = null;
    });

    chrome.storage[this.storageType].clear();
  }
}

class StorageHelper
{
  /**
   * Sets the value to local storage
   * @param  {string} db - store variable name
   * @param  {string} storageType - STORAGE_TYPE - either 'local' or 'session'
   * @param  {value}  value - value to be set
   */
  static set(db, storageType, value)
  {
    chrome.storage[storageType].set({ [db]: value });
  }

  /**
   * Gets the value for the db specified. Async call
   * @param  {string} db - store variable name
   * @param  {string} storageType - STORAGE_TYPE - either 'local' or 'session'
   * @returns {promise} - returns a promise
   */
  static get(db, storageType)
  {
    const handleResponse = (resolve, result) =>
    {
      // Resolve all variables found in storage
      if (db === null)
      {
        resolve(result);
      }

      // Resolve specific property provided by db string
      let value = result[db];
      if (typeof value !== 'boolean' && typeof value !== 'number')
      {
        value = value || null;
      }

      resolve(value);
    };

    return new Promise((resolve) => chrome.storage[storageType].get(db, (result) => handleResponse(resolve, result)));
  }

  /**
   * Removes the db and associated values from local storage
   * @param  {string | string[]} db - store variable name
   */
  static remove(db, storageType)
  {
    chrome.storage[storageType].remove(db);
  }
}
