import Logger from '../Common-Utils-Package/logger/logger';
import GTIBackgroundListener from './gti/gti_background_listener';
import { GTIGlobals } from './gti/gti_globals';
import GTIRequestor from './gti/gti_requestor';

export default class BackgroundGTIRequestor
{
  /**
   * GTI Requestor: constructor
   * @param {object} wasm - wasm engine object
   * @param {function} wasm.wasm_gti_hmac_sha1 - should contain function to hmac_sha1 for gti
   * @param {object} options - object containinig optional params that alter the behavior of this lib
   * @param {string} options.clientIdGuid - client id passed as part of the gti request body (default as: empty string)
   * @param {string} options.serverUrl - url string to the gti server (default as: 'https://webadvisorc.rest.gti.mcafee.com/1')
   * @param {string} options.cspid - cspid use for telemetry (default as: '__not_available__')
   * @param {string} options.productVersion - product version for gti call (default as: '0.0.0.0')
   */
  constructor(wasm, options)
  {
    this.promisesToComplete = null;

    GTIGlobals.gtiRequestor = new GTIRequestor(wasm, options);
  }

  /**
   * TODO: can be removed when we fully transition to HTI from GTI
   * This function returns the API type
   * @param {string} returns 'GTI'
   */
  getType()
  {
    return 'GTI';
  }

  async init()
  {
    this.promisesToComplete = new Promise(async (resolve) =>
    {
      const gtiRequestInit = await GTIGlobals.gtiRequestor.init();
      if (!gtiRequestInit)
      {
        Logger.error('Failed to initialize GTI Requestor.');
      }

      resolve();
    });

    const gtiListener = new GTIBackgroundListener(this.promisesToComplete);
    gtiListener.init();

    await this.promisesToComplete;
  }

  /**
   * This function allows modifying the global variables
   * ie: globalValues = { serverUrl: 'https://someurl.com', otherGlobal: 'somedata' }
   * @param {object} globalValues
   * @param {key} globalsValues.key
   * @param {value} globalsValues.value
   */
  setGlobals(globalValues)
  {
    GTIGlobals.gtiRequestor.setGlobals(globalValues);
  }

  /**
   * Does post to GTI splitting the requests as per guidelines to 5 questions per request.
   * @param {object} requestData      - Array of request URLs whose reputation is asked to GTI
   * @param {string} referrer         - Value of referer header (or undefined)
   * @param {boolean} checkCacheOnly  - Check Cache only for GTI, do not make HTTP GTI request
   */
  async post(requestData, referrer = null, checkCacheOnly = false)
  {
    await this.promisesToComplete;

    if (!GTIGlobals || !GTIGlobals.gtiRequestor)
    {
      Logger.error('Error to post GTI request has GTIRequestor has not yet been initialized');
      return null;
    }

    const options = { referrer, checkCacheOnly };
    const promise = new Promise((resolve) => GTIGlobals.gtiRequestor.post(requestData, resolve, options));

    const result = await promise;
    return result;
  }

  async clearCache()
  {
    GTIGlobals.gtiRequestor.getCache().clear();
  }

  setReputation(url, value)
  {
    GTIGlobals.gtiRequestor.getCache().setReputation(url, value);
  }

  getReputation(url)
  {
    return GTIGlobals.gtiRequestor.getCache().getReputation(url);
  }
}
