import Logger from '../logger';
import TransportBase from './transport_base';
import { TelemetryGlobals } from '../telemetry_globals';

export default class TransportEventHub extends TransportBase
{
  constructor()
  {
    super();
    this.apiVersion = null;
    this.servicebusNamespace = null;
    this.eventHubPath = null;
    this.sharedAccessKey = null;
    this.sharedAccessName = null;
    this.sharedAccessToken = null;
    this.tokenCreationTime = null;
    this.timeout = 60;
    this.url = TelemetryGlobals.url !== null ? TelemetryGlobals.url : 'https://{servicebusNamespace}.servicebus.windows.net/{eventHubPath}/messages?timeout={timeout}&api-version={apiVersion}';
  }

  appendDottedKeys(eventData)
  {
    const data = { ...eventData };
    const attributesToConvert = [
      'csp_clientid',
      'event_category',
      'event_action',
      'event_label',
      'engagement_interactive',
      'engagement_userinitiated',
      'hit_type',
      'hit_uniqueid',
      'hit_severity',
      'hit_label_1',
      'hit_label_2',
      'device_antimalware_provider_enabled',
      'device_os_type',
      'wss_istrial'
    ];

    // Replacing underscores with dots
    for (const attr of attributesToConvert)
    {
      if (data.hasOwnProperty(attr))
      {
        data[attr.replace(/_/g, '\.')] = data[attr];
      }
    }

    return data;
  }

  async createSharedAccessToken(uri, saName, saKey)
  {
    if (!uri || !saName || !saKey)
    {
      throw 'Missing required parameter';
    }

    const encoded = encodeURIComponent(uri);
    const now = new Date();
    const week = 60 * 60 * 24 * 7;
    const ttl = Math.round(now.getTime() / 1000) + week;
    const signature = `${encoded}\n${ttl}`;
    const textEncoder = new TextEncoder('utf-8');

    const key = await crypto.subtle.importKey(
      'raw', // raw format of the key - type Uint8Array
      textEncoder.encode(this.sharedAccessKey),
      { // algorithm details
        name: 'HMAC',
        hash: { name: 'SHA-256' }
      },
      false, // export = false
      ['sign', 'verify'] // what the key can do
    );

    const arrayBuffer = await crypto.subtle.sign('HMAC', key, textEncoder.encode(signature));
    const hash = btoa(String.fromCharCode(...new Uint8Array(arrayBuffer)));

    return `SharedAccessSignature sr=${encoded}&sig=${encodeURIComponent(hash)}&se=${ttl}&skn=${saName}`;
  }

  /**
   * Send the event data to the analytics server
   * https://docs.microsoft.com/en-us/rest/api/eventhub/send-event
   * @param {String} eventData - Outbound object
   */
  async send(eventDataStr)
  {
    const cleanEventData = this.sanitizeEventData(eventDataStr);
    const eventData = TelemetryGlobals.dictionaryMapper.map(this.dictionary, cleanEventData, false);
    const mappedEventData = this.appendDottedKeys(eventData);

    Logger.debug(`Event_Hub_Transport::Send: Sending using uri: ${this.url}`);

    const sendStatusCode = await this.sendHttpRequest(mappedEventData);

    if (sendStatusCode !== 201)
    {
      Logger.error(`Event_Hub_Transport.Send: Failed to send. Send status code: ${sendStatusCode}`);
    }
    else
    {
      Logger.debug(`Event_Hub_Transport.Send: sendStatusCode ${sendStatusCode}`);
    }

    const uniqueId = cleanEventData['hit.uniqueid'];
    Logger.debug(`${uniqueId} sent to ${this.name}: ${JSON.stringify(mappedEventData)}`);

    return sendStatusCode;
  }

  async sendHttpRequest(eventData)
  {
    try
    {
      const token = await this.createSharedAccessToken(this.url, this.sharedAccessName, this.sharedAccessKey);
      const response = await fetch(this.url, {
        method: 'POST',
        headers: {
          Authorization: token,
          'Content-Type': 'application/atom+xml;type=entry;charset=utf-8',
          Host: `${this.servicebusNamespace}.servicebus.windows.net`,
          BrokerProperties: JSON.stringify({ PartitionKey: TelemetryGlobals.engine.getContextId() })
        },
        body: JSON.stringify(eventData)
      });

      return response.status;
    }
    catch (e)
    {
      Logger.error(e.message);
      return 0;
    }
  }

  /**
   * Lowercase the object keys
   * @param {Object} eventData - Outbound object
   */
  sanitizeEventData(eventData)
  {
    const sanitizedEvent = {};

    Object.keys(eventData).forEach((key) =>
    {
      sanitizedEvent[key.toLowerCase()] = eventData[key];
    });

    return sanitizedEvent;
  }

  setup()
  {
    this.apiVersion = this.transportConfig.apiVersion;
    if (!this.apiVersion)
    {
      Logger.error('Event_Hub_Transport:: Initialize Invalid (unspecified) _apiVersion');
      return false;
    }

    this.servicebusNamespace = this.transportConfig.servicebusNamespace;
    if (!this.servicebusNamespace)
    {
      Logger.error('Event_Hub_Transport:: Initialize Invalid (unspecified) _servicebusNamespace');
      return false;
    }

    this.eventHubPath = this.transportConfig.eventHubPath;
    if (!this.eventHubPath)
    {
      Logger.error('Event_Hub_Transport:: Initialize Invalid (unspecified) _eventHubPath');
      return false;
    }

    this.sharedAccessKey = this.transportConfig.sharedAccessKey;
    if (!this.sharedAccessKey)
    {
      Logger.error('Event_Hub_Transport:: Initialize Invalid (unspecified) sharedAccessKey');
      return false;
    }

    this.sharedAccessName = this.transportConfig.sharedAccessName;
    if (!this.sharedAccessName)
    {
      Logger.error('Event_Hub_Transport:: Initialize Invalid (unspecified) sharedAccessName');
      return false;
    }

    this.tokenRefreshTime = this.transportConfig.tokenRefreshTime;
    if (!this.tokenRefreshTime)
    {
      this.tokenRefreshTime = 1;// hour
    }

    this.updateURL('{servicebusNamespace}', this.servicebusNamespace);
    this.updateURL('{eventHubPath}', this.eventHubPath);
    this.updateURL('{timeout}', this.timeout);
    this.updateURL('{apiVersion}', this.apiVersion);

    return true;
  }

  updateURL(replacementKey, replacementValue)
  {
    this.url = this.url.replace(replacementKey, replacementValue);
  }
}
