import Logger from '../../../Common-Utils-Package/logger/logger';
import ScheduledTaskBase from '../../../Common-Utils-Package/scheduler/scheduled_task_base';
import { HTIGlobals } from '../hti_globals';
import { HTIStorageVariables } from '../hti_storage_variables';

export default class CSPTokenRefresh extends ScheduledTaskBase
{
  async init(name, fsetTTL)
  {
    this.retryIntervals = [30, 30, 1 * 60, 3 * 60, 5 * 60, 15 * 60, 30 * 60]; // Retry after 30 seconds twice and then after 1 minute, 3 minute etc. AFter 30 minute then go to usual schedule frequency.
    this.retryIndex = 0;
    this.name = name;
    this.fsetTTL = fsetTTL;
    this.keys = await this.getCSPKeys();
    this.appKey = this.keys.appKey;
    this.sharedKey = this.keys.sharedKey;
    this.body = JSON.stringify({ grant_type: 'client_credentials', client_id: HTIGlobals.cspClientId, claims: { csp_client_id: HTIGlobals.cspClientId } });
  }

  // fetch new csp token
  run()
  {
    return this.fetchToken();
  }

  async getCSPKeys()
  {
    const keys = {
      appKey: await HTIGlobals.wasm.get_wa_app_key('WA', 'APP_KEY'),
      sharedKey: await HTIGlobals.wasm.get_wa_shared_key('WA', 'SHARED_KEY')
    };

    return keys;
  }

  getNextRetryInterval()
  {
    if (this.retryIndex >= this.retryIntervals.length - 1)
    {
      // revert to default interval
      return 24 * 60 * 60; // i.e. 1 day 24 hours in seconds
    }
    ++this.retryIndex;
    return this.retryIntervals[this.retryIndex];
  }

  async fetchToken()
  {
    /* eslint-disable */
    const body = this.body;
    /* eslint-disable */

    const reqParams = {
      HttpMethod: 'POST',
      Body: body,
      ClientId: HTIGlobals.cspClientId,
      WA: true
    };

    const authContext = await HTIGlobals.wasm.generate_value_for_ac(JSON.stringify(reqParams));

    try
    {
      const initBody = {
        method: 'POST',
        headers: {
          ac: authContext,
          ak: this.appKey,
          cd: HTIGlobals.cspClientId,
          st: 1,
          sv: 'V1',
          'Content-Type': 'application/json',
        },
        body

      };
      const response = await fetch(HTIGlobals.cspTokenUrl, initBody);
      if (response !== null && response.status === 200)
      {
        const cspToken = await response.json();
        if (cspToken && cspToken.access_token)
        {
          const tokenData = { access_token: cspToken.access_token, access_token_ttl: cspToken.access_token_ttl, token_type: cspToken.token_type };
          HTIGlobals.storage.set(HTIStorageVariables.CSP_TOKEN_HTI, tokenData)
          
          const everGreenTask = HTIGlobals.scheduleManager.getTask(HTIStorageVariables.EVER_GREEN_URL_REFRESH);
          if (everGreenTask !== null)
          {
            everGreenTask.enabled = true;
          }
          // The value in response is in seconds. Converting to milliseconds for scheduler manager.
          // Lets set the ttl to be 15 minutes before expiry so that we keep customers protected better.
          const fifteenMinutes = 60 * 15;

          const ttl = cspToken.access_token_ttl <= fifteenMinutes ? cspToken.access_token_ttl : cspToken.access_token_ttl - fifteenMinutes;
          this.fsetTTL(this.name, ttl * 1000);
          return true;
        }
      }
    }
    catch (e)
    {
      Logger.error(e.message);
    }

    const retryInterval = this.getNextRetryInterval();
    this.fsetTTL(this.name, retryInterval * 1000);
    return false;
  }
}
