import { BlockState, WPSBlockPageType } from '@extension/common/constants';
import { BackgroundGlobals } from './background_globals';

export default class BlockPageCommon
{
  /**
   * TODO
   * @param {Number} statusOfBlocking
   * @return {object} return object containing url to the block page
   */
  static toBlockPageUrl(statusOfBlocking)
  {
    const states = [...BlockState];
    let blockPageUrl = null;

    if (states.includes(statusOfBlocking))
    {
      const filename = BlockPageCommon.resolveBlockPage(statusOfBlocking);
      blockPageUrl = chrome.runtime.getURL(filename);
    }

    return { url: blockPageUrl };
  }

  /**
   * Retrieve the corresponding block page's internal url.
   * @param {number} state - The current state of the web page
   */
  static resolveBlockPage(state)
  {
    let blockPage = null;

    switch (state)
    {
      case BlockState.YELLOW:
      case BlockState.RED:
      case BlockState.PHISHING:
      {
        blockPage = 'html/site_status_block_page.html';
        break;
      }
      case BlockState.TYPOSQUATTING:
        blockPage = 'html/site_status_typosquatting.html';
        break;
    }

    return blockPage;
  }

  /**
   *
   * @param {*} typeOfBlockPage is the WPS type of block page 1 = red, 2 = yellow, 3 = phishing, 4 = typosquat, 5 = iframe, 6 = crypto
   * @param {*} topUrl the top page url that the contains the blocked url on its webpage
   * @param {*} blockedUrl the url that the block page is blocking
   * @param {*} overriden if the user previously told the extension to not show block page
   */
  static sendBlockPageEvent(typeOfBlockPage, topUrl, blockedUrl, overriden)
  {
    BackgroundGlobals.wssMessageDispatcher.sendBlockPageEvent(typeOfBlockPage, topUrl, blockedUrl, overriden);
  }

  /**
   *
   * @param {*} statusOfBlocking is BlockState from constants.js
   * @param {*} topUrl the url that the block page is blocking
   * @param {*} overriden if the user previously told the extension to not show block page
   */
  static checkWhitelistedBlockpage(statusOfBlocking, referrer, blockUrl, overriden)
  {
    let wpsBlockType = -1;
    switch (statusOfBlocking)
    {
      case BlockState.YELLOW:
      case BlockState.RED:
      case BlockState.PHISHING:
        wpsBlockType = BlockPageCommon.convertWABlockTypeToWPS(statusOfBlocking);
        break;
    }
    if (wpsBlockType !== -1)
    {
      const topURL = referrer || blockUrl;
      BlockPageCommon.sendBlockPageEvent(wpsBlockType, topURL, blockUrl, overriden);
    }
  }

  static convertWABlockTypeToWPS(statusOfBlocking)
  {
    let type = -1;
    switch (statusOfBlocking)
    {
      case BlockState.YELLOW:
        type = WPSBlockPageType.Yellow;
        break;
      case BlockState.RED:
        type = WPSBlockPageType.Red;
        break;
      case BlockState.PHISHING:
        type = WPSBlockPageType.Phishing;
        break;
      case BlockState.TYPOSQUATTING:
        type = WPSBlockPageType.Typoquatting;
        break;
    }
    return type;
  }
}
