// Common
import { extension } from '@extension/common/webextension';
// Telemetry
import { InteractionTypes, EventNames } from '@extension/telemetry/common_telemetry';
import ContextTelemetry from '@extension/telemetry/context_telemetry';
import { BackgroundGlobals } from '../background_globals';

export default class BrowserActionListener
{
  async browserAction({ id, url })
  {
    // For Handling Telemetry
    BrowserActionListener.sendTelemetry();

    BackgroundGlobals.messageDispatcher && BackgroundGlobals.messageDispatcher.showSidebarMain(id, url);
  }

  static sendTelemetry()
  {
    const sidebarTelemetry = {
      identifier: EventNames.SIDEBAR_PANEL,
      interaction_type: InteractionTypes.SIDEBAR_PANEL.MAIN,
      isFromBackground: true
    };
    ContextTelemetry.handle(sidebarTelemetry);

    const actionClickTelemetry = {
      identifier: EventNames.BROWSER_ACTION_CLICKED,
      count: 0,
      isFromBackground: true
    };
    ContextTelemetry.handle(actionClickTelemetry);
  }

  init()
  {
    extension.action.onClicked.addListener(this.browserAction);
  }
}
