/* eslint-disable camelcase */
// c++ is sending in snakecase, change to camel case here later

// Common
import { BackgroundGlobals } from '@extension/background/background_globals';
import SmartNativeClient from '@mcafee/browser-host/smartnativeclient';
import { Logger } from '@extension/common/logger';
import { LocalStorageVariables } from '@extension/common/cache_store_variables';
import MTIInitializer from '@extension/background/mti_initializer';

const WSSExtensionCommands = {
  HANDSHAKE_GREETING: 8,
  SUBSCRIBE: 116,
  SET: 117,
};

export const WSSNativeCommands = {
  PUBLISH: 115,
  SUBSCRIBE: 116,
  SET: 117,
  GET: 118,
};

export const SubRequestTypes = {
  WA_EVENT: 'WA.EVENT',
  SET_SETTINGS_EXTN: 'SET.SETTINGS.TO.EXTN',
};

/**
 * Dispatcher class handling all communication between extension and WSS native.
 * Both extension and native maintain a persistent connection.
 */
export default class NativeWSSMessageDispatcher
{
  constructor()
  {
    this.wssClient = null;
  }

  /**
   * Initialize the dispatcher class, setting up the event listeners and initializing setting variables
   * @param {Object} port - Runtime port object
   * @param {String} initialMessage - TODO: Comment
   */
  init()
  {
    try
    {
      this.connectToWssPort();
      this.sendSubscribeRequest();
      this.sendGetSettingsRequest();
    }
    catch (e)
    {
      Logger.error(e.message);
    }
  }

  connectToWssPort()
  {
    /**
     * Incoming messages from wss browserhost
     * @returns {Boolean} Return false to disallow forwarding of message to other extensions
     */
    const onWssMessage = (message) =>
    {
      this.processMessage(message);
      return false;
    };

    /**
     * When external extension or wss browserhost disconnected from long-lived connection
     */
    const onWssDisconnectEvent = (port) =>
    {
      Logger.warn(`Error connecting to wss host: ${port.error}`);
      if (BackgroundGlobals.telemetryEngine)
      {
        BackgroundGlobals.telemetryEngine.updateContext('wss_installed', 'false');
      }
    };

    // wss browserhost connection
    this.wssClient = new SmartNativeClient('wss.mcafee.chrome.extension', null, onWssMessage, onWssDisconnectEvent, Logger);
    this.wssClient.init(async (status) =>
    {
      if (BackgroundGlobals.telemetryEngine)
      {
        BackgroundGlobals.telemetryEngine.updateContext('wss_installed', status.connected.toString());
      }

      if (!status.connected)
      {
        Logger.warn('Failed to connect to wss host wss.mcafee.chrome.extension');
        return;
      }

      const wssResponse = await this.getWssParams();
      const { csp_client_id: cspClientId,
        provision_id: provisionId,
        entitled_products: entitedProducts } = wssResponse;

      if (!cspClientId || !provisionId || !entitedProducts)
      {
        BackgroundGlobals.IDPS_IS_ENTITLED = false;
      }
      else
      {
        BackgroundGlobals.IDPS_CSP_CLIENT_ID = cspClientId;
        BackgroundGlobals.IDPS_PROVISION_ID = provisionId;
        BackgroundGlobals.IDPS_IS_ENTITLED = entitedProducts.includes('IDPS');
      }
    });
  }

  sendSubscribeRequest()
  {
    this.postMessage({
      request_type: WSSNativeCommands.SUBSCRIBE,
      payload:
      {
        sub_request_type: SubRequestTypes.SET_SETTINGS_EXTN
      }
    });
  }

  sendGetSettingsRequest()
  {
    this.postMessage({
      request_type: WSSNativeCommands.GET,
      payload:
      {
        keys: Array.from(BackgroundGlobals.wpsRequestedSettings)
      }
    });
  }

  postMessage(message)
  {
    this.wssClient.postMessage(message);
  }

  getWssParams()
  {
    return new Promise((resolve) =>
    {
      const fn = (res) => resolve(res);
      this.wssClient.postMessage({
        request_type: 108,
        id: 0,
        payload: {
          app_id: 'a053060c-3a34-11e4-8a01-005056b7244f'
        },
      }, fn);
    });
  }

  /**
   * Handles the default behaviour for messages being returned or captured by the "onMessage" event listener.
   * @param {Object} nativeResponse - JSON serialized object containing information send from the native app
   */
  processMessage(nativeResponse)
  {
    const message = JSON.stringify(nativeResponse);

    try
    {
      this.processInwardMessage(nativeResponse);
    }
    catch (error)
    {
      Logger.log(`Native json message may be missing some keys: ${message} + ${error.message} `);
    }
  }

  /**
   * Parse the incoming JSON serialized message from the native app.
   * @param {Object} nativeMessage - JSON serialized object which was sent from native
   */
  processInwardMessage(nativeMessage)
  {
    const { payload, request_type } = nativeMessage;
    const commandName = Object.keys(WSSExtensionCommands).find((key) => WSSExtensionCommands[key] === request_type);
    Logger.log(`Processing message from binary in wps handler: ${request_type} (${commandName})\n${JSON.stringify(payload)}`);

    switch (request_type)
    {
      case WSSExtensionCommands.HANDSHAKE_GREETING:
        this.processHandshake(payload);
        break;
      case WSSExtensionCommands.SET:
        this.setRequestType(payload);
        break;
    }
  }

  processHandshake(payload)
  {
    const { version } = payload;
    if (version && version.startsWith('WPS'))
    {
      // Initialize HTI here if native flags are read from WA Native Host already or if HTI is not yet initialized.
      BackgroundGlobals.isWPSInstalled = true;
      MTIInitializer.init();
    }
    else
    {
      BackgroundGlobals.WSS_INSTALLED = true;
    }
  }

  setRequestType(payload)
  {
    const { key, value } = payload;
    let newValue = value;
    const localStorageKeys = Object.keys(LocalStorageVariables);
    if (localStorageKeys.indexOf(key) !== -1)
    {
      const settingValue = BackgroundGlobals.cacheStore.get(key);
      const typeOfValue = typeof settingValue;

      if (typeOfValue === 'number')
      {
        newValue = Number(value);
      }
      Logger.log(`[WPS Native] Recieved a set request from WPS Native ${JSON.stringify(payload)}`);
      BackgroundGlobals.cacheStore.set(key, newValue);
    }
  }

  setSettings(settingName, settingValue)
  {
    // append the browserType at the end to indicate which browser is being used
    if (settingName === 'SEARCH_ANNOTATION_OPTION' || settingName === 'SMA_ANNOTATION_OPTIONS')
    {
      settingName = `${settingName}_${BackgroundGlobals.browserType}`;
    }
    const strSettingValue = settingValue.toString();
    const payload = {
      key: settingName,
      value: strSettingValue,
    };
    const request = {
      request_type: WSSNativeCommands.SET,
      payload
    };
    Logger.log(`[WPS Native] Sent settings event to WPS Native ${JSON.stringify(request)}`);
    this.postMessage(request);
  }
}
