// The local storage for user settings
import { extension } from '@extension/common/webextension';
import { LocalStorageVariables, CacheStorageVariables } from '@extension/common/cache_store_variables';
import { BackgroundGlobals } from './background_globals';

export class CacheStore
{
  constructor()
  {
    this.onChangedListener = this.getOnChangedListener();
    // Initialize store variables as member variables to null
    for (const variable of Object.values(LocalStorageVariables)) this[variable.name] = null;
    for (const variable of Object.values(CacheStorageVariables)) this[variable.name] = null;

    this.init();
    this.initListeners();
  }

  async init()
  {
    const promises = [];
    // Get data from local storage and assign
    for (const storeVariable of Object.values(LocalStorageVariables))
    {
      promises.push(LocalStorage.get(storeVariable.name).then((result) =>
      {
        const response = {};
        response.storeVariable = storeVariable;
        response.result = result;
        return response;
      }));
    }

    // Get cache storage data from local storage and assign
    for (const storeVariable of Object.values(CacheStorageVariables))
    {
      promises.push(LocalStorage.get(storeVariable.name).then((result) =>
      {
        const response = {};
        response.storeVariable = storeVariable;
        response.result = result;
        return response;
      }));
    }

    const results = await Promise.all(promises);
    results.forEach(({ storeVariable, result }) =>
    {
      if (typeof result !== 'boolean' && typeof result !== 'number')
      {
        result = result || null;
      }
      this[storeVariable.name] = result;
    });
  }

  initListeners()
  {
    if (!extension.storage.local.onChanged.hasListener(this.onChangedListener))
    {
      extension.storage.onChanged.addListener(this.onChangedListener);
    }
  }

  getOnChangedListener()
  {
    const onChanged = (setting) =>
    {
      const settingName = Object.keys(setting);
      if (setting[settingName].oldValue === setting[settingName].newValue)
      {
        return;
      }
      if (BackgroundGlobals.wpsRequestedSettings.has(settingName[0]))
      {
        BackgroundGlobals.wssMessageDispatcher.setSettings(settingName[0], setting[settingName].newValue);
      }
    };
    return onChanged;
  }

  /**
   * Get the value from the cache
   * @param  {string} db - store variable name
   * @param  {any}  defaultValue - return default value given by the caller (if db is null)
   * @returns {value} - value of what was saved on the db
   */
  get(db, defaultValue)
  {
    const cachedValue = this[db];

    // if a defaultValue is not provided, then return the cached value
    if (arguments.length < 2) return cachedValue;

    // If a default value is given, then perform a value check for undefined
    // or null, if so then return defaultValue, otherwise, return the cached value
    return (cachedValue === null || cachedValue === undefined) ? defaultValue : cachedValue;
  }

  /**
   * Sets the value to local storage and cache
   * @param  {string} db - store variable name
   * @param  {value}  value - value to be set
   */
  set(db, value)
  {
    this[db] = value;
    if (LocalStorageVariables[db] || CacheStorageVariables[db]) LocalStorage.set(db, value);
  }

  /**
   * Removes the db from local storage and sets cache db to null
   * @param  {string} db - store variable name
   */
  clear(db)
  {
    if (this[db] === undefined) return;

    this[db] = null;
    if (LocalStorageVariables[db] || CacheStorageVariables[db]) LocalStorage.clear(db);
  }

  /**
   * Gets mutliple values to the local storage/cache
   * @param  {object} data - store variable name
   *          - Key : db value to get
   *          - Value: default value to retrieve if not in db
   * Ex, data = { "SOME_DB_VALUE_NAME": false }
   */
  getMultiple(data)
  {
    for (const prop in data)
    {
      data[prop] = this.get(prop, data[prop]);
    }
    return data;
  }

  /**
   * Sets mutliple values to the local storage/cache
   * @param  {object} data - store variable name
   *          - Key : db name to set
   *          - Value: value of the db name
   * Ex, data = { "SOME_DB_VALUE_NAME": true }
   */
  setMultiple(data)
  {
    for (const prop in data)
    {
      this.set(prop, data[prop]);
    }
  }

  clearSessionVariables()
  {
    for (const value of Object.values(CacheStorageVariables))
    {
      this.clear(value.name);
    }
  }
}

export class LocalStorage
{
  /**
   * Sets the value to local storage
   * @param  {string} db - store variable name
   * @param  {value}  value - value to be set
   */
  static set(db, value)
  {
    extension.storage.local.set({ [db]: value });
  }

  /**
   * Gets the value for the db specified. Async call
   * @param  {string} db - store variable name
   * @returns {promise} - returns a promise
   */
  static get(db)
  {
    const handleResponse = (resolve, result) =>
    {
      // Resolve all variables found in storage
      if (db === null)
      {
        resolve(result);
      }

      // Resolve specific property provided by db string
      let value = result[db];
      if (typeof value !== 'boolean' && typeof value !== 'number')
      {
        value = value || null;
      }

      resolve(value);
    };

    return new Promise((resolve) => extension.storage.local.get(db, (result) => handleResponse(resolve, result)));
  }

  /**
   * Removes the db and associated values from local storage
   * @param  {string} db - store variable name
   */
  static clear(db)
  {
    extension.storage.local.remove(db);
  }
}
