import Logger from '../../../Common-Utils-Package/logger/logger';
import ScheduledTaskBase from '../../../Common-Utils-Package/scheduler/scheduled_task_base';
import { HTIGlobals } from '../hti_globals';
import { generateHeaders } from '../hti_headers';
import { HTIStorageVariables } from '../hti_storage_variables';

export default class EverGreenUrlsRefresh extends ScheduledTaskBase
{
  init(name, fsetTTL)
  {
    this.name = name;
    this.fsetTTL = fsetTTL;
  }

  // fetch latest cache of Ever green URLs
  async run()
  {
    const everGreenURLs = HTIGlobals.storage.get(HTIStorageVariables.EVER_GREEN_URLS, null);
    if (!everGreenURLs || Date.now() > everGreenURLs.cacheTTL + everGreenURLs.timeCached)
    {
      await this.fetchEverGreenURLs();
    }
  }

  /**
  * Ever Green URL list
  */
  async fetchEverGreenURLs()
  {
    try
    {
      const headers = generateHeaders();
      const response = await fetch(HTIGlobals.serverUrl + HTIGlobals.urlEverGreen, {
        method: 'GET',
        headers
      });

      if (response !== null && response.status === 200)
      {
        const result = await response.json();

        if (result.cacheid === 'url-evergreen')
        {
          // store url cache with TTL
          const data = { cacheTTL: result.ttl, timeCached: Date.now(), content: result.content };
          HTIGlobals.storage.set(HTIStorageVariables.EVER_GREEN_URLS, data);
        }
      }
    }
    catch (e)
    {
      Logger.error(e.message);
    }
  }
}
