import { BackgroundGlobals } from '@extension/background/background_globals';
import { schedulers } from '@extension/background/schedulers/schedulers';
import { extension } from '@extension/common/webextension';
import { Logger } from '@extension/common/logger';

export default class ScheduleManager
{
  constructor()
  {
    this.schedulers = schedulers;
    this.alarmListener = this.getAlarmListener();
    this.setTTL = this.setTTL.bind(this);
  }

  getAlarmListener()
  {
    const alarmListener = (alarm) =>
    {
      for (const [taskName, task] of Object.entries(this.schedulers))
      {
        if (alarm.name === taskName)
        {
          Logger.log(`Running task ${taskName}`);
          try
          {
            task.task.run(); // Run the current task and schedule next task.
          }
          catch (e)
          {
            Logger.error(e.message);
          }
          this.setLastRunTime(taskName);
          this.scheduleTask();
        }
      }
    };

    return alarmListener;
  }

  setTTL(taskName, ttl)
  {
    BackgroundGlobals.cacheStore.set(`${taskName}_ttl`, { expiry: ttl });
    this.reinit();
  }

  getTTL(taskName)
  {
    const expiry = BackgroundGlobals.cacheStore.get(`${taskName}_ttl`, { expiry: 0 });
    if (expiry !== null)
    {
      return expiry.expiry;
    }
    return 0;
  }

  getLastRunTime(taskName)
  {
    // Get from local storage the last run time of this task.
    const lastRun = BackgroundGlobals.cacheStore.get(taskName, { lr: -1 });
    return lastRun.lr;
  }

  setLastRunTime(taskName)
  {
    // Set the last time that this task ran
    BackgroundGlobals.cacheStore.set(taskName, { lr: Date.now() });
  }

  scheduleTask()
  {
    let taskToScheduleName = null;
    let lowestNextRun = -1;
    for (const [taskName, task] of Object.entries(this.schedulers))
    {
      if (task.enabled === false)
      {
        continue;
      }

      const lastRunTime = this.getLastRunTime(taskName);

      let nextRunTime = 0;

      const freq = this.getTTL(taskName) || task.frequency;
      // If the task was never run
      if (lastRunTime === -1)
      {
        // If the task should be run after the interval even for the first time then.
        if (!task.runImmediatelyInitially)
        {
          nextRunTime = Date.now() + freq;
          // Sets last run time as today and now initially so that we donot push the run each time
          // if the interval was never hit.
          this.setLastRunTime(taskName);
        }
      }
      else
      {
        nextRunTime = lastRunTime + freq;// Last time task was run plus frequency tells new run time.
      }

      if (nextRunTime <= Date.now()) // If next run time is already due then run it immediately so break out of loop
      {
        taskToScheduleName = taskName;
        lowestNextRun = 0; // indicate to run immediately
        break;
      }
      else
      {
        const taskNextFreq = nextRunTime - Date.now();
        // If lowestNextRun was never calculated OR if previous lowestRun calculated is higher than current one
        if (lowestNextRun === -1 || lowestNextRun > taskNextFreq)
        {
          taskToScheduleName = taskName;
          lowestNextRun = taskNextFreq;
        }
      }
    }

    // run immediately for due task
    if (lowestNextRun === 0)
    {
      Logger.log(`Next task to run is ${taskToScheduleName} at 0 minute(s)`);
      Logger.log(`Running task ${taskToScheduleName}`);
      this.schedulers[taskToScheduleName].task.run();
      this.setLastRunTime(taskToScheduleName);
      this.scheduleTask();

      return;
    }

    // default set to Chrome minimum alarm interval 1 minute
    let alarmInterval = 1;

    // if the actual next run is more than 1 minute then convert it into minutes,
    // otherwise use default 1 minute interval
    if (lowestNextRun > 1000 * 60)
    {
      alarmInterval = parseInt(lowestNextRun / 1000 / 60, 10);
    }
    Logger.log(`Next task to run is ${taskToScheduleName} at ${alarmInterval} minute(s)`);
    extension.alarms.create(taskToScheduleName, { delayInMinutes: alarmInterval });
  }

  initListeners()
  {
    // avoid adding multiple listener and fired multiple times
    if (!chrome.alarms.onAlarm.hasListener(this.alarmListener))
    {
      chrome.alarms.onAlarm.addListener(this.alarmListener);
    }
  }

  clearAlarms()
  {
    for (const [taskName, task] of Object.entries(this.schedulers))
    {
      if (task.enabled === false)
      {
        continue;
      }
      extension.alarms.clear(taskName);
    }
  }

  clearListeners()
  {
    if (extension.alarms.onAlarm.hasListener(this.alarmListener))
    {
      extension.alarms.onAlarm.removeListener(this.alarmListener);
    }
  }

  stop()
  {
    this.clearAlarms();
    this.clearListeners();
  }

  reinit()
  {
    this.init();
  }

  init()
  {
    this.clearAlarms();

    for (const [taskName, task] of Object.entries(this.schedulers))
    {
      try
      {
        task.task.init(taskName, this.setTTL);
      }
      catch (e)
      {
        Logger.error(e.message);
      }
    }
    this.initListeners();
    this.scheduleTask();
  }

  getTask(taskName)
  {
    const taskKey = Object.keys(this.schedulers).find((taskKey) => taskKey === taskName);
    return this.schedulers[taskKey] || null;
  }
}
