/**
 * To know which is the active tab at any point in time. From background script if we call to get active tab it might not be obtained,
 * so this event is required to get which is the active tab. The active tab id is then stored in globals.
 */
// Common
import { BrowserType, BlockState, BuildSettings, ExecuteCommands, Color } from '@extension/common/constants';
import { CacheStorageVariables } from '@extension/common/cache_store_variables';
import { extension } from '@extension/common/webextension';
import { Logger } from '@extension/common/logger';
// Background
import BackgroundCommon from '@extension/common/background_common';
import { BackgroundGlobals } from '@extension/background/background_globals';
import BrowserAction from '@extension/background/browser_action';
import BlockPageCommon from '@extension/background/block_page_common';
import WebRequestDispatcher, { WebRequestHelper } from '@extension/background/dispatchers/webrequest_dispatcher';

/**
 * Fires when the tab is created.
 * @param {Object} tab - Details about the tab that was created
 */
function onTabCreatedHandler(tab)
{
  if (!BackgroundCommon.isPersonalPolicyEnabled())
  {
    return;
  }

  let args = {};
  let shouldSendNewTabEvent = true;

  if (BrowserType.FIREFOX === BuildSettings.BROWSER_TYPE)
  {
    if (BackgroundGlobals.ffViewports !== null)
    {
      args = BackgroundGlobals.ffViewports;
    }
    else
    {
      // We dont have the firefox viewports yet
      Logger.warn('Warning: raising new tab without firefox viewports');
    }
  }

  if (BrowserType.CHROME === BuildSettings.BROWSER_TYPE)
  {
    const { pendingUrl } = tab;
    const whitelist = [
      'https://chrome.google.com/webstore/detail/mcafee%C2%AE-secure-search/enppghjcblldgigemljohkgpcompnjgh',
    ];

    if (pendingUrl && (BackgroundCommon.isExtensionUrl(pendingUrl) || whitelist.includes(pendingUrl)))
    {
      shouldSendNewTabEvent = false;
    }
  }

  if (shouldSendNewTabEvent) BackgroundGlobals.messageDispatcher.executeCommand({ command_id: ExecuteCommands.RAISE_NEW_TAB_EVENT, args });
}

/**
 * Fired multiple times when the tab is updated (user navigating to a new URL in a tab).
 * This handler is only targetting and firing when the status property of the tab is changed (loading|complete).
 * @param {number} tabId      - ID of the tab that was updated
 * @param {Object} changeInfo - Containing properties of the tab properties changed
 * @param {Object} tab        - New state of the tab
 */
async function onTabUpdatedHandler(tabId, { status }, tab)
{
  if (!BackgroundCommon.isPersonalPolicyEnabled())
  {
    return;
  }

  if (status === 'loading')
  {
    // Assign meta data to each individual tabs differentiated by their respective ids
    const newTabData = {
      url: tab.url,
      apsDetails: null, // APS related: details used by content script (populated by injector code)
      injectDetailsByFrameId: null, // Clearing Injector's "injectedFiles" files
    };

    Logger.log(`Updating APS Details for tabId : ${tabId} to null`);

    const tabData = BackgroundGlobals.tabData.get(tabId);

    Logger.log(`tabData.url (${tabData.url}) tab.url : (${tab.url})`);

    if (tabData !== null && tabData.url === tab.url)
    {
      Logger.log(`Not updating tab data apsDetails as its same as earlier Url: ${tabData.url} tabId:${tabId} `);
      newTabData.apsDetails = tabData.apsDetails !== undefined ? tabData.apsDetails : null;
    }

    // Set the new tab data
    BackgroundGlobals.tabData.update(tabId, newTabData);
  }
  else if (status === 'complete')
  {
    if (tab.url)
    {
      // Set browser action icon
      // Set icon to McAfee Secure if tab contains a block page
      if (BackgroundCommon.isSafeUrl(tab.url))
      {
        BrowserAction.setIcon(Color.GREEN, tabId);
      }
      // Set icon to disabled if tab contains a non-webpage
      else if (!BackgroundCommon.isProperWebURL(tab.url))
      {
        BrowserAction.setIcon(Color.DISABLED, tabId);
      }

      // Online mode only - update browser action icon (if GTI resposne comes early)
      // Note: browser action icon is updated either in onHeadersReceived() or here, depending on when GTI response arrives
      // Browser resets browser action (BA) on each navigation so we have to be careful
      // Scenario: onHeadersReceived sends GTI request -> GTI request comes and updates BA ->
      // browser resets BA -> tabs.onUpdated updates BA again
      else
      {
        // Update the browser action icon data
        const color = BackgroundGlobals.messageDispatcher.toBrowserActionColor(tab.url);
        if (color !== null)
        {
          BrowserAction.setIcon(color, tabId);
        }
      }

      redirectIfNotBlocked(tab.url, tabId);
      WebRequestDispatcher.handleTopFrameNavigateComplete(tab.url, tabId);
    }
  }
}

// Usually block page redirection happens on the webrequest listener, however there is an edge case
// where the browser might ignore the tabs.update command (more specifically on tab re-navigate/refresh).
// To fix this we would also do a url reputation check on tab complete. Note that we are not making a GTI
// request call, but just checking if the URL's reputation is in the GTI cache.
// - If the URL's reputation is not in the cache, then webrequest is still yet to perform tab.update so we
// can let webrequest handle the redirection (and the tab has completely loaded)
// - Otherwise, if the URL's reputation is in the cache and the tab url is malicious, then we can assume that
// the webrequest was able to get the URL reputation but failed to redirect (tab.update), as a result, we will
// take the tab's url and check the GTI cache for the reputation, if it needs to be blocked we would redirect here.
async function redirectIfNotBlocked(url, tabId)
{
  const results = await WebRequestHelper.toUrlReputation(url, tabId, 0, null, true);

  const {
    color = null,
    statusOfBlocking = null,
    typoDomain = null,
    whitelisted,
    categories = [],
  } = results;

  const isBlocked = [...BlockState].includes(statusOfBlocking);

  // We can return early if it's not blocked or whitelisted
  if (whitelisted || !isBlocked)
  {
    return;
  }

  // Navigate to block page URL. If it is typosquatted then typoDomain is a valid string
  const qUrl = typoDomain !== null ? typoDomain : url;
  const blockPageStatus = BlockPageCommon.toBlockPageUrl(statusOfBlocking, color, qUrl);
  BackgroundGlobals.tabData.setTabBlockPageData(tabId, { bState: statusOfBlocking, url: qUrl, categories });
  extension.tabs.update(tabId, { url: blockPageStatus.url });
}

/**
 * Fires when the tab is closed.
 * @param {number} tabId - ID of the tab being closed
 */
function onTabRemoveHandler(tabId)
{
  if (!BackgroundCommon.isPersonalPolicyEnabled())
  {
    return;
  }

  // send MFW_REQUEST to native
  BackgroundGlobals.messageDispatcher.nativeDispatcher.onTabClose();

  BackgroundGlobals.tabData.delete(tabId);

  // clean RAT visited domains data for a closed tab
  cleanRatDomainsShowing(tabId);
}

function cleanRatDomainsShowing(tabId)
{
  const allTabDomains = BackgroundGlobals.cacheStore.get(CacheStorageVariables.RAT_DETECTION_SHOWING.name);

  if (!allTabDomains) return;

  delete allTabDomains[tabId];
  BackgroundGlobals.cacheStore.set(CacheStorageVariables.RAT_DETECTION_SHOWING.name, allTabDomains);
}

export default class TabListener
{
  init()
  {
    extension.tabs.onCreated.addListener(onTabCreatedHandler);
    extension.tabs.onUpdated.addListener(onTabUpdatedHandler);
    extension.tabs.onRemoved.addListener(onTabRemoveHandler);
  }
}
