import { GTIConstants } from './gti_contants';
import { GTIGlobals } from './gti_globals';

export const GTI_UPO_FLAGS = {
  None: 0x00000000,
  SkipCustomKeywords: 0x00000001,
  SkipCustomSites: 0x00000002,
  SkipWebDatabaseNotKeywords: 0x00000004,
  SkipWebDatabaseKeywords: 0x00000008,
  SkipPatterns: 0x00000010,
  SkipCGIPatterns: 0x00000020,
  SkipEmbeddedURLs: 0x00000040,
  SkipTrustedSourceQuery: 0x00000080,
  All: 0x000007ff
};

export function GtiClientInfo(clientIdGuid, rid)
{
  this.cliid = clientIdGuid;
  this.prn = GTIConstants.prn;
  this.sdkv = GTIConstants.sdkv;
  this.pv = GTIGlobals.productVersion;
  this.pev = GTIConstants.pev;
  this.rid = rid; // Random number
  this.affid = '0'; // Denotes the request is from McAfee Product
  this.cid = GTIConstants.customerId; // Adding for the GTI implementation
}

export function GtiQueryItems()
{
  this.op = null;
  this.uop = GTI_UPO_FLAGS.SkipEmbeddedURLs;
  this.url = null;
  this.catset = 4;
}

export function GtiBody(clientIdGuid, rid)
{
  this.ci = new GtiClientInfo(clientIdGuid, rid);
  this.q = [];
}
