// Background
import HTIResponse from './hti_response';

/**
 * Parse the HTI http response data.
 */
export default class HTIResponseBuilder
{
  constructor(reputationCache)
  {
    this.response = new HTIResponse(); // The response serialization object.
    this.htiResponse = null; // The response array received from HTI Server.
    this.requestData = null; // The request data from content page.
    this.htiCache = reputationCache;
    this.cachedItems = [];
  }

  init(htiResponse, requestdata)
  {
    this.htiResponse = htiResponse; // The response array received from HTI Server.
    this.requestdata = requestdata; // The request data from content page.
  }

  addCachedItem(item)
  {
    this.cachedItems.push(item);
  }

  buildLinkInfo(url, linkInfo)
  {
    this.response.annotationMap[url] = linkInfo;
  }

  prepareFromCacheResponse()
  {
    for (let index = 0; index < this.cachedItems.length; index++)
    {
      const cacheInfo = this.cachedItems[index];
      const { url, value } = cacheInfo;
      this.buildLinkInfo(url, value);
    }
  }

  prepareFromHTIResponse()
  {
    if (this.htiResponse.length === 0)
    {
      return;
    }

    // Array containing hti response of each url
    this.htiResponse.forEach((respObj) =>
    {
      const { url } = respObj;
      this.htiCache.setReputation(url, respObj);
      this.buildLinkInfo(url, respObj);
    });
  }

  prepareResponse(fncallback)
  {
    this.prepareFromHTIResponse();
    this.prepareFromCacheResponse();
    fncallback(this.response);
  }
}
