// Background
import { BackgroundGlobals } from '@extension/background/background_globals';
import SearchEngine from '@core-engine/search_engine';
import { ANNOTATION_OPTIONS } from '@mcafee/wa-annotations/Annotation/annotation_variables';
import { ContentCommands } from '@extension/common/commands';
import InjectBase from '@extension/background/content_injector/inject_base';
import { SocialMediaTypes } from '@extension/common/sma_variables';

export default class InjectAnnotation extends InjectBase
{
  constructor({ url, data })
  {
    super('content_annotation.js');
    this.url = url;
    this.data = data;
    this.pingCommand = ContentCommands.PING_CONTENT_ANNOTATION;
  }

  check()
  {
    const { sma, searchAnnotationOption } = this.data;
    const { socialMediaToggle, socialMediaOption } = sma;
    const searchEngine = new SearchEngine(BackgroundGlobals.searchEngines, socialMediaToggle, socialMediaOption, BackgroundGlobals.nativeSettings.SECURE_SEARCH_REGEXES);
    const engine = searchEngine.getEngineForUrl(this.url);

    // If annotations have to be displayed then check whether the page is part of our engines
    if (!engine)
    {
      return false;
    }

    // Handle scenario when the site is of an SMA site
    if (engine.socialMediaType)
    {
      if (socialMediaToggle && socialMediaOption !== SocialMediaTypes.NONE && engine.enabled)
      {
        return true;
      }

      return false;
    }

    // Handle scenario when the site is of a search engine site
    const { all, onlySecureSearch } = ANNOTATION_OPTIONS;
    // Inject if annotations have to be displayed for all search engine
    if (searchAnnotationOption === all)
    {
      return true;
    }

    // If has to show only for secure search and if engine is secure search then also inject
    if (searchAnnotationOption === onlySecureSearch && engine.issecuresearch)
    {
      return true;
    }

    return false;
  }
}
