import Logger from '../logger';
import TransportBase from './transport_base';
import { TelemetryGlobals } from '../telemetry_globals';

export default class TransportAwsApiGateway extends TransportBase
{
  constructor()
  {
    super();
    this.apikey = null;
    this.url = 'https://{dns}.awscommon.mcafee.com/1.0/{gateway}/v1/record';
  }

  appendDottedKeys(eventData)
  {
    const data = { ...eventData };
    const attributesToConvert = [
      'csp_clientid',
      'event_category',
      'event_action',
      'event_label',
      'engagement_interactive',
      'engagement_userinitiated',
      'hit_type',
      'hit_uniqueid',
      'hit_severity',
      'hit_label_1',
      'hit_label_2',
      'device_antimalware_provider_enabled',
      'device_os_type',
      'wss_istrial'
    ];

    // Replacing underscores with dots
    for (const attr of attributesToConvert)
    {
      if (data.hasOwnProperty(attr))
      {
        data[attr.replace(/_/g, '\.')] = data[attr];
      }
    }

    return data;
  }

  /**
     * Send the event data to the analytics server
     * https://docs.microsoft.com/en-us/rest/api/eventhub/send-event
     * @param {String} eventData - Outbound object
     */
  async send(eventDataStr)
  {
    const cleanEventData = this.sanitizeEventData(eventDataStr);
    const eventData = TelemetryGlobals.dictionaryMapper.map(this.dictionary, cleanEventData, false);
    const mappedEventData = this.appendDottedKeys(eventData);

    // Device_id (aka extension_id) is the best user identifier we have in standalone extension
    let partitionKey = TelemetryGlobals.dataCollector.get('device_id');

    // In the rare case where device_id is not available, we hardcode the partition key
    if (partitionKey === undefined || partitionKey === null || partitionKey.length > 256)
    {
      partitionKey = 'pfs1N2j8xXcLjpmuwG6lQk7y4nzxQ794jGMRokaMPDr6vz2OdhHMetZPjjNE2dmOrJ4MXylrGnytEMpaueFGIkcZZqrUUCVVtOmckTojrth8txAtrNcnRkcPPeyFqstY83xdyBQCg1cmYbhHwQv9m0';
    }

    const awsPayload = { Data: mappedEventData, PartitionKey: partitionKey };

    Logger.debug(`TransportAwsApiGateway::Send: Sending using uri: ${this.url}`);

    const sendStatusCode = await this.sendHttpRequest(awsPayload);

    if (sendStatusCode !== 200)
    {
      Logger.error(`TransportAwsApiGateway::Send: Failed to send. Send status code: ${sendStatusCode}`);
    }
    else
    {
      Logger.debug(`TransportAwsApiGateway sendStatusCode: ${sendStatusCode}`);
    }

    const uniqueId = cleanEventData['hit.uniqueid'];
    Logger.debug(`${uniqueId} sent to ${this.name}: ${JSON.stringify(awsPayload)}`);

    return sendStatusCode;
  }

  async sendHttpRequest(eventData)
  {
    try
    {
      // This line is being added so that we can test extension based telemetry in automation
      const url = (TelemetryGlobals.url && TelemetryGlobals.url.includes('localhost')) ? TelemetryGlobals.url : this.url;
      const response = await fetch(url, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'x-api-key': this.apikey
        },
        body: JSON.stringify(eventData)
      });

      return response.status;
    }
    catch (e)
    {
      Logger.error(e.message);
      return 0;
    }
  }

  /**
     * Lowercase the object keys
     * @param {Object} eventData - Outbound object
     */
  sanitizeEventData(eventData)
  {
    const sanitizedEvent = {};

    Object.keys(eventData).forEach((key) =>
    {
      sanitizedEvent[key.toLowerCase()] = eventData[key];
    });

    return sanitizedEvent;
  }

  setup()
  {
    this.apikey = this.transportConfig.apiKey;
    if (!this.apikey)
    {
      Logger.error('TransportAwsApiGateway::Setup() Invalid (unspecified) apiKey');
      return false;
    }

    this.dns = this.transportConfig.dns;
    if (!this.dns)
    {
      Logger.error('TransportAwsApiGateway::Setup() Invalid (unspecified) dns');
      return false;
    }

    this.gateway = this.transportConfig.gateway;
    if (!this.gateway)
    {
      Logger.error('TransportAwsApiGateway::Setup() Invalid (unspecified) gateway');
      return false;
    }

    this.updateURL('{dns}', this.dns);
    this.updateURL('{gateway}', this.gateway);

    return true;
  }

  updateURL(replacementKey, replacementValue)
  {
    this.url = this.url.replace(replacementKey, replacementValue);
  }
}
