import Logger from './logger';
import TransportEventHub from './Transport/transport_event_hub';
import TransportAwsApiGateway from './Transport/transport_aws_api_gateway';
import { TelemetryGlobals } from './telemetry_globals';

const transportMapper = {
  eh: TransportEventHub,
  aws_api_gateway: TransportAwsApiGateway,
};

export default class Emitter
{
  constructor(profile, name)
  {
    this.name = name;
    this.datasets = profile.datasets;
    this.dictionary = profile.dictionary;
    this.enableRules = profile.enableRules;
    this.maxDimensionLength = profile.maxDimensionLength;
    this.throttleMultiplier = profile.throttleMultiplier;
    this.throttleRule = profile.throttleRule;
    this.transportConfiguration = profile.transport_config;
    this.transportType = profile.transport;
  }

  send(outbound)
  {
    if (!this.isEnabled())
    {
      Logger.debug(`isEnabled() returned false. Will not send data to ${this.transportType}`);
      return false;
    }

    if (!transportMapper[this.transportType])
    {
      Logger.error('Matching transport object not found for the transport type');
      return false;
    }

    outbound = this.sanitize(outbound);

    const dictionary = TelemetryGlobals.dictionaryMapper.getProfileMapStringified(this.dictionary);
    const transport = new transportMapper[this.transportType]();
    transport.initialize(this.name, dictionary, JSON.stringify(this.transportConfiguration));
    if (!transport.setup())
    {
      Logger.error('Unable to setup transport');
      return false;
    }

    Logger.debug(`Emit outbound data: ${JSON.stringify(outbound)}`);

    return transport.send(outbound);
  }

  isEnabled()
  {
    try
    {
      if (this.enableRules === undefined) return true;

      if (this.enableRules.sourceType === 'data_collector')
      {
        const value = TelemetryGlobals.dataCollector.get(this.enableRules.name);
        return TelemetryGlobals.rulesManager.evaluate(value, this.enableRules.rule);
      }

      Logger.error(`isEnabled: unexpected enableRules.sourceType. sourceType ${this.enableRules.sourceType}`);
      return false;
    }
    catch (e)
    {
      Logger.error(`isEnabled: ${e.message}`);
      return false;
    }
  }

  sanitize(data)
  {

    for (const entry of Object.keys(data))
    {
      
      data[entry] = String(data[entry]);

      // Truncate characters to limit
      if ((this.maxDimensionLength != null) && !isNaN(this.maxDimensionLength))
      {
        // If exceeds max, truncate and add "..." at the end
        if (data[entry].length > this.maxDimensionLength)
        {
          data[entry] = `${data[entry].substring(0, this.maxDimensionLength - 3)}...`;
        }
      }
    }

    return data;
  }
}
