// Background
import { BackgroundGlobals } from '@extension/background/background_globals';
import BackgroundUtils from '@extension/background/background_utils';
import FeatureStats, { featureStatsTypes } from '@extension/background/feature_stats';
// Common
import BackgroundCommon from '@extension/common/background_common';
import { LocalStorageVariables } from '@extension/common/cache_store_variables';
import { SEARCH_EXTENSION_STORE_URLS, BrowserType } from '@extension/common/constants';
import { ContentCommands } from '@extension/common/commands';
import { AnnotationOptions } from '@extension/common/annotation_variables';
import { Logger } from '@extension/common/logger';

const { DOWNLOADS_BLOCKED, ANNOTATION_JOURNEY_TOAST, ANNOTATION_JOURNEY_COHORT } = BackgroundGlobals.nativeSettings;

const REQUEST_TYPE = {
  ACTION: 'action',
};

const REQUEST_NAME = {
  SS_TOAST: 'ss_toast',
  WSS_INSTALL_FLAG_CHANGED: 'wss_install_flag_changed',
  SETTING_CHANGE: 'setting_change'
};

export default class NativeGenericRequest
{
  constructor(dispatcher)
  {
    this.nativeDispatcher = dispatcher;
  }

  /**
 * Messages being sent from native to the extension.
 * @param {Object} payload - eg. [{"request_type": "action", "name": "ss_toast", "value": <JSON string>}]
 */
  processMessage(payload)
  {
    payload.forEach((request) =>
    {
      const { request_type: requestType, name: requestName, value } = request;
      if (requestType === REQUEST_TYPE.ACTION) this.processAction(requestName, value);
    });
  }

  processAction(requestName, value)
  {
    if (!requestName) return;

    switch (requestName)
    {
      case REQUEST_NAME.SS_TOAST:
      {
        this.handleSecureSearchToast(value);
        break;
      }
      case REQUEST_NAME.WSS_INSTALL_FLAG_CHANGED:
      {
        this.handleWSSInstallFlag(value);
        break;
      }
      case REQUEST_NAME.SETTING_CHANGE:
      {
        this.handleSettingChange(value);
        break;
      }
      default:
        break;
    }
  }

  handleSecureSearchToast(value)
  {
    try
    {
      const data = JSON.parse(value);
      const { enabled, post_accept_action: postAcceptAction } = data;
      if (enabled)
      {
        const POST_ACCEPT_ACTION = {
          REDIRECT_STORE: 'redirect_store',
          REDIRECT_EXTENSION_PAGE: 'redirect_extension_page'
        };

        switch (postAcceptAction)
        {
          case POST_ACCEPT_ACTION.REDIRECT_STORE:
          {
            BackgroundUtils.focusOrCreateTab(SEARCH_EXTENSION_STORE_URLS[BackgroundCommon.browserTypeToString()]);
            break;
          }
          case POST_ACCEPT_ACTION.REDIRECT_EXTENSION_PAGE:
          {
            const extensionPage = this.toExtensionPage(BackgroundGlobals.browserType);
            if (extensionPage !== null)
            {
              BackgroundUtils.focusOrCreateTab(extensionPage);
            }
            break;
          }
          default:
            break;
        }
      }
    }
    catch (err)
    {
      Logger.log(`handleSecureSearchToast err: ${err}`);
    }
  }

  handleWSSInstallFlag(value)
  {
    try
    {
      const data = JSON.parse(value);
      const { wss_installed: wssInstalled } = data;
      if (typeof wssInstalled === 'boolean')
      {
        BackgroundGlobals.WSS_INSTALLED = wssInstalled;
      }
    }
    catch (err)
    {
      Logger.log(`handleWSSInstallFlag err: ${err}`);
    }
  }

  handleSettingChange(value)
  {
    try
    {
      const data = JSON.parse(value);
      const { name: settingName, value: settingValue } = data;
      if (!settingName || settingValue === undefined || settingValue === null) return;

      // update download blocked stats data from native request
      if (settingName === DOWNLOADS_BLOCKED.name) FeatureStats.add(featureStatsTypes.DOWNLOADS_BLOCKED);

      else if (settingName === ANNOTATION_JOURNEY_TOAST.name) this.handleAnnotationJourneyToast(settingValue);
    }
    catch (err)
    {
      Logger.log(`handleSettingChange err: ${err}`);
    }
  }

  toExtensionPage(browserType)
  {
    let extensionPage = null;

    switch (browserType)
    {
      case BrowserType.CHROME:
        extensionPage = 'chrome://extensions';
        break;
      case BrowserType.EDGE:
        extensionPage = 'edge://extensions';
        break;
      case BrowserType.FIREFOX:
        extensionPage = 'about:addons';
        break;
    }
    return extensionPage;
  }

  handleAnnotationJourneyToast(value)
  {
    try
    {
      if (typeof value !== 'boolean')
      {
        return;
      }
      chrome.runtime.sendMessage({ command: ContentCommands.TRIGGER_AJ_TOAST, payload: value });

      BackgroundGlobals.cacheStore.set(LocalStorageVariables.AJ_TOAST.name, value);

      if (value === true)
      {
        if ([1, 3].includes(ANNOTATION_JOURNEY_COHORT))
        {
          BackgroundGlobals.cacheStore.set(LocalStorageVariables.SEARCH_ANNOTATION_OPTION.name, AnnotationOptions.ONLY_SECURE_SEARCH);
        }
        else if ([2, 4].includes(ANNOTATION_JOURNEY_COHORT))
        {
          BackgroundGlobals.cacheStore.set(LocalStorageVariables.SEARCH_ANNOTATION_OPTION.name, AnnotationOptions.ALL);
        }
      }
    }
    catch (err)
    {
      Logger.log(`handleAnnotationJourneyToast err: ${err}`);
    }
  }
}
