import { HTIConstants } from './hti_contants';
import { HTIGlobals } from './hti_globals';
import { HTIStorageVariables } from './hti_storage_variables';
import Logger from '../../Common-Utils-Package/logger/logger';

export function generateHeaders()
{
  const headersData = {
    'X-McAfee-ClientID': HTIGlobals.clientIdGuid,
    'X-McAfee-ProductID': HTIConstants.productId,
    'X-McAfee-ProductVersion': HTIGlobals.productVersion,
    'Content-Type': HTIConstants.contentType,
    'cache-control': HTIConstants.cacheControl,
    'X-McAfee-Flag': 1
  };

  const cspToken = HTIGlobals.storage.get(HTIStorageVariables.CSP_TOKEN_HTI, {});
  const accessToken = cspToken.access_token || '';
  if (!accessToken)
  {
    Logger.error('Failed to retrieve cspToken from storage.');
  }

  headersData.Authorization = `Bearer ${accessToken}`;
  return headersData;
}
