import BackgroundCommon from '@extension/common/background_common';
import TelemetryEngine from '@mcafee/wa-telemetry';

export default class StandAloneTelemetryWrapper extends TelemetryEngine
{
  constructor(jsonLocation, context = {}, externalLogger = null)
  {
    super(jsonLocation, context, externalLogger);
  }

  sendEvent(uniqueId, payload)
  {
    if (!BackgroundCommon.isPersonalPolicyEnabled() || !BackgroundCommon.isActivityPolicyEnabled())
    {
      return;
    }

    super.sendEvent(uniqueId, payload);
  }
}
