import Logger from './logger';

/**
 * Extract out all nested properties and move to top-level of the object
 */
export function normalizePayload(recordPayload)
{
  if (!recordPayload)
  {
    return null;
  }

  const normalizedPayload = {};

  for (const key of Object.keys(recordPayload))
  {
    const obj = recordPayload[key];
    if (obj instanceof Array)
    {
      // Arrays doesn't have key-value pair,so just assign it to the attribute (corner case)
      normalizedPayload[key] = obj;
    }
    else if (typeof obj === 'object')
    {
      for (const objKey of Object.keys(obj))
      {
        normalizedPayload[objKey] = obj[objKey];
      }
    }
    else
    {
      normalizedPayload[key] = obj;
    }
  }

  return normalizedPayload;
}

/**
 * HTTP fetch call to obtain the json data
 */
export async function fetchJSONContent(filename)
{
  const response = await fetch(`./TestData/${filename}.json`, {
    headers: {
      'Content-Type': 'application/json'
    }
  });

  return response.json();
}

/**
 * Shallow copy objects
 * @param {Object} destination - Object with properties being merged into it
 * @param {Object} source  - Source object that will be copied by the destination
 * @param {Boolean} lowerCaseKeys - Lowercase keys if applicable
 */
export function mergeObjects(destination, source, lowerCaseKeys = false)
{
  if (!destination || !source) return;

  Object.keys(source).forEach((key) =>
  {
    const newKey = lowerCaseKeys ? key.toLowerCase() : key;
    destination[newKey] = source[key];
  });
}

/**
 * Lowercase key and values of an object.
 */
export function lowerCaseObjectKeyValuePair(obj, isKeyLowered, isValueLowered)
{
  Object.keys(obj).forEach((key) =>
  {
    // Create new key and value
    const newKey = isKeyLowered ? key.toLowerCase() : key;
    obj[newKey] = isValueLowered ? obj[key].toLowerCase() : obj[key];

    // Delete old key if it doesn't match with the new key from the object
    if (newKey !== key)
    {
      delete obj[key];
    }
  });
}

/**
 * Remove duplicates from an array
 */
export function arrayRemoveDuplicates(arr)
{
  return [...new Set(arr)];
}


/**  Checks if an item exists in an array
  * PARAMS:
  * @param {Array} array - array to be searched
  * @param {Array} itemName - search value
  *
  * RETURN:
  *  true: if exists, false otherwise
**/
export function arrayIncludes(array, itemTocheck)
{
  for (const item of array)
  {
    if (itemTocheck === item)
    {
      return true;
    }
  }

  return false;
}

/**
 * Extract common elements from two arrays
 * @param {String[]} arrayOne - First array being iterated
 * @param {String[]} arrayTwo - Second array used to test if element exists
 * @returns A new array with the common elements
 */
export function getCommonElements(arrayOne, arrayTwo)
{
  return arrayOne.filter(element => arrayTwo.includes(element));
}

/** Deletes items from an array - first occurence
    *
    * PARAMS:
    *  @param {Array} array: target array
    *  @param {Array} itemName: search value
    *
    * RETURN:
    *  true: if exists, false otherwise
**/
export function removeItemFromArray(array, itemName)
{
  for (const [idx, value] of array.entries())
  {
    if (itemName === value)
    {
      array.splice(idx, 1);
      break;
    }
  }
}

/**
 * Generate hash using MD5 algorithm
 * @param {String} string
 */
export function processMD5Hash(string)
{
  function RotateLeft(lValue, iShiftBits)
  {
    return (lValue << iShiftBits) | (lValue >>> (32 - iShiftBits));
  }

  function AddUnsigned(lX, lY)
  {
    let lX4; let lY4; let lX8; let lY8; let
      lResult;
    lX8 = (lX & 0x80000000);
    lY8 = (lY & 0x80000000);
    lX4 = (lX & 0x40000000);
    lY4 = (lY & 0x40000000);
    lResult = (lX & 0x3FFFFFFF) + (lY & 0x3FFFFFFF);
    if (lX4 & lY4)
    {
      return (lResult ^ 0x80000000 ^ lX8 ^ lY8);
    }
    if (lX4 | lY4)
    {
      if (lResult & 0x40000000)
      {
        return (lResult ^ 0xC0000000 ^ lX8 ^ lY8);
      }

      return (lResult ^ 0x40000000 ^ lX8 ^ lY8);
    }

    return (lResult ^ lX8 ^ lY8);
  }

  function F(x, y, z) { return (x & y) | ((~x) & z); }
  function G(x, y, z) { return (x & z) | (y & (~z)); }
  function H(x, y, z) { return (x ^ y ^ z); }
  function I(x, y, z) { return (y ^ (x | (~z))); }

  function FF(a, b, c, d, x, s, ac)
  {
    a = AddUnsigned(a, AddUnsigned(AddUnsigned(F(b, c, d), x), ac));
    return AddUnsigned(RotateLeft(a, s), b);
  }

  function GG(a, b, c, d, x, s, ac)
  {
    a = AddUnsigned(a, AddUnsigned(AddUnsigned(G(b, c, d), x), ac));
    return AddUnsigned(RotateLeft(a, s), b);
  }

  function HH(a, b, c, d, x, s, ac)
  {
    a = AddUnsigned(a, AddUnsigned(AddUnsigned(H(b, c, d), x), ac));
    return AddUnsigned(RotateLeft(a, s), b);
  }

  function II(a, b, c, d, x, s, ac)
  {
    a = AddUnsigned(a, AddUnsigned(AddUnsigned(I(b, c, d), x), ac));
    return AddUnsigned(RotateLeft(a, s), b);
  }

  function ConvertToWordArray(string)
  {
    let lWordCount;
    const lMessageLength = string.length;
    const lNumberOfWords_temp1 = lMessageLength + 8;
    const lNumberOfWords_temp2 = (lNumberOfWords_temp1 - (lNumberOfWords_temp1 % 64)) / 64;
    const lNumberOfWords = (lNumberOfWords_temp2 + 1) * 16;
    const lWordArray = Array(lNumberOfWords - 1);
    let lBytePosition = 0;
    let lByteCount = 0;
    while (lByteCount < lMessageLength)
    {
      lWordCount = (lByteCount - (lByteCount % 4)) / 4;
      lBytePosition = (lByteCount % 4) * 8;
      lWordArray[lWordCount] = (lWordArray[lWordCount] | (string.charCodeAt(lByteCount) << lBytePosition));
      lByteCount++;
    }
    lWordCount = (lByteCount - (lByteCount % 4)) / 4;
    lBytePosition = (lByteCount % 4) * 8;
    lWordArray[lWordCount] = lWordArray[lWordCount] | (0x80 << lBytePosition);
    lWordArray[lNumberOfWords - 2] = lMessageLength << 3;
    lWordArray[lNumberOfWords - 1] = lMessageLength >>> 29;
    return lWordArray;
  }

  function WordToHex(lValue)
  {
    let WordToHexValue = '';
    let WordToHexValue_temp = '';
    let lByte;
    let lCount;
    for (lCount = 0; lCount <= 3; lCount++)
    {
      lByte = (lValue >>> (lCount * 8)) & 255;
      WordToHexValue_temp = `0${lByte.toString(16)}`;
      WordToHexValue += WordToHexValue_temp.substr(WordToHexValue_temp.length - 2, 2);
    }
    return WordToHexValue;
  }

  function Utf8Encode(string)
  {
    string = string.replace(/\r\n/g, '\n');
    let utftext = '';

    for (let n = 0; n < string.length; n++)
    {
      const c = string.charCodeAt(n);

      if (c < 128)
      {
        utftext += String.fromCharCode(c);
      }
      else if ((c > 127) && (c < 2048))
      {
        utftext += String.fromCharCode((c >> 6) | 192);
        utftext += String.fromCharCode((c & 63) | 128);
      }
      else
      {
        utftext += String.fromCharCode((c >> 12) | 224);
        utftext += String.fromCharCode(((c >> 6) & 63) | 128);
        utftext += String.fromCharCode((c & 63) | 128);
      }
    }

    return utftext;
  }

  let x = Array();
  let k; let AA; let BB; let CC; let DD; let a; let b; let c; let
    d;
  const S11 = 7; const S12 = 12; const S13 = 17; const
    S14 = 22;
  const S21 = 5; const S22 = 9; const S23 = 14; const
    S24 = 20;
  const S31 = 4; const S32 = 11; const S33 = 16; const
    S34 = 23;
  const S41 = 6; const S42 = 10; const S43 = 15; const
    S44 = 21;

  string = Utf8Encode(string);

  x = ConvertToWordArray(string);

  a = 0x67452301; b = 0xEFCDAB89; c = 0x98BADCFE; d = 0x10325476;

  for (k = 0; k < x.length; k += 16)
  {
    AA = a; BB = b; CC = c; DD = d;
    a = FF(a, b, c, d, x[k + 0], S11, 0xD76AA478);
    d = FF(d, a, b, c, x[k + 1], S12, 0xE8C7B756);
    c = FF(c, d, a, b, x[k + 2], S13, 0x242070DB);
    b = FF(b, c, d, a, x[k + 3], S14, 0xC1BDCEEE);
    a = FF(a, b, c, d, x[k + 4], S11, 0xF57C0FAF);
    d = FF(d, a, b, c, x[k + 5], S12, 0x4787C62A);
    c = FF(c, d, a, b, x[k + 6], S13, 0xA8304613);
    b = FF(b, c, d, a, x[k + 7], S14, 0xFD469501);
    a = FF(a, b, c, d, x[k + 8], S11, 0x698098D8);
    d = FF(d, a, b, c, x[k + 9], S12, 0x8B44F7AF);
    c = FF(c, d, a, b, x[k + 10], S13, 0xFFFF5BB1);
    b = FF(b, c, d, a, x[k + 11], S14, 0x895CD7BE);
    a = FF(a, b, c, d, x[k + 12], S11, 0x6B901122);
    d = FF(d, a, b, c, x[k + 13], S12, 0xFD987193);
    c = FF(c, d, a, b, x[k + 14], S13, 0xA679438E);
    b = FF(b, c, d, a, x[k + 15], S14, 0x49B40821);
    a = GG(a, b, c, d, x[k + 1], S21, 0xF61E2562);
    d = GG(d, a, b, c, x[k + 6], S22, 0xC040B340);
    c = GG(c, d, a, b, x[k + 11], S23, 0x265E5A51);
    b = GG(b, c, d, a, x[k + 0], S24, 0xE9B6C7AA);
    a = GG(a, b, c, d, x[k + 5], S21, 0xD62F105D);
    d = GG(d, a, b, c, x[k + 10], S22, 0x2441453);
    c = GG(c, d, a, b, x[k + 15], S23, 0xD8A1E681);
    b = GG(b, c, d, a, x[k + 4], S24, 0xE7D3FBC8);
    a = GG(a, b, c, d, x[k + 9], S21, 0x21E1CDE6);
    d = GG(d, a, b, c, x[k + 14], S22, 0xC33707D6);
    c = GG(c, d, a, b, x[k + 3], S23, 0xF4D50D87);
    b = GG(b, c, d, a, x[k + 8], S24, 0x455A14ED);
    a = GG(a, b, c, d, x[k + 13], S21, 0xA9E3E905);
    d = GG(d, a, b, c, x[k + 2], S22, 0xFCEFA3F8);
    c = GG(c, d, a, b, x[k + 7], S23, 0x676F02D9);
    b = GG(b, c, d, a, x[k + 12], S24, 0x8D2A4C8A);
    a = HH(a, b, c, d, x[k + 5], S31, 0xFFFA3942);
    d = HH(d, a, b, c, x[k + 8], S32, 0x8771F681);
    c = HH(c, d, a, b, x[k + 11], S33, 0x6D9D6122);
    b = HH(b, c, d, a, x[k + 14], S34, 0xFDE5380C);
    a = HH(a, b, c, d, x[k + 1], S31, 0xA4BEEA44);
    d = HH(d, a, b, c, x[k + 4], S32, 0x4BDECFA9);
    c = HH(c, d, a, b, x[k + 7], S33, 0xF6BB4B60);
    b = HH(b, c, d, a, x[k + 10], S34, 0xBEBFBC70);
    a = HH(a, b, c, d, x[k + 13], S31, 0x289B7EC6);
    d = HH(d, a, b, c, x[k + 0], S32, 0xEAA127FA);
    c = HH(c, d, a, b, x[k + 3], S33, 0xD4EF3085);
    b = HH(b, c, d, a, x[k + 6], S34, 0x4881D05);
    a = HH(a, b, c, d, x[k + 9], S31, 0xD9D4D039);
    d = HH(d, a, b, c, x[k + 12], S32, 0xE6DB99E5);
    c = HH(c, d, a, b, x[k + 15], S33, 0x1FA27CF8);
    b = HH(b, c, d, a, x[k + 2], S34, 0xC4AC5665);
    a = II(a, b, c, d, x[k + 0], S41, 0xF4292244);
    d = II(d, a, b, c, x[k + 7], S42, 0x432AFF97);
    c = II(c, d, a, b, x[k + 14], S43, 0xAB9423A7);
    b = II(b, c, d, a, x[k + 5], S44, 0xFC93A039);
    a = II(a, b, c, d, x[k + 12], S41, 0x655B59C3);
    d = II(d, a, b, c, x[k + 3], S42, 0x8F0CCC92);
    c = II(c, d, a, b, x[k + 10], S43, 0xFFEFF47D);
    b = II(b, c, d, a, x[k + 1], S44, 0x85845DD1);
    a = II(a, b, c, d, x[k + 8], S41, 0x6FA87E4F);
    d = II(d, a, b, c, x[k + 15], S42, 0xFE2CE6E0);
    c = II(c, d, a, b, x[k + 6], S43, 0xA3014314);
    b = II(b, c, d, a, x[k + 13], S44, 0x4E0811A1);
    a = II(a, b, c, d, x[k + 4], S41, 0xF7537E82);
    d = II(d, a, b, c, x[k + 11], S42, 0xBD3AF235);
    c = II(c, d, a, b, x[k + 2], S43, 0x2AD7D2BB);
    b = II(b, c, d, a, x[k + 9], S44, 0xEB86D391);
    a = AddUnsigned(a, AA);
    b = AddUnsigned(b, BB);
    c = AddUnsigned(c, CC);
    d = AddUnsigned(d, DD);
  }

  const temp = WordToHex(a) + WordToHex(b) + WordToHex(c) + WordToHex(d);

  return temp.toLowerCase();
}

export function generateGUIDFromHash(hashStr)
{
  if (hashStr.length != 32)
  {
    Logger.error(`Hashed string passed does not match required length(32). Value: ${hashStr}`);
    return null;
  }

  let uid = '';
  try
  {
    const data1 = hashStr.substr(0, 8);
    const data2 = hashStr.substr(8, 4);
    const data3 = hashStr.substr(12, 4);
    const data4 = hashStr.substr(16, 4);
    const data5 = hashStr.substr(20, 12);

    uid = `{${data1}-${data2}-${data3}-${data4}-${data5}}`;
    uid = uid.toUpperCase();
  }
  catch (e)
  {
    Logger.error(`Error retrieving guid from: ${hashStr}. Message: ${e.message}`);
    return null;
  }

  return uid;
}

export function isoDateToDate(value)
{
  try
  {
    if (!value || (value.length < 8))
    {
      return null;
    }

    const year = parseInt(value.substr(0, 4), 10);
    const month = parseInt(value.substr(5, 2), 10);
    const day = parseInt(value.substr(8, 2), 10);
    let hour = parseInt(value.substr(11, 2), 10);
    let minute = parseInt(value.substr(14, 2), 10);
    let second = parseInt(value.substr(17, 2), 10);
    if (!year || !month || !day)
    {
      return null;
    }

    hour = isNaN(hour) ? 0 : hour;
    minute = isNaN(minute) ? 0 : minute;
    second = isNaN(second) ? 0 : second;

    return new Date(Date.UTC(year, month - 1, day, hour, minute, second));
  }
  catch (e)
  {
    Logger.warn(`isoDateToDate: exception caught with message: ${e.message}`);
    return null;
  }
}
