import Logger from '../Common-Utils-Package/logger/logger';
import HTIBackgroundListener from './hti/hti_background_listener';
import { HTIGlobals } from './hti/hti_globals';
import HTIRequestor from './hti/hti_requestor';

export default class BackgroundHTIRequestor
{
  /**
   * HTI Requestor: constructor
   * @param {object} wasm - wasm engine object
   * @param {function} wasm.get_wa_app_key - should contain function
   * @param {function} wasm.get_wa_shared_key - should contain function
   * @param {function} wasm.generate_value_for_ac - should contain function
   * @param {object} options - object containinig optional params that alter the behavior of this lib
   * @param {string} options.clientIdGuid - client id passed as part of the hti request header (default as: '87d8d1082c2f2f821f438b2359b7a5b4')
   * @param {string} options.cspClientId - csp client id
   * @param {string} options.serverUrl - url string to the hti server (default as: 'https://threat.api.mcafee.com')
   * @param {string} options.productVersion - product version for gti call (default as: '0.0.0.0')
   */
  constructor(wasm, options)
  {
    this.promisesToComplete = null;
    HTIGlobals.htiRequestor = new HTIRequestor(wasm, options);
  }

  /**
   * TODO: can be removed when we fully transition to HTI from GTI
   * This function returns the API type
   * @param {string} returns 'HTI'
   */
  getType()
  {
    return 'HTI';
  }

  async init()
  {
    this.promisesToComplete = new Promise(async (resolve) =>
    {
      const htiRequestInit = await HTIGlobals.htiRequestor.init();
      if (!htiRequestInit)
      {
        Logger.error('Failed to initialize HTI Requestor.');
      }

      resolve();
    });

    const htiListener = new HTIBackgroundListener(this.promisesToComplete);
    htiListener.init();

    await this.promisesToComplete;
  }

  /**
   * This function allows modifying the global variables
   * ie: globalValues = { serverUrl: 'https://someurl.com', otherGlobal: 'somedata' }
   * @param {object} globalValues
   * @param {key} globalsValues.key
   * @param {value} globalsValues.value
   */
  setGlobals(globalValues)
  {
    HTIGlobals.htiRequestor.setGlobals(globalValues);
  }

  /**
   * Does post to HTI splitting the requests as per guidelines to 5 questions per request.
   * @param {object} requestData      - Array of request URLs whose reputation is asked to GTI
   * @param {string} referrer         - Value of referer header (or undefined)
   * @param {boolean} checkCacheOnly  - Check Cache only for GTI, do not make HTTP GTI request
   */
  async post(requestData, referrer = null, checkCacheOnly = false)
  {
    await this.promisesToComplete;

    if (!HTIGlobals || !HTIGlobals.htiRequestor)
    {
      Logger.error('Error to post HTI request has HTIRequestor has not yet been initialized');
      return null;
    }

    const options = { referrer, checkCacheOnly };
    const promise = new Promise((resolve) => HTIGlobals.htiRequestor.post(requestData, resolve, options));
    const result = await promise;
    return result;
  }

  async clearCache()
  {
    HTIGlobals.htiRequestor.getCache().clear();
  }

  setReputation(url, value)
  {
    HTIGlobals.htiRequestor.getCache().setReputation(url, value);
  }

  getReputation(url)
  {
    return HTIGlobals.htiRequestor.getCache().getReputation(url);
  }
}
