// Background
import { BackgroundGlobals } from '@extension/background/background_globals';
import UrlParser from '@extension/common/url_parser';
import { CacheStorageVariables } from '@extension/common/cache_store_variables';
import { WhitelistActions, WhitelistTypes } from '@extension/common/whitelist_variables';

import BackgroundCommon from '@extension/common/background_common';
import { ContentCommands } from '@extension/common/commands';
import { Logger } from '@extension/common/logger';
import InjectBase from '@extension/background/content_injector/inject_base';

export default class InjectRATDetection extends InjectBase
{
  constructor({ url, tabId, categories })
  {
    super('content_rat_detection.js');
    this.url = url;
    this.domain = UrlParser.getDomain(this.url);
    this.tabId = tabId;
    this.categories = categories;
    this.nativeSettings = BackgroundGlobals.nativeSettings;
    this.pingCommand = ContentCommands.PING_CONTENT_RAT_DETECTION;
    this.ratBlacklist = BackgroundGlobals.ratBlacklist;
  }

  check()
  {
    const { RAT_DETECTION_ENABLED } = this.nativeSettings;
    if (!RAT_DETECTION_ENABLED.value)
    {
      return false;
    }

    // not proper URL or SiteAdvisor URL: not show
    if (!BackgroundCommon.isProperWebURL(this.url) || BackgroundCommon.isSiteAdvisorUrl(this.url))
    {
      return false;
    }

    // inside a same tab and a same domain that already showed once: not show
    const domainVisited = this.getDomainVisitedDetails(this.tabId);
    if (domainVisited === this.domain)
    {
      return false;
    }
    this.updateRatDetectionShowingFlag(false);

    // inside RAT Whitelist: not show
    const whitelist = BackgroundGlobals.messageDispatcher.whitelistNoCallback(WhitelistActions.GET, WhitelistTypes.RAT_DETECTION, {});
    if (!Array.isArray(whitelist))
    {
      Logger.error('Invalid RAT whitelist from background cache storage');
      return false;
    }

    if (this.isDomainWhitelisted(whitelist, this.domain))
    {
      return false;
    }

    // inside RAT Blacklist: show
    for (const site of this.ratBlacklist)
    {
      if (site.domain === this.domain)
      {
        if (site.urls.length === 0)
        {
          return true;
        }

        if (site.urls.includes(UrlParser.getCleanURI(this.url)))
        {
          return true;
        }
      }
    }

    // ignore if page does not have any GTI categories
    if (!this.categories)
    {
      return false;
    }

    if (!Array.isArray(this.categories))
    {
      Logger.error('invalid category result from GTI response');
      return false;
    }

    // Check if it contains the 'remote access' category
    return this.categories.includes('ra');
  }

  isDomainWhitelisted(whitelistedList, domain)
  {
    for (let index = 0; index < whitelistedList.length; index++)
    {
      const whitelistItem = whitelistedList[index];
      if (whitelistItem.includes(domain) || domain.includes(whitelistItem))
      {
        return true;
      }
    }
    return false;
  }

  getDomainVisitedDetails()
  {
    let response = '';
    const storeVariables = CacheStorageVariables.RAT_DETECTION_SHOWING.name;
    const allTabDomains = BackgroundGlobals.cacheStore.get(storeVariables);

    if (!allTabDomains)
    {
      return response;
    }

    response = allTabDomains[this.tabId] || response;
    return response;
  }

  updateRatDetectionShowingFlag(showed)
  {
    const storeVariables = CacheStorageVariables.RAT_DETECTION_SHOWING.name;
    const allTabDomains = BackgroundGlobals.cacheStore.get(storeVariables) || {};
    let currentTabDomain = allTabDomains[this.tabId] || '';

    if (showed && this.domain)
    {
      currentTabDomain = this.domain;
    }
    else
    {
      currentTabDomain = '';
    }

    allTabDomains[this.tabId] = currentTabDomain;
    BackgroundGlobals.cacheStore.set(storeVariables, allTabDomains);
  }
}
