import UrlParser from '@extension/common/url_parser';
import FeatureStats, { featureStatsTypes } from '@extension/background/feature_stats';
import { EngineType } from '@extension/common/annotation_variables';
import { Logger } from '@extension/common/logger';
// Telemetry
import ContextTelemetry from '@extension/telemetry/context_telemetry';
import { PageLevel, EventNames } from '@extension/telemetry/common_telemetry';
import { NativeCommands } from '@extension/background/dispatchers/native/native_message_dispatcher_v1';
// Injector
import Injector from '@extension/background/content_injector/injector';
import InjectAnnotation from '@extension/background/content_injector/inject_annotations';
import InjectRATDetection from '@extension/background/content_injector/inject_rat_detection';
import InjectIframeBanner from '@extension/background/content_injector/inject_iframe_banner';
import InjectIframeBlock from '@extension/background/content_injector/inject_iframe_block';
import InjectAutoPlayDetection from '@extension/background/content_injector/inject_autoplay_detection';
import InjectFFDownloadOverlay from '@extension/background/content_injector/inject_ff_download_overlay';
import InjectFFViewports from '@extension/background/content_injector/inject_ff_viewports';
import InjectFormCheck from '@extension/background/content_injector/inject_form_check';
import InjectFormDetection from '@extension/background/content_injector/inject_form_detection';
import InjectIDPS from '@extension/background/content_injector/inject_idps';
import InjectAPSBalloon from '@extension/background/content_injector/inject_aps_balloon';
import InjectSiteListener from '@extension/background/content_injector/inject_site_listener';
import InjectAPSToast from '@extension/background/content_injector/inject_aps_toast';
import InjectAPSObserver from '@extension/background/content_injector/inject_aps_observer';

export default class WebRequestDispatcher
{
  /**
   * Invoked in the GTI Request and handle the response.
   * Route the user to the safest destination accordingly.
   * @param {string} url        - Url being navigated to
   * @param {number} tabId      - Current tab id
   * @param {number} frameId    - Frame id if called from an iframe
   * @param {string} referer    - value of Referer header or undefined
   */
  static async onHeadersReceived(url, tabId, frameId, referer)
  {
    // Don't check reputation or typosquatting for localhost
    const domain = UrlParser.getDomain(url);
    if (domain === 'localhost' && !BackgroundGlobals.ALLOW_LOCALHOST)
    {
      return;
    }

    const results = await WebRequestHelper.toUrlReputation(url, tabId, frameId, referer, false, true);
    WebRequestHelper.handleUrlReputation(results, url, tabId, frameId, referer);
  }

  /**
   * Invoked in the GTI Request and handle the response.
   * Route the user to the safest destination accordingly.
   * @param {string} url        - Url being navigated to
   * @param {number} tabId      - Current tab id
   * @param {number} frameId    - Frame id if called from an iframe
   * @param {string} referer    - value of Referer header or undefined
   */
  static async onNavigateComplete(url, tabId, frameId)
  {
    const isTopFrame = frameId <= 0;
    if (isTopFrame)
    {
      WebRequestHelper.sendRespectiveTelemetry([EventNames.BROWSER_NAVIGATE], { url });

      // Telemetry for Secure Search Hit
      WebRequestHelper.onSSHitTelemetry(url);

      // On Engine Search trigger
      WebRequestHelper.onEngineSearch(url);

      // Inject code into top level navigate complete
      WebRequestDispatcher.handleTopFrameNavigateComplete(url, tabId);
    }
    else
    {
      const contentToInject = [
        new InjectAutoPlayDetection(),
      ];

      // Temporary code to keep current form detection functionality while also
      // being able to test new form detection optimization.
      const { IFRAME_FORM_CHECK_ENABLED } = BackgroundGlobals.nativeSettings;
      contentToInject.push(IFRAME_FORM_CHECK_ENABLED.value ? new InjectFormCheck() : new InjectFormDetection());

      Injector.inject(tabId, contentToInject, frameId);
    }
  }

  static async handleTopFrameNavigateComplete(url, tabId)
  {
    // if the top frame is a chrome page or chrome extension ignore content script injects, will give errors otherwise
    if (url.startsWith('chrome')) return;

    if (!BackgroundGlobals.messageDispatcher)
    {
      Logger.error('handleTopFrameNavigateComplete: messageDispatcher is invalid.');
      return;
    }

    const promises = [];
    promises.push(new Promise((resolve) => BackgroundGlobals.messageDispatcher.getSettingsData(resolve)));
    promises.push(BackgroundGlobals.messageDispatcher.getURLReputation(url));
    const [data, gtiResponse] = await Promise.all(promises);
    const { color, categories } = gtiResponse;

    // Content Script Inject
    const contentToInject = [
      new InjectAnnotation({ url, data }),
      new InjectAPSToast({ url }),
      new InjectAPSObserver({ url }),
      new InjectRATDetection({ url, tabId, categories }),
      new InjectAutoPlayDetection(),
      new InjectIDPS({ tabId }),
      new InjectSiteListener({ url }),
      new InjectAPSBalloon({ tabId, url, categories, color }),
    ];

/////////////////////////////////////
///////////////////////////////////////////////////////////////
//////////////////////////////////////////////////
//////////////

    // Temporary code to keep current form detection functionality while also
    // being able to test new form detection optimization.
    const { IFRAME_FORM_CHECK_ENABLED } = BackgroundGlobals.nativeSettings;
    contentToInject.push(IFRAME_FORM_CHECK_ENABLED.value ? new InjectFormCheck() : new InjectFormDetection());

    Injector.inject(tabId, contentToInject);
  }
}

export class WebRequestHelper
{
  /**
   * Get the block state and color for the url passed.
   * @param {string} url, or object with url and referer - The url being examined
   * @param {boolean} checkCacheOnly - checkCacheOnly - only get URL reputation from cache and not do an actual HTTP request call
   * @return {promise} containing reputation data
   */
  static async toUrlReputation(originalUrl, tabId, frameId, referer, checkCacheOnly = false)
  {
    let url = originalUrl;
    const isTopFrame = frameId <= 0;

    if (!BackgroundCommon.isProperWebURL(url))
    {
      return {
        color: Color.UNKNOWN,
        statusOfBlocking: BlockState.UNKNOWN,
        whitelisted: false
      };
    }

    if (BackgroundCommon.isSafeUrl(url))
    {
      return {
        color: Color.GREEN,
        statusOfBlocking: BlockState.GREEN,
        whitelisted: false
      };
    }

    // Don't check reputation if main frame is a known trusted domain
    const tabData = BackgroundGlobals.tabData.get(tabId);
    if (tabData.tabDomainTrusted)
    {
      return {
        color: Color.GREEN,
        statusOfBlocking: BlockState.GREEN,
        whitelisted: false
      };
    }

    if (BackgroundGlobals.browserType === BrowserType.FIREFOX)
    {
      url = BackgroundGlobals.messageDispatcher.removeSearchTermOnEngineUrl(originalUrl);
    }

    // if is TopFrame, check if tab's domain is trusted, if so update tabData and return GOOD result.
    if (isTopFrame)
    {
      const tabDomain = UrlParser.getDomain(url);
      const isTrusted = BackgroundGlobals.trustedDomains.has(tabDomain);

      if (isTrusted)
      {
        BackgroundGlobals.tabData.update(tabId, { tabDomainTrusted: true });
        return {
          color: Color.GREEN,
          statusOfBlocking: BlockState.GREEN,
          whitelisted: false
        };
      }
    }

    // Make GTI/HTI reputation request
    const response = await BackgroundGlobals.messageDispatcher.makeMTIRequest([url], referer, checkCacheOnly);
    const domain = UrlParser.getDomain(url);
    const isWhitelisted = WhitelistWrapper.isDomainInWhitelist(domain, LocalStorageVariables.BLOCK_PAGE_WHITELIST.name);
    let result = {
      state: BlockState.UNKNOWN,
      color: Color.UNKNOWN
    };

    let cat = [];
    if (response && response.annotationMap)
    {
      const annotationDetails = response.annotationMap[url];
      if (annotationDetails)
      {
        result = BackgroundGlobals.messageDispatcher.toBlockState(annotationDetails);

        // Page GTI categories
        cat = annotationDetails.category || [];
      }
      else
      {
        Logger.warn('WebRequestHelper makeMTIRequest: annotation details undefined');
      }
    }

    // If it is whitelisted or if the page is unknown or good then alone go and check for typosquatting. In otherwords malicious url takes
    // precedence over typosquatting url. Again if the url is whitelisted and if this is top level page then.
    if ((isWhitelisted || result.state === BlockState.GREEN || result.state === BlockState.UNKNOWN) && (tabId > 0 && frameId <= 0))
    {
      // Check for typosquatting
      const { whitelisted, domain } = await WebRequestHelper.handleTyposquatting(url);
      let statusOfBlocking = result.state;
      let whitelistVal = isWhitelisted; // Top-level url whitelist value (not taken typosquatting into account)

      // If typosquatting then handle it so
      if (domain !== null)
      {
        statusOfBlocking = BlockState.TYPOSQUATTING;
        whitelistVal = whitelisted; // Use the typosquatting equivalent value
      }

      return {
        color: result.color,
        statusOfBlocking,
        whitelisted: whitelistVal,
        typoDomain: domain,
        categories: cat || []
      };
    }

    // If URL is not whitelisted and is malicious then handle it so.
    return {
      color: result.color,
      statusOfBlocking: result.state,
      whitelisted: isWhitelisted,
      categories: cat || []
    };
  }

  /**
      * Handle typosquatting call
      * @param  {string} url - url to check for typosquatting
      * @return {object} typosquatting data (whitelisted and domain)
      */
  static async handleTyposquatting(url)
  {
    const ts = new Typosquatting();
    if (ts.isInWhitelist(UrlParser.getDomain(url)))
    {
      BlockPageCommon.sendBlockPageEvent(WPSBlockPageType.Typoquatting, url, url, true);
      return { whitelisted: true, domain: null };
    }

    try
    {
      const domain = await ts.lookupTypoSquatFromServer(url);
      return { whitelisted: false, domain };
    }
    catch (error)
    {
      Logger.error(`handleTyposquatting Error: ${error}`);
      return { whitelisted: false, domain: null };
    }
  }

  static async handleUrlReputation(results, url, tabId, frameId, referer)
  {
    const {
      color = null,
      statusOfBlocking = null,
      typoDomain = null,
      whitelisted,
      categories = [],
    } = results;

    const isBlocked = [...BlockState].includes(statusOfBlocking);
    const isTopFrame = frameId <= 0;

    if (isTopFrame)
    {
      WebRequestHelper.increasePageScannedCounter();
    }

    // We can return early if it's not blocked or whitelisted
    if (whitelisted || !isBlocked)
    {
      if (isTopFrame)
      {
        BrowserAction.setIcon(color, tabId);
        // whitelisted is a normal block page (red/yellow/phishing/typo), does not require the top url
        if (whitelisted)
        {
          BlockPageCommon.checkWhitelistedBlockpage(statusOfBlocking, null, url, true);
        }
      }
      else
      {
        // whitelisted is iframe blocker, required the top url
        if (whitelisted)
        {
          BlockPageCommon.checkWhitelistedBlockpage(statusOfBlocking, referer, url, true);
        }
      }
      return;
    }

    // Add stats if it's blocked
    FeatureStats.add(featureStatsTypes.PAGES_BLOCKED);

    // For telemetry purposes
    let pageLevel;
    const telemetryContext = [];

    if (isTopFrame)
    {
      // Telemetry
      if (statusOfBlocking === BlockState.TYPOSQUATTING)
      {
        // Send telemetry for typosquatting page
        telemetryContext.push(EventNames.TYPOSQUATTING);
      }
      else
      {
        pageLevel = PageLevel.TOP;
        telemetryContext.push(EventNames.BLOCK_PAGE);
      }

      // Navigate to block page URL. If it is typosquatted then typoDomain is a valid string
      const qUrl = typoDomain !== null ? typoDomain : url;
      const blockPageStatus = BlockPageCommon.toBlockPageUrl(statusOfBlocking, color, qUrl);
      BackgroundGlobals.tabData.setTabBlockPageData(tabId, { bState: statusOfBlocking, url: qUrl, categories });

      // If native is not available then always show block page in this scenario.
      // If the page is not a direct download item then show block page else donot show block page
      if (!BackgroundGlobals.isNativeConnected || !BackgroundGlobals.downloadUrls.has(url))
      {
        // Lets check again to see whether this URL was a top level download URL or not.
        extension.tabs.update(tabId, { url: blockPageStatus.url });
      }
      // Set browser action always for top level page
      BrowserAction.setIcon(color, tabId);
      // Send block page event to browserhost
      // Converts red, yellow, typo, phishing from blockStatus to the appropriate WPS block page type
      const WPSBlockType = BlockPageCommon.convertWABlockTypeToWPS(statusOfBlocking);
      BlockPageCommon.sendBlockPageEvent(WPSBlockType, url, url, false);
    }
    else
    {
      BackgroundGlobals.tabData.addBlockedFrame(tabId, { url, frameId });

      // Inject Iframe block page to iframe page
      let contentToInject = [new InjectIframeBlock()];
      Injector.inject(tabId, contentToInject, frameId);

      // Inject Iframe banner to content page
      contentToInject = [new InjectIframeBanner()];
      Injector.inject(tabId, contentToInject);

      pageLevel = PageLevel.FRAME;
      telemetryContext.push(EventNames.BLOCK_PAGE);

      BlockPageCommon.sendBlockPageEvent(WPSBlockPageType.iFrame, referer, url, false);
    }

    WebRequestHelper.sendRespectiveTelemetry(telemetryContext, { color, pageLevel, pageStyle: null });
  }

  static increasePageScannedCounter()
  {
    FeatureStats.add(featureStatsTypes.PAGES_SCANNED);

    const options = {
      identifier: EventNames.WSS_ANALYTICS,
      message: 'msad.sites.safe',
      isFromBackground: true
    };

    ContextTelemetry.handle(options);
  }

  // Handling SSHit Telemetry
  static onSSHitTelemetry(url)
  {
    if (!BackgroundGlobals.isNativeConnected)
    {
      const urlObj = new URL(url);
      const isYahooSS = urlObj.host.includes('yahoo') && urlObj.searchParams.get('fr') === 'mcafee';

      if (isYahooSS && BackgroundGlobals.telemetryEngine)
      {
        BackgroundGlobals.telemetryEngine.sendEvent('wa_secure_search_hit', {
          hit_label_2: urlObj.searchParams.get('type'),
        });
      }
    }
  }

  static onEngineSearch(url)
  {
    const { searchEngines, nativeSettings } = BackgroundGlobals;
    const searchEngine = new SearchEngine(searchEngines, null, null, nativeSettings.SECURE_SEARCH_REGEXES);
    const engine = searchEngine.getEngineForUrl(url);
    if (engine && engine.enginetype === EngineType.SEARCH)
    {
      // On Engine Search trigger Native
      const { name, issecuresearch } = engine;
      BackgroundGlobals.messageDispatcher.postMessage({
        request_type: NativeCommands.MFW_REQUEST,
        payload: {
          name: 'event_on_engine_search',
          data: {
            engine: name,
            secure_search: issecuresearch,
          }
        }
      });

      // Trigger NPS Checklist
//////////////////////////////////////
      BackgroundGlobals.messageDispatcher.postMessage({
        request_type: NativeCommands.MFW_REQUEST,
        payload: {
          name: 'EVENT_BROWSER_SearchPageNavigate',
          data: {
            Url: url
          }
        }
      });
////////////////
    }
  }

  // For handling telemetry
  static sendRespectiveTelemetry(telemetryContext, attributes)
  {
    const options = {
      isFromBackground: true
    };

    telemetryContext.forEach((context) =>
    {
      switch (context)
      {
        case EventNames.BLOCK_PAGE:
          options.identifier = context;
          options.colour = attributes.color;
          options.level = attributes.pageLevel;
          options.blockPageStyle = attributes.pageStyle;
          break;
        case EventNames.TYPOSQUATTING:
          options.identifier = context;
          break;
        case EventNames.BROWSER_NAVIGATE:
          options.identifier = context;
          options.url = attributes.url;
          break;
        default:
          break;
      }

      if (options.identifier)
      {
        ContextTelemetry.handle(options);
      }
    });
  }
}
