// Common
import HttpService from '@extension/common/http_service';
import { Logger } from '@extension/common/logger';

// Form Detection
import weights from '@mcafee/form-detection/weights.json';
import * as exceptions from '@mcafee/form-detection/exceptions.json';
import { crfParams } from '@mcafee/form-detection/crfParams';

/**
 * TODO: Comments
 */
export default class FormDetection
{
  constructor()
  {
    this.http = new HttpService();
  }

  async sendAndReceiveRequest({ url })
  {
    let timeoutId = null;
    try
    {
      timeoutId = setTimeout(() =>
      {
        throw new Error('Remembear API call timed out');
      }, 3000);

      const response = await this.http.sendHttpRequest({
        headers: { 'Content-Type': 'application/json' },
        url
      });

      clearTimeout(timeoutId);
      return response.json();
    }
    catch (error)
    {
      clearTimeout(timeoutId);
      return Promise.reject(error);
    }
  }

  /**
   * Get the form detection model weights.
   * @returns weights.json for the remembrear form detection library.
   */
  static getWeights()
  {
    try
    {
      // TODO: Make http call when json file is hosted or endpoint is ready to be used
      return weights;
    }
    catch (error)
    {
      Logger.error(error);
      return null;
    }
  }

  /**
   * Get the exceptions for the form detection library.
   * @returns exceptions.json for the remembear form detection library.
   */
  static getExceptions()
  {
    try
    {
      // TODO: Make http call when json file is hosted or endpoint is ready to be used
      return exceptions;
    }
    catch (error)
    {
      Logger.error(error);
      return null;
    }
  }

  /**
   * Get the default crfParams for the form detection library.
   * @returns default crfParams object for the remembear form detection library.
   */
  static getCrfParams()
  {
    try
    {
      // TODO: Make http call when json file is hosted or endpoint is ready to be used
      return crfParams;
    }
    catch (error)
    {
      Logger.error(error);
      return null;
    }
  }
}
