import { HTIStorageVariables } from '../hti_storage_variables';
import CSPTokenRefresh from './csp_token_refresh';
import EverGreenUrlsRefresh from './ever_green_urls_refresh';

export const schedulers = {
  [HTIStorageVariables.CSP_TOKEN_HTI_REFRESH]: { frequency: 1000 * 60 * 60 * 24, runImmediatelyInitially: true, enabled: true, task: new CSPTokenRefresh() },
  [HTIStorageVariables.EVER_GREEN_URL_REFRESH]: { frequency: 1000 * 60 * 60 * 24, runImmediatelyInitially: true, enabled: false, task: new EverGreenUrlsRefresh() }, // This will get enabled by CSPTokenRefresh
};
