// Parses the passed URI to read out the value of a particular
// query string parameter within the URI string.
export default class UrlParser
// this is because we declare the function in both contentplg and iframeplg,
// using const will throw double declaration error here
{
  // get meaningful part of an uri (the part before ? or #)
  static getURI(uri)
  {
    let parts = uri.split('?');
    if (parts.length > 1)
    {
      return parts[0];
    }
    parts = uri.split('#');
    if (parts.length > 1)
    {
      return parts[0];
    }
    return uri;
  }

  // get a clean URI (subdomain + domain only)
  static getCleanURI(uri)
  {
    let cleanURI = uri;
    if (cleanURI.endsWith('/'))
    {
      cleanURI = cleanURI.slice(0, -1);
    }
    if (cleanURI.startsWith('http://'))
    {
      cleanURI = cleanURI.slice(7);
    }
    else if (cleanURI.startsWith('https://'))
    {
      cleanURI = cleanURI.slice(8);
    }
    return this.getURI(cleanURI);
  }

  static getParam(uri, paramName)
  {
    const qpos = uri.indexOf('?');
    if (qpos === -1)
    {
      return null;
    }
    const qString = uri.substring(qpos + 1);

    // Now split each param delimited by &
    const params = qString.split('&');
    for (let index = 0; index < params.length; ++index)
    {
      // Now separate out each param name and value delimited by =.
      const position = params[index].indexOf('=');
      if (position === -1)
      {
        continue;
      }
      const name = params[index].substring(0, position);
      if (name === paramName)
      {
        const value = params[index].substring(position + 1);
        return value;
      }
    }
    return null; // param not found.
  }

  static getDomain(url)
  {
    // there must be http(s) in the url, if not then return empty
    if (url === undefined || !/^https?:\/\//.test(url))
    {
      return '';
    }
    const domainURL = new URL(url);
    return domainURL.hostname;
  }

  static decodeQueryParam(p)
  {
    return decodeURIComponent(p.replace(/\+/g, ' '));
  }
}
